import React, { Component, Fragment } from "react";
import PropTypes from "prop-types";

import Configuration from "../../Configuration";

import $ from "./core/Services";

import { getSupportedSwapRouterName } from "./FarmPoolExtensions";

import { PriceBox, TitleText, MainText, DetailText, InfoText } from "../InfoBox";

/*
{
    creditToken,
    poolLiquidatable,
    blockTime,
    currentBlock
}
*/
export default class FarmLiquidatable extends Component {
    static propTypes = {
        liquidate: PropTypes.func,
        blockTime: PropTypes.number
    }

    static defaultProps = {
        liquidate: async () => {
        }
    };

    constructor(props) {
        super(props);

        this.state = {
            isInteractionDisabled: {}
        };

        this.formatDepositToken = $.format(this.props.poolLiquidatable.pool.depositToken.details.decimals);
        this.formatCreditToken = $.format(this.props.creditToken.details.decimals);

        this.execute = this.execute.bind(this);
    }

    render() {
        const now = Date.now() / 1000;
        const creditMultiplier = parseFloat(this.props.poolLiquidatable.pool.creditMultiplier) / 10000;
        const depositFeeRate = parseFloat(this.props.poolLiquidatable.pool.depositFee.rate) / 10000;
        const creditLiquidationRate = parseFloat(this.props.poolLiquidatable.pool.creditLiquidationRate) / 10000;
        const liquidatorFeeRate = parseFloat(this.props.poolLiquidatable.pool.liquidatorFee.rate) / 10000;
        const senderBalance = this.formatDepositToken.fromWei(this.props.poolLiquidatable.pool.senderBalance);
        const senderDeposited = this.formatDepositToken.fromWei(this.props.poolLiquidatable.pool.senderPosition.deposited);
        const senderCredited = this.formatCreditToken.fromWei(this.props.poolLiquidatable.pool.senderPosition.credited);
        const senderLiquidated = this.formatDepositToken.fromWei(this.props.poolLiquidatable.pool.senderPosition.liquidated.total);
        const senderCreditLiquidationLevel = this.formatCreditToken.fromWei(this.props.poolLiquidatable.pool.senderPosition.creditLiquidationLevel);
        const senderCurrentCredit = this.formatCreditToken.fromWei(this.props.poolLiquidatable.pool.senderCurrentCredit);
        const totalDeposited = this.formatDepositToken.fromWei(this.props.poolLiquidatable.pool.totalPosition.deposited);
        const totalCredited = this.formatCreditToken.fromWei(this.props.poolLiquidatable.pool.totalPosition.credited);
        const totalLiquidated = this.formatDepositToken.fromWei(this.props.poolLiquidatable.pool.totalPosition.liquidated.total);
        const seigniorageFee = parseFloat(this.props.poolLiquidatable.pool.seigniorageFee.rate) / 10000;
        const seigniorageFeeCollected = $.convert.fromWeiNativeToken(this.props.poolLiquidatable.pool.seigniorageFee.collected.total);
        const seignioryExplorerUri = `${Configuration.getBlockExplorerLink($.chainId)}/address/${this.props.poolLiquidatable.pool.seigniory}`;

        let updatedBlockId = parseInt(this.props.poolLiquidatable.pool.senderPosition.updatedBlockId);
        const senderTimeFromUpdate = updatedBlockId > 0 ? (parseInt(this.props.currentBlock) - updatedBlockId) * this.props.blockTime : 0;

        updatedBlockId = parseInt(this.props.poolLiquidatable.pool.totalPosition.updatedBlockId);
        const totalTimeFromUpdate = updatedBlockId > 0 ? (parseInt(this.props.currentBlock) - updatedBlockId) * this.props.blockTime : 0;

        const totalLiquidity = this.formatCreditToken.fromWei(this.props.poolLiquidatable.totalLiquidity.total);
        const totalLiquidatorFee = this.formatCreditToken.fromWei(this.props.poolLiquidatable.totalLiquidatorFee.total);

        const { fee, count } = this.getPoolSelfTotals(this.props.creditToken, this.props.poolLiquidatable);
        const netLiquidatorFee = parseFloat(totalLiquidatorFee) - fee;

        return (
            <div className="card bg-[#1e0f3c80] bg-diagonal-lines">
                <div className="grid grid-cols-1 gap-y-4">
                    <div className="grid grid-cols-3 gap-x-2 gap-y-4 text-[80%]">
                        <PriceBox
                            title="Deposit Token"
                            main={$.farm.getDepositTokenSymbol(this.props.poolLiquidatable.pool)}
                            mainUnit={getSupportedSwapRouterName($.chainId, this.props.poolLiquidatable.pool)}
                            isMainUnitOnTop={true}
                            mainClassName="font-display"
                            mainUnitClassName="!opacity-25 mb-3 font-display"
                            titleClassName="light-box"
                        />
                        <PriceBox
                            title="Total Liquidity"
                            main={this.formatCreditToken.formatCurrency(totalLiquidity)}
                            mainUnit={this.props.creditToken.details.symbol}
                            info={(<><div className="flex justify-evenly space-x-1"><span className="light-box w-full"><i className="bi bi-app-indicator mr-1" />{$.convert.formatInteger(this.props.poolLiquidatable.totalLiquidity.count)}</span></div></>)}
                            isMainUnitOnTop={true}
                            mainUnitClassName="!opacity-25 mb-3"
                            titleClassName="light-box"
                        />
                        <PriceBox
                            title="Total Liquidator Fee"
                            main={this.formatCreditToken.formatCurrency(netLiquidatorFee)}
                            mainUnit={this.props.creditToken.details.symbol}
                            info={(<><div className="flex justify-evenly space-x-1"><span className="light-box w-full"><i className="bi bi-app-indicator mr-1" />{$.convert.formatInteger(parseInt(this.props.poolLiquidatable.totalLiquidatorFee.count) - count)}</span></div></>)}
                            isMainUnitOnTop={true}
                            mainUnitClassName="!opacity-25 mb-3"
                            titleClassName="light-box"
                        />
                    </div>

                    <div className="grid grid-cols-4 gap-x-2 gap-y-2 text-center text-[80%]">
                        <div className="flex flex-col">
                            <TitleText title="Investor" titleClassName="light-box" />
                            <MainText
                                mainUnit={getSupportedSwapRouterName($.chainId, this.props.poolLiquidatable.pool)}
                                isMainUnitOnTop={true}
                                mainClassName="font-display"
                                mainUnitClassName="!opacity-25 mt-3 font-display"
                            />
                        </div>
                        <div className="flex flex-col">
                            <TitleText title="Liquidity" titleClassName="light-box" />
                            <MainText
                                mainUnit={this.props.creditToken.details.symbol}
                                isMainUnitOnTop={true}
                                mainUnitClassName="!opacity-25 mt-3"
                            />
                        </div>
                        <div className="flex flex-col">
                            <TitleText title="Liquidator Fee" titleClassName="light-box" />
                            <MainText
                                mainUnit={this.props.creditToken.details.symbol}
                                isMainUnitOnTop={true}
                                mainUnitClassName="!opacity-25 mt-3"
                            />
                        </div>
                        <div className="flex flex-col">
                            <TitleText title="Action" titleClassName="light-box" />
                            <MainText
                                mainUnit="&nbsp;"
                                isMainUnitOnTop={true}
                                mainUnitClassName="!opacity-25 mt-3"
                            />
                        </div>
                        {this.getFarmLiquidatables(this.props.poolLiquidatable.pool.poolId, this.props.poolLiquidatable.investorLiquidatables)}
                    </div>
                </div>
            </div>
        );
    }

    // Content generators
    getActivity(count, timestamp, now) {
        return (
            <div className="flex justify-evenly space-x-1">
                <span className="light-box w-full"><i className="bi bi-app-indicator mr-1" />{$.convert.formatInteger(count)}</span>
                <span className="light-box w-full"><i className="bi bi-clock mr-1" />{timestamp > 0 ? $.convert.formatTimespan(now - timestamp) : "-"}</span>
            </div>
        );
    }

    getFarmLiquidatables(poolId, investorLiquidatables) {
        const items = [];

        investorLiquidatables.forEach((investorLiquidatable, i) => {
            items.push((
                <Fragment key={i}>
                    <div>
                        <MainText
                            main={$.convert.formatAccount(investorLiquidatable.investor)}
                            mainClassName="text-[0.875em]"
                        />
                        {investorLiquidatable.isSelf &&
                            <div>
                                <DetailText
                                    detail="You"
                                    detailClassName="font-display rounded-lg bg-red-600 px-1"
                                />
                            </div>
                        }
                    </div>
                    <MainText main={this.formatCreditToken.formatCurrency(this.formatCreditToken.fromWei(investorLiquidatable.liquidity))}
                    />
                    <MainText main={investorLiquidatable.isSelf
                        ? (<del>{this.formatCreditToken.formatCurrency(this.formatCreditToken.fromWei(investorLiquidatable.liquidatorFee))}</del>)
                        : (this.formatCreditToken.formatCurrency(this.formatCreditToken.fromWei(investorLiquidatable.liquidatorFee)))
                    }
                    />
                    <button
                        type="button"
                        className="h-7 btn btn-red px-2"
                        disabled={this.state.isInteractionDisabled[i]}
                        onClick={async () => {
                            await this.execute(i, () => $.farm.liquidateAccount(poolId, investorLiquidatable.investor));
                        }}
                    >
                        Liquidate
                    </button>
                </Fragment>
            ));
        });

        return items;
    }

    getPoolSelfTotals(creditToken, pool) {
        const liquidatables = pool.investorLiquidatables.filter(i => i.isSelf);
        const fee = liquidatables.reduce((sum, liquidatable) => sum + $.convert.fromWei(liquidatable.liquidatorFee, creditToken.details.decimals), 0);

        return { fee, count: liquidatables.length };
    }

    async execute(i, func) {
        this.setState((prevState, props) => ({
            isInteractionDisabled: { ...prevState.isInteractionDisabled, [i]: true }
        }));

        try {
            await func();
        } catch (error) {
            console.log(`${error.code}: ${error.message}`);
        } finally {
            this.setState((prevState, props) => ({
                isInteractionDisabled: { ...prevState.isInteractionDisabled, [i]: false }
            }));
        }
    }
}
