export const MAX_UINT256 = "115792089237316195423570985008687907853269984665640564039457584007913129639935";

export const cmpi = (str1, str2) => str1.localeCompare(str2, undefined, { sensitivity: "accent" });
export const equals = (str1, str2) => cmpi(str1, str2) === 0;

export function contains(str1, str2) {
    return (new RegExp(str2, "i")).test(str1);
}

export function sleep(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
}

export function promisify(inner) {
    return new Promise((resolve, reject) =>
        inner((err, res) => {
            if (err) {
                reject(err);
            } else {
                resolve(res);
            }
        })
    );
}

export async function waitMined(web3, txHash, interval = 500) {
    while (true) {
        const receipt = await promisify(cb => web3.eth.getTransactionReceipt(txHash, cb));

        if (receipt == null) {
            await sleep(interval);
        }
        else {
            return receipt;
        }
    }
}

function createClamp(minWidthPx, maxWidthPx, minFontSizePx, maxFontSizePx, pixelsPerRem = 16) {
    const minWidth = minWidthPx / pixelsPerRem;
    const maxWidth = maxWidthPx / pixelsPerRem;

    const minFontSize = minFontSizePx / pixelsPerRem;
    const maxFontSize = maxFontSizePx / pixelsPerRem;

    const slope = (maxFontSize - minFontSize) / (maxWidth - minWidth);
    const yAxisIntersection = -minWidth * slope + minFontSize

    return `clamp(${minFontSize}rem, ${yAxisIntersection}rem + ${slope * 100}vw, ${maxFontSize}rem)`;
}

function createClamp2(minWidthPx, maxWidthPx, minFontSizePx, maxFontSizePx, pixelsPerRem = 16) {
    const scale = 1.40;
    return createClamp(minWidthPx, maxWidthPx, minFontSizePx * scale, maxFontSizePx * scale, pixelsPerRem)
}

