import { useEffect, useState } from "react";
import PropTypes from "prop-types";

import Web3 from "web3";

import { fromWei } from "../../utils/Web3Converter";
import { formatInteger, formatCurrency } from "../../utils/Format";

import { PriceBox, TitleText, MainText, DetailText, InfoText } from "../InfoBox";
import FarmLiquidatable from "./FarmLiquidatable";

export default function FarmLiquidatables({
    farm,
    balance,
    priceInStableToken,
    nativeTokenPriceInStableToken,
    blockTime,
}) {
    const [activePools, setActivePools] = useState(farm.poolLiquidatables);

    const totalLiquidity = parseFloat(fromWei(farm.totalLiquidity.total, farm.creditToken.details.decimals));
    const totalLiquidatorFee = parseFloat(fromWei(farm.totalLiquidatorFee.total, farm.creditToken.details.decimals));

    useEffect(() => {
        const pools = [...farm.poolLiquidatables];

        pools.sort(function (a, b) {
            return Web3.utils.toBN(b.totalLiquidity.total).cmp(Web3.utils.toBN(a.totalLiquidity.total));
        });

        // Pools where sender has deposits come first.
        pools.sort(function (a, b) {
            return Web3.utils.toBN(b.pool.senderPosition.deposited).cmp(Web3.utils.toBN(a.pool.senderPosition.deposited));
        });

        setActivePools(pools);
    }, [farm]);

    return (
        <>
            {activePools.length > 0
                ? <>
                    <div className="card bg-[#1e0f3c80] bg-diagonal-lines text-center">
                        <div className="grid grid-cols-2 sm:grid-cols-4 gap-x-2 gap-y-4 text-[80%]">
                            <PriceBox
                                title="Credit Token"
                                main={farm.creditToken.details.symbol}
                                mainUnit="&nbsp;"
                                isMainUnitOnTop={true}
                                titleClassName="light-box"
                                mainClassName="font-display"
                                mainUnitClassName="mb-3"
                            />
                            <PriceBox
                                title="Liquidatable Pools"
                                main={formatInteger(farm.poolLiquidatables.length)}
                                mainUnit="&nbsp;"
                                isMainUnitOnTop={true}
                                titleClassName="light-box"
                                mainUnitClassName="mb-3"
                            />
                            <PriceBox
                                title="Total Liquidity"
                                main={formatCurrency(totalLiquidity, farm.creditToken.details.decimals)}
                                mainUnit={farm.creditToken.details.symbol}
                                isMainUnitOnTop={true}
                                titleClassName="light-box"
                                mainUnitClassName="!opacity-25 mb-3"
                            />
                            <PriceBox
                                title="Total Liquidator Fee"
                                main={formatCurrency(totalLiquidatorFee, farm.creditToken.details.decimals)}
                                mainUnit={farm.creditToken.details.symbol}
                                isMainUnitOnTop={true}
                                titleClassName="light-box"
                                mainUnitClassName="!opacity-25 mb-3"
                            />
                        </div>
                    </div>
                    <div className="grid grid-cols-1 lg:grid-cols-2 xl:grid-cols-3 gap-x-5 gap-y-10">
                        {getFarmLiquidatables(activePools)}
                    </div>
                </>
                : <div className="card bg-[#1e0f3c80] bg-diagonal-lines text-center">
                    <p className="light-box text-red-500 ">No liquidatable pools found.</p>
                </div>
            }
        </>
    );

    function getFarmLiquidatables(poolLiquidatables) {
        const items = [];

        poolLiquidatables.forEach((poolLiquidatable, i) => {
            items.push((
                <div key={i} className="max-h-full">
                    <div className="rounded-lg border-2 border-white/10">
                        <FarmLiquidatable
                            creditToken={farm.creditToken}
                            poolLiquidatable={poolLiquidatable}
                            nativeTokenPriceInStableToken={nativeTokenPriceInStableToken}
                            blockTime={blockTime}
                            currentBlock={farm.currentBlock}
                        />
                    </div>
                </div>
            ));
        });

        return items;
    }
}

FarmLiquidatables.propTypes = {
    liquidate: PropTypes.func
};

FarmLiquidatables.defaultProps = {
    liquidate: async (poolId, investor) => {
    }
};
