import { useState } from "react";
import { Link, NavLink } from "react-router-dom";

import Wallet from "./Wallet";
import Links from "./Links";

export default function Navbar() {
    const [isNavbarCollapsed, setIsNavbarCollapsed] = useState(true);

    function toggleNavbar() {
        setIsNavbarCollapsed(!isNavbarCollapsed);
    }

    function getRoot(uri) {
        const index = uri.indexOf("//");

        if (index == -1) {
            return undefined;
        }

        let result = uri.substring(0, index + 2);
        const right = uri.substring(index + 2);
        const parts = right.split(".");

        if (parts.length < 2) {
            return uri;
        }

        let delimiter = "";

        for (let i = parts.length - 2; i < parts.length; i++) {
            result += delimiter;
            result += parts[i];
            delimiter = ".";
        }

        return result;
    }

    return (
        <nav>
            <div className="flex h-[64px] items-center justify-between">
                <div className="flex items-center lg:space-x-16">
                    <Link className="navbar-brand flex items-center gap-2" to="/">
                        {/* <div className="bg-weeb-logo hidden h-[1200px] max-h-[64px] w-[1200px] max-w-[64px] bg-left-top bg-no-repeat sm:inline-block"></div>
                        <div className="bg-weeb-text h-[464px] max-h-[48px] w-[908px] max-w-[94px] bg-left-top bg-no-repeat"></div> */}

                        {/* <div className="bg-weeb-logo hidden max-h-[80px] max-w-[80px] h-[64px] w-[64px] bg-left-top bg-no-repeat sm:inline-block"></div> */}
                        <div className="bg-weeb-text h-[52px] max-h-[52px] w-[102px] max-w-[102px] bg-left-top bg-no-repeat self-end"></div>
                    </Link>
                    <div className="hidden lg:flex lg:space-x-4">
                        <Links />
                    </div>
                </div>

                <div className="flex items-center justify-end space-x-4">
                    <Wallet />
                    <button type="button" className={`lg:hidden rounded-md ring-4 focus:outline-none ${isNavbarCollapsed
                        ? "text-gray-100/75 ring-gray-100/5 hover:bg-gray-100/10 hover:text-gray-100 hover:ring-gray-100/25"
                        : "bg-gray-100/10 text-gray-100 ring-gray-100/25"
                        }`}
                        onClick={toggleNavbar}>
                        <svg xmlns="http://www.w3.org/2000/svg" className={`h-10 w-12 fill-current p-1`} fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 6h16M4 12h16m-7 6h7" />
                        </svg>
                    </button>
                </div>
            </div>

            <div className={`lg:hidden ${isNavbarCollapsed
                ? "hidden"
                : "mt-2 grid w-full grid-cols-2 gap-2 rounded-lg bg-gray-100/10"
                }`}
            >
                <div className="flex flex-col p-2 gap-1">
                    <Links />
                </div>
                <div className="flex flex-col items-end justify-end p-2">
                    {/* <NavLink className="nav-link" to="/" activeclassname="active">
                        <i className="bi bi-file-earmark-text mr-1" />
                        Documentation
                    </NavLink> */}
                    {/* <NavLink className="nav-link" to={getRoot(window.location.href)} activeclassname="active">
                        <i className="bi bi-box-arrow-right mr-1" />
                        Exit App
                    </NavLink> */}

                    <a className="nav-link" href={getRoot(window.location.href)}>
                        <i className="bi bi-box-arrow-right mr-1" />
                        Exit App
                    </a>
                </div>
            </div>
        </nav>
    );
}
