export function getQuoteAssetInflowRequiredToIncreaseThePriceByFactorOf(factor, reserveQuoteAsset) {
    return (Math.sqrt(factor) - 1) * reserveQuoteAsset;
}

export function getBaseAssetOutflowRequiredToDecreaseThePriceByFactorOf(factor, reserveBaseAsset) {
    return (1 / Math.sqrt(factor) - 1) * reserveBaseAsset;
}

export function getPriceIncreaseFactorFromQuoteAssetInflow(inflowQuoteAssetAmount, reserveQuoteAsset) {
    return Math.pow((inflowQuoteAssetAmount / reserveQuoteAsset + 1), 2);
}

export function getPriceDecreaseFactorFromBaseAssetOutflow(outflowBaseAssetAmount, reserveBaseAsset) {
    return Math.pow(1 / (outflowBaseAssetAmount / reserveBaseAsset + 1), 2);
}

export function getAmountOut(amount0In, reserve0, reserve1) {
    return amount0In * reserve1 / (amount0In + reserve0);
}