import { useState, useEffect } from "react";
import { Switch } from "@headlessui/react";
import PropTypes from "prop-types";

import $ from "./core/Services";

import DelayedInput from "./dialogs/core/DelayedInput";
import { PriceBox } from "../InfoBox";

import { getPriceIncreaseFactorFromQuoteAssetInflow, getPriceDecreaseFactorFromBaseAssetOutflow } from "../../utils/PriceExtensions";

export default function TargetPriceCalculator({
    tokenReserve,
    nativeTokenReserve,
    tokenPriceInStableToken,
    nativeTokenPriceInStableToken
}) {
    const Mode = Object.freeze({
        Inflow: 0,
        Outflow: 1
    });

    const [mode, setMode] = useState(Mode.Inflow);
    const [assetAmount, setAssetAmount] = useState(1);
    const [targetPrice, setTargetPrice] = useState(0);

    useEffect(() => {
        const factor = mode === Mode.Inflow
            ? getPriceIncreaseFactorFromQuoteAssetInflow(assetAmount, nativeTokenReserve)
            : getPriceDecreaseFactorFromBaseAssetOutflow(assetAmount, tokenReserve);
        setTargetPrice(tokenPriceInStableToken * factor);
    }, [tokenReserve, nativeTokenReserve, tokenPriceInStableToken, nativeTokenPriceInStableToken, mode, assetAmount]);

    return (
        <>
            <div className="grid grid-cols-1 gap-x-2 gap-y-4">
                <div>
                    <span className="text-[0.5em] text-violet-100/50 light-box block mb-4 min-h-[5.5em] xs:min-h-[4em]">
                        Calculates the level to which the given inflow or outflow amount brings the price.
                    </span>
                    <div className="grid grid-cols-1 gap-y-1">
                        <Switch.Group>
                            <div className="flex items-center">
                                <Switch
                                    checked={mode === Mode.Inflow}
                                    onChange={toggleMode}
                                    className="bg-violet-300 relative inline-flex items-center w-10 h-[1em] rounded-full transition-colors focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-offset-slate-800 focus:ring-violet-300/50"
                                >
                                    <span className="sr-only">Flow mode</span>
                                    <span className={`${mode === Mode.Inflow ? "translate-x-6" : "translate-x-1"} inline-block w-[0.75em] h-[0.75em] transform bg-slate-800 rounded-full transition-transform`} />
                                </Switch>
                                <Switch.Label className="input-label leading-6 ml-1.5">
                                    {mode === Mode.Inflow ? `Inflow ${$.contracts.nativeToken.symbol}` : `Outflow ${$.contracts.token.symbol}`}
                                </Switch.Label>
                            </div>
                        </Switch.Group>
                        <DelayedInput
                            delay={400}
                            type="number"
                            id="assetAmount"
                            className="numeric !text-[1em] text-right"
                            defaultValue={assetAmount}
                            placeholder="Asset amount"
                            min={0}
                            step={1}
                            onInput={value => setAssetAmount(parseFloat(value))} />
                    </div>
                </div>

                <div className="text-[80%]">
                    <PriceBox
                        title="Target Price"
                        main={isNaN(targetPrice) ? "-" : $.convert.formatStableToken(targetPrice)}
                        mainUnit={$.contracts.stableToken.symbol}
                        detail={isNaN(targetPrice) ? "-" : $.convert.formatNativeToken(targetPrice / nativeTokenPriceInStableToken)}
                        detailUnit={$.contracts.nativeToken.symbol}
                        mainUnitClassName="glow"
                    />
                </div>
            </div>
        </>
    );

    function toggleMode() {
        if (mode === Mode.Inflow) {
            setMode(Mode.Outflow);
        } else {
            setMode(Mode.Inflow);
        }
    }
}

TargetPriceCalculator.propTypes = {
    tokenReserve: PropTypes.number,
    nativeTokenReserve: PropTypes.number,
    tokenPriceInStableToken: PropTypes.number,
    nativeTokenPriceInStableToken: PropTypes.number
}

TargetPriceCalculator.defaultProps = {

};
