import appsettings from "./appsettings.json";

export default class Configuration {
    static getProjectName() {
        return appsettings.projectName;
    }

    static getDefaultChainId() {
        return appsettings.defaultChainId;
    }

    static getPercentageYieldInvestmentInStablecoin() {
        return parseFloat(appsettings.percentageYieldInvestmentInStablecoin);
    }

    static getSlippageTolerance() {
        return parseFloat(appsettings.slippageTolerance);
    }

    static getMoralisApiKey() {
        return appsettings.moralisApiKey;
    }

    static getChain(chainId) {
        return appsettings.chains[chainId];
    }

    getChain() { }

    static getNativeCoin(chainId) {
        return Configuration.getChain(chainId).nativeCoin;
    }

    static getContracts(chainId) {
        return appsettings.chains[chainId].contracts;
    }

    getContracts() { }

    static getNativeToken(chainId) {
        return Configuration.getContracts(chainId).nativeToken;
    }

    static getStableToken(chainId) {
        return Configuration.getContracts(chainId).stableToken;
    }

    static getSwapRouters(chainId) {
        return Configuration.getContracts(chainId).swapRouters;
    }

    static getDefaultSwapRouter(chainId) {
        return Configuration.getContracts(chainId).defaultSwapRouter;
    }

    getSwapRouters() {
        // In order getSwapRouterName to be able to call the static getSwapRouters a non static getSwapRouters has to be defined.
        // Interesting requirement.
    }

    static getSwapRouterName(chainId, address) {
        return Configuration.getSwapRouters(chainId)[address].name ?? "Unknown Router";
    }

    static getSwapRouterSwapFee(chainId, address) {
        return parseFloat(Configuration.getSwapRouters(chainId)[address].swapFee);
    }

    static getChains() {
        const map = new Map();

        for (const [key, value] of Object.entries(appsettings.chains)) {
            map[parseInt(key)] = value.name;
        }

        return map;
    }

    static isSupportedChain(chainId) {
        return Configuration.getChains()[chainId] ? true : false;
    }

    static getRpcUris() {
        const map = new Map();

        for (const [key, value] of Object.entries(appsettings.chains)) {
            map[parseInt(key)] = value.rpcUri;
        }

        return map;
    }

    static getRpcUri(chainId) {
        return Configuration.getRpcUris()[chainId];
    }

    static getBlockExplorerLinks() {
        const map = new Map();

        for (const [key, value] of Object.entries(appsettings.chains)) {
            map[parseInt(key)] = value.blockExplorerLink;
        }

        return map;
    }

    getBlockExplorerLinks() { }

    static getBlockExplorerLink(chainId) {
        return Configuration.getBlockExplorerLinks()[chainId];
    }

    static getInitialLiquidity(chainId) {
        return Configuration.getChain(chainId).initialLiquidity;
    }
}