import $ from "../core/Services";

export default function GameList({
    games,
    defaultGameId,
    setGameId
}) {
    function getGameList() {
        const items = [];
        let i = 0;

        games.forEach((game, gameId) => {
            var isActive = false;

            if (i++ === 0) {
                if (defaultGameId === -1 || defaultGameId > games.length) {
                    isActive = true;
                }
            }

            let minimumBet;
            let unit;

            if (game.isPercentage) {
                minimumBet = $.convert.formatPercentage(parseFloat(game.minimumBet) / 10000);
                unit = "%";
            } else {
                minimumBet = $.convert.formatCurrency($.convert.fromWei(game.minimumBet, game.tokenInfo.details.decimals), game.tokenInfo.details.decimals);
                unit = game.tokenInfo.details.symbol;
            }

            items.push((
                <button
                    key={gameId}
                    className={`card btn btn-violet ${isActive || gameId === defaultGameId ? "active" : ""}`}
                    onClick={e => {
                        e.preventDefault();
                        setGameId(gameId);
                    }}>
                    <div className="grid grid-cols-1 gap-y-1 text-center">
                        <h6 className="card-header-title-text font-semibold">
                            {game.name}
                        </h6>
                        <div className="text-[0.5625em] opacity-80">
                            <div>
                                Draws every
                                <span className="numeric mx-1 font-bold">{game.blockLength}</span>
                                blocks
                            </div>
                            <div>
                                Min. bet is
                                <span className="mx-1 font-bold">
                                    <span className="numeric">{minimumBet}</span> {unit}
                                </span>
                                {game.isPercentage ? "of the reward" : ""}
                            </div>
                        </div>
                    </div>
                </button>
            ));
        });

        return items;
    }

    return (
        <div className="grid grid-cols-2 gap-x-2 gap-y-2 max-h-40 overflow-y-auto overflow-x-hidden section p-1">
            {getGameList()}
        </div>
    )
}
