import { fromWei } from "../../utils/Web3Converter";

import InflowOutflowCalculator from "./InflowOutflowCalculator";
import TargetPriceCalculator from "./TargetPriceCalculator";
import RewardCalculator from "./RewardCalculator";

export default function TokenCalculators({
    token,
    priceInNativeToken,
    priceInStableToken,
    blockTime
}) {
    const nativeTokenPriceInStableToken = priceInStableToken / priceInNativeToken;
    const senderBalance = fromWeiToken(token.senderBalance);
    const totalStaked = fromWeiToken(token.totalStaked);
    const maxRewardPerBlock = fromWeiToken(token.maximumRewardPerBlock);
    const currentRewardPerBlock = fromWeiToken(token.currentRewardPerBlock);

    return (
        <div className="grid grid-cols-2 lg:grid-cols-4 gap-x-2 xs:gap-x-5 gap-y-5 text-center">
            <div className={`card bg-weeb-card1/30 bg-diagonal-lines`}>
                <InflowOutflowCalculator
                    tokenReserve={token.tokenReserve}
                    nativeTokenReserve={token.nativeTokenReserve}
                    tokenPriceInStableToken={priceInStableToken}
                    nativeTokenPriceInStableToken={nativeTokenPriceInStableToken}
                />
            </div>
            <div className={`card bg-weeb-card1/30 bg-diagonal-lines`}>
                <TargetPriceCalculator
                    tokenReserve={token.tokenReserve}
                    nativeTokenReserve={token.nativeTokenReserve}
                    tokenPriceInStableToken={priceInStableToken}
                    nativeTokenPriceInStableToken={nativeTokenPriceInStableToken}
                />
            </div>
            <div className={`card bg-weeb-card1/30 bg-diagonal-lines col-span-2`}>
                <RewardCalculator
                    senderBalance={senderBalance}
                    totalStaked={totalStaked}
                    currentRewardPerBlock={currentRewardPerBlock}
                    maxRewardPerBlock={maxRewardPerBlock}
                    isMaximizedEmission={token.isInflationRegulated}
                    blockTime={blockTime}
                    tokenPriceInStableToken={priceInStableToken}
                />
            </div>
        </div>
    );

    // Helpers
    function fromWeiToken(value) {
        return parseFloat(fromWei(value, token.details.decimals));
    }
}
