import { useState, useEffect } from "react";
import PropTypes from "prop-types";

import { MainText } from "../../../InfoBox";

export default function RangeInput({
    isDisabled,
    min,
    max,
    decimals,
    percentages,
    valueClassName,
    rangeClassName,
    buttonClassName,
    setValue,
    setPercentage
}) {
    const [value, setValueInternal] = useState(0);

    useEffect(() => {
        const v = parseFloat(value);
        const m = parseFloat(max);

        if (v > m) {
            updateValues(m);
        }
    }, [max]);

    function format(x) {
        return x ? parseFloat(parseFloat(x).toFixed(decimals)) : 0;
    }

    function getStep() {
        return !isNaN(decimals) ? Math.pow(10, -decimals) : 0;
    }

    function updateValues(n) {
        const mx = parseFloat(max);
        const mn = parseFloat(min);

        if (n > mx) {
            n = mx;
        }

        if (n < mn) {
            n = mn;
        }

        setValueInternal(n);
        setValue(n);

        // Update the percentage.
        const r = mx - mn;
        setPercentage(r !== 0 ? n / r : 0);
    }

    function onValueChanged(e) {
        updateValues(e.target.value != null ? format(e.target.value) : 0);
    }

    function setPercentageFormatted(percentage) {
        const mx = parseFloat(max);
        const mn = parseFloat(min);

        const n = mn + (mx - mn) * percentage / 100;
        updateValues(n);
    }

    function getSelectablePercentages() {
        const items = [];

        for (let i = 0; i < percentages.length; ++i) {
            const percentage = percentages[i];

            items.push((
                <button key={i}
                    type="button"
                    className={`${buttonClassName} ${i < 2 ? "hidden lg:inline" : ""}`}
                    onClick={() => setPercentageFormatted(percentage)}
                >
                    <MainText
                        sizeCount={1}
                        main={percentage}
                        mainUnit="%"
                        mainClassName="!text-[0.875em]"
                        mainUnitClassName="!text-[0.6875em] ml-0.5"
                    />
                </button>
            ));
        }

        return items;
    }

    return (
        <div className="grid grid-cols-1 gap-y-1">
            <input
                disabled={isDisabled}
                type="number"
                className={`text-[2em] numeric text-right h-fit ${valueClassName}`}
                min={format(min)}
                max={format(max)}
                step={format(getStep())}
                value={format(value)}
                onInput={onValueChanged}
            />
            <input
                disabled={isDisabled}
                type="range"
                list="tickmarks"
                className={`border-0 focus:border-0 ring-0 focus:ring-0 ${rangeClassName}`}
                min={format(min)}
                max={format(max)}
                step={format(getStep())}
                value={format(value)}
                onInput={onValueChanged}
            />
            {/* <datalist id="tickmarks">
                <option value="0" label="0%"></option>
                <option value="10"></option>
                <option value="20"></option>
                <option value="30"></option>
                <option value="40"></option>
                <option value="50" label="50%"></option>
                <option value="60"></option>
                <option value="70"></option>
                <option value="80"></option>
                <option value="90"></option>
                <option value="100" label="100%"></option>
            </datalist> */}
            <div className="flex items-center justify-center space-x-2 lg:justify-evenly">
                {getSelectablePercentages()}
            </div>
        </div>
    );
}

RangeInput.propTypes = {
    percentages: PropTypes.array,
    buttonClassName: PropTypes.string
};

RangeInput.defaultProps = {
    percentages: [1, 2, 5, 10, 25, 50, 75, 100],
    buttonClassName: "btn btn-indigo text-violet-50 numeric w-11 h-7"
};
