import { getOutcomeImage, getOutcomeText, getRandomizeText, getRandomInteger } from "../GameExtensions";

export default function OutcomeList({
    outcomeCount,
    defaultOutcome,
    setOutcome
}) {
    function getOutcomeList() {
        const items = [];

        for (let outcome = 0; outcome < outcomeCount; ++outcome) {
            const isActive = outcome === parseInt(defaultOutcome);

            items.push((
                <button
                    key={outcome}
                    type="button"
                    className={`group btn btn-outcome font-bold uppercase tracking-wide ${isActive ? "active" : ""} p-1`}
                    onClick={() => setOutcome(outcome)}>
                    {getOutcomeImage(parseInt(outcomeCount), outcome)}
                    <div className={`text-[0.875em] mt-1 light-box group-active:text-violet-800 ${isActive ? "text-violet-800" : ""}`}>
                        {getOutcomeText(parseInt(outcomeCount), outcome)}
                    </div>
                </button>
            ));
        }

        return items;
    }

    return (
        <div className="grid grid-cols-1 gap-y-3">
            <div className="flex items-start justify-center space-x-2 h-[180px]">
                {getOutcomeList()}
            </div>

            <div className="mx-auto text-[80%]">
                <button
                    type="button"
                    className="btn btn-randomize w-16 h-9 text-violet-50 mt-3"
                    onClick={() => setOutcome(getRandomInteger(0, parseInt(outcomeCount)))}
                >
                    {getRandomizeText(parseInt(outcomeCount))}
                </button>
            </div>
        </div>
    )
}
