import React, { Component } from "react";
import PropTypes from "prop-types";
import Web3 from "web3";

import $ from "./core/Services";

import Spinner from "../../Spinner";
import Configuration from "../../Configuration";

import { PriceBox } from "../InfoBox";
import { StyledPopover } from "../StyledPopover";

import { BYTE_MAX_VALUE, getOutcomeText, getRandomizeText } from "./GameExtensions";

/*
{
    game,
    balance,
    priceInStableToken,
    bet,
    compound,
    claim,
    senderPoolSnapshots
    blockTime
}
*/
export default class Game extends Component {
    static propTypes = {
        bet: PropTypes.func,
        claim: PropTypes.func
    }

    static defaultProps = {
        bet: async () => {
        },
        claim: async () => {
        }
    };

    constructor(props) {
        super(props)

        this.state = {
            isInteractionDisabled: false,
            showLastBetPool: false,
            showSenderLastBetPool: false,
            isPoolTotalsVisibile: false,
            showYourBetHistory: false,
            senderBetPools: [],
            isLoading: false
        };

        this.format = $.format(this.props.game.tokenInfo.details.decimals);

        this.toggleLastBetPoolVisibility = this.toggleLastBetPoolVisibility.bind(this);
        this.toggleSenderLastBetPoolVisibility = this.toggleSenderLastBetPoolVisibility.bind(this);
        this.togglePoolTotalsVisibility = this.togglePoolTotalsVisibility.bind(this);
        this.toggleYourBetHistoryVisibility = this.toggleYourBetHistoryVisibility.bind(this);
        this.loadSenderBetPools = this.loadSenderBetPools.bind(this);

        this.execute = this.execute.bind(this);
    }

    render() {
        const now = Date.now() / 1000;
        const tokenExplorerUri = `${Configuration.getBlockExplorerLink($.chainId)}/token/${this.props.game.tokenInfo.token}`;
        const swapRouterName = Configuration.getSwapRouterName($.chainId, Configuration.getDefaultSwapRouter($.chainId));
        const minimumBet = this.props.game.isPercentage ? parseFloat(this.props.game.minimumBet) / 10000 : this.format.fromWei(this.props.game.minimumBet);

        const unclaimedBalance = this.format.fromWei(this.props.game.unclaimedBalance);
        const seigniorageFee = parseFloat(this.props.game.seigniorageFee.rate) / 10000;
        const seigniorageFeeCollected = this.format.fromWei(this.props.game.seigniorageFee.collected.total);
        const seignioryExplorerUri = `${Configuration.getBlockExplorerLink($.chainId)}/address/${this.props.game.seigniory}`;

        const zero = Web3.utils.toBN(0);
        const senderUnclaimedPrizeBN = Web3.utils.toBN(this.props.game.senderUnclaimedPrize.winningBet).add(Web3.utils.toBN(this.props.game.senderUnclaimedPrize.prize));
        const isPrizeClaimable = senderUnclaimedPrizeBN.cmp(zero) === 1; // > 0
        const senderUnclaimedPrize = this.format.fromWei(senderUnclaimedPrizeBN);
        const currentBetPoolWinningPrize = this.format.fromWei(this.props.game.currentBetPool.statement.senderPrize);
        const currentBetPoolPot = this.format.fromWei(this.props.game.currentBetPool.statement.allAggregatedBets.total);
        const drawBlock = parseInt(this.props.game.currentPoolId) + parseInt(this.props.game.blockLength);
        const blocksToDraw = drawBlock - parseInt(this.props.game.currentBlock);
        const timeToDraw = blocksToDraw * this.props.blockTime;

        const totalPlacedBets = this.format.fromWei(this.props.game.totalPosition.placedBets.total);
        const totalClaimedBets = this.format.fromWei(this.props.game.totalPosition.claimedBets.total);
        const totalClaimedPrizes = this.format.fromWei(this.props.game.totalPosition.claimedPrizes.total);

        const senderAggregatedBets = this.format.fromWei(this.props.game.currentBetPool.statement.senderAggregatedBets.total);
        const senderLosingBets = this.format.fromWei(this.props.game.currentBetPool.statement.senderLosingBets.total);
        const senderPrize = this.format.fromWei(this.props.game.currentBetPool.statement.senderPrize);
        const hasOpponent = this.isWinner(this.props.game.currentBetPool) > 0;
        const senderPnL = hasOpponent ? (senderPrize - senderLosingBets) : senderAggregatedBets;

        const tooltipSeigniorageFeeText = (<span className="tooltip">Fee paid to the seignory (game owner) from claimed prize.</span>);

        // 1. popoverGameRef
        const popoverGameContent = (
            <div className="grid grid-cols-2 gap-x-2 gap-y-4 text-[80%]">
                <PriceBox
                    title="Bet Token"
                    main={(<a href={tokenExplorerUri} rel="noreferrer" target="_blank" className="font-display">{this.props.game.tokenInfo.details.symbol}</a>)}
                    detailUnit={swapRouterName}
                />
                <PriceBox
                    title="Minimum Bet"
                    main={this.props.game.isPercentage ? $.convert.formatPercentage(minimumBet) : this.format.formatToken(minimumBet)}
                    mainUnit={this.props.game.isPercentage ? "%" : this.props.game.tokenInfo.details.symbol}
                    detail={this.props.game.isPercentage ? "" : $.convert.formatStableToken(minimumBet * this.props.priceInStableToken)}
                    detailUnit={this.props.game.isPercentage ? /*this.props.game.tokenInfo.details.symbol*/"Of Pending Reward" : $.contracts.stableToken.symbol}
                    mainClassName="glow"
                />
                <PriceBox
                    title="Outcome Count"
                    main={$.convert.formatInteger(this.props.game.outcomeCount)}
                />
                <PriceBox
                    title="Unclaimed Balance"
                    main={this.format.formatToken(unclaimedBalance)}
                    mainUnit={this.props.game.tokenInfo.details.symbol}
                    detail={$.convert.formatStableToken(unclaimedBalance * this.props.priceInStableToken)}
                    detailUnit={$.contracts.stableToken.symbol}
                    mainClassName="glow"
                />
                <PriceBox
                    title={(<><span className="mr-1">Seigniorage Fee</span><StyledPopover isTooltip={true} children={tooltipSeigniorageFeeText} /></>)}
                    main={$.convert.formatPercentage(seigniorageFee)}
                    mainUnit="%"
                    detail={(<a href={seignioryExplorerUri} rel="noreferrer" target="_blank" className="numeric">{$.convert.formatAccount(this.props.game.seigniory)}</a>)}
                    info={this.props.game.isSenderSeignory ? (<span className="font-display">You're Seignory</span>) : null}
                    infoClassName="opacity-75 light-box font-semibold uppercase text-red-500"
                />
                <PriceBox
                    title="Seigniorage Collected"
                    main={this.format.formatToken(seigniorageFeeCollected)}
                    mainUnit={this.props.game.tokenInfo.details.symbol}
                    detail={$.convert.formatStableToken(seigniorageFeeCollected * this.props.priceInStableToken)}
                    detailUnit={$.contracts.stableToken.symbol}
                    info={this.getActivity(this.props.game.seigniorageFee.collected.count, this.props.game.seigniorageFee.collected.timestamp, now)}
                />
            </div>
        );

        const isCurrentBetPoolSenderUnderWin = this.isSenderUnderWin(this.props.game.currentBetPool.statement);

        return (
            <div className="grid grid-cols-1 gap-y-3">
                <div className={`card bg-indigo-900/25 bg-diagonal-lines`}>
                    <div className="grid grid-cols-12 gap-x-2 gap-y-0.5">
                        <div className="col-span-5">
                            <span className="card-header-title-text opacity-75">
                                <span className="mr-1">{this.props.game.name}</span>
                                <StyledPopover children={popoverGameContent} />
                            </span>
                        </div>

                        <div className="col-span-7 flex items-center justify-end card-header-item-text">
                            <span className="uppercase opacity-50">Time to Draw</span>
                            <span className="numeric opacity-75 ml-1 glow">{$.convert.formatTimespan(timeToDraw)}</span>
                            <span className="2xs:hidden xs:block numeric opacity-75 ml-2">{blocksToDraw}</span>
                            <span className="2xs:hidden xs:block uppercase opacity-50 ml-1">blocks</span>
                        </div>

                        <div className="col-span-8 flex items-center justify-start card-header-item-text">
                            <span className="2xs:hidden xs:block opacity-25">Draws every</span>
                            <span className="2xs:hidden xs:block numeric opacity-50 ml-1">{this.props.game.blockLength}</span>
                            <span className="2xs:hidden xs:block opacity-25 ml-1 mr-2">blocks.</span>
                            <span className="uppercase opacity-50">Pool Id</span>
                            <span className="numeric opacity-25 ml-1">{$.convert.formatInteger(this.props.game.currentBetPool.poolId)}</span>
                        </div>

                        <div className="col-span-4 flex items-center justify-end card-header-item-text">
                            <span className="numeric opacity-25">
                                <i className="bi bi-box mr-1" />
                                {$.convert.formatInteger(this.props.game.currentBlock)}
                            </span>
                        </div>

                        <div className="col-span-12 mt-0.5 mb-3 border-bottom-t2 border-2"></div>
                    </div>

                    <div className="grid grid-cols-1 gap-y-3">
                        <div className="grid grid-cols-2 gap-x-2">
                            <PriceBox
                                title="Your Unclaimed Prize"
                                main={this.format.formatToken(senderUnclaimedPrize)}
                                mainUnit={this.props.game.tokenInfo.details.symbol}
                                detail={$.convert.formatStableToken(senderUnclaimedPrize * this.props.priceInStableToken)}
                                detailUnit={$.contracts.stableToken.symbol}
                                mainClassName="glow"
                            />
                            <div className="grid grid-cols-2 gap-x-2 gap-y-3 leading-none auto-rows-auto text-[0.75em]">
                                <button
                                    type="button"
                                    className="w-full h-9 justify-self-end self-end btn btn-green"
                                    disabled={this.state.isInteractionDisabled || !isPrizeClaimable} onClick={async () => {
                                        await this.execute(() => this.props.claim());
                                    }}
                                >
                                    Claim the Prize
                                </button>
                                <button
                                    type="button"
                                    className="w-full h-9 justify-self-start self-end btn btn-blue"
                                    disabled={this.state.isInteractionDisabled || !isPrizeClaimable} onClick={async () => {
                                        await this.execute(() => this.props.compound());
                                    }}
                                >
                                    Bet the Prize
                                </button>
                                <button
                                    type="button"
                                    className="w-full h-9 justify-self-center self-start col-span-2 btn btn-red"
                                    disabled={this.state.isInteractionDisabled || this.props.balance === 0} onClick={async () => {
                                        await this.execute(() => this.props.bet());
                                    }}
                                >
                                    Place a Bet
                                </button>
                            </div>
                        </div>

                        <div className="border-bottom-t2"></div>

                        <div className="grid grid-cols-2 xl:grid-cols-4 gap-x-2 gap-y-4 text-[80%]">
                            <PriceBox
                                title="Winning Outcome"
                                main={getOutcomeText(parseInt(this.props.game.outcomeCount), parseInt(this.props.game.currentBetPool.outcome))}
                                detailUnit={parseInt(this.props.game.currentBetPool.outcome) === BYTE_MAX_VALUE ? (<><span className="animate-pulse">Place a Bet</span></>) : (<><span className="animate-pulse">Bet to Change</span></>)}
                                titleClassName="light-box"
                                mainClassName="font-display neon"
                            />
                            <PriceBox
                                title="Pot"
                                main={this.format.formatToken(currentBetPoolPot)}
                                mainUnit={this.props.game.tokenInfo.details.symbol}
                                detail={$.convert.formatStableToken(currentBetPoolPot * this.props.priceInStableToken)}
                                detailUnit={$.contracts.stableToken.symbol}
                                // info={(<span><i className="bi bi-app-indicator mr-1" />{$.convert.formatInteger(this.props.game.currentBetPool.statement.allAggregatedBets.count)}</span>)}
                                titleClassName="light-box"
                            />

                            <PriceBox
                                title="Potential Prize"
                                main={this.format.formatToken(currentBetPoolWinningPrize)}
                                mainUnit={this.props.game.tokenInfo.details.symbol}
                                detail={$.convert.formatStableToken(currentBetPoolWinningPrize * this.props.priceInStableToken)}
                                detailUnit={$.contracts.stableToken.symbol}
                                info={isCurrentBetPoolSenderUnderWin ? "Under Win" : null}
                                titleClassName="light-box"
                                mainClassName="neon"
                                infoClassName={`glow ${isCurrentBetPoolSenderUnderWin ? " blinker" : null}}`}
                            />
                            <PriceBox
                                title="PnL"
                                main={this.format.formatToken(senderPnL)}
                                mainUnit={this.props.game.tokenInfo.details.symbol}
                                detail={$.convert.formatStableToken(senderPnL * this.props.priceInStableToken)}
                                detailUnit={$.contracts.stableToken.symbol}
                                titleClassName="light-box"
                                mainClassName="glow"
                            />

                            <div className="col-span-2 xl:col-span-4">
                                {this.getBetInfo(this.props.game.currentBetPool, this.props.game.outcomeCount)}
                            </div>
                        </div>

                        <div className="border-bottom-t2"></div>

                        <div className="grid grid-cols-2 gap-x-2 text-[0.75em]">
                            <button
                                type="button"
                                className="w-full h-9 btn btn-dark"
                                disabled={parseInt(this.props.game.lastBetPool.poolId) === 0}
                                onClick={this.toggleLastBetPoolVisibility}>
                                Last Bet Pool{this.state.showLastBetPool && <i className="bi bi-caret-down-fill ml-1" />}
                            </button>
                            <button
                                type="button"
                                className="w-full h-9 btn btn-dark"
                                disabled={parseInt(this.props.game.senderLastBetPool.poolId) === 0}
                                onClick={this.toggleSenderLastBetPoolVisibility}>
                                Your Last Bet Pool{this.state.showSenderLastBetPool && <i className="bi bi-caret-down-fill ml-1" />}
                            </button>
                        </div>

                        <div className="border-bottom-t2"></div>

                        <div className="flex items-center justify-between">
                            <span className="card-header-item-text uppercase opacity-75 cursor-pointer" onClick={this.togglePoolTotalsVisibility}><i className={`bi ${(this.state.isPoolTotalsVisibile ? "bi-caret-down-fill" : "bi-caret-right-fill")} mr-1`} />
                                Pool Totals
                            </span>
                            <span className={`card-header-item-text opacity-25 ${parseInt(this.props.game.senderBetPoolCount) === 0 ? "disabled" : "cursor-pointer"}`} onClick={() => {
                                if (parseInt(this.props.game.senderBetPoolCount) > 0) {
                                    this.toggleYourBetHistoryVisibility();
                                }
                            }}>
                                <i className="bi bi-calendar3 mr-1" />
                                Your Bet History (<span className="numeric">{$.convert.formatInteger(this.props.game.senderBetPoolCount)}</span>)
                            </span>
                        </div>

                        {this.state.isPoolTotalsVisibile &&
                            <div className="grid grid-cols-3 gap-x-2 text-[80%]">
                                <PriceBox
                                    title="Placed Bets"
                                    main={this.format.formatToken(totalPlacedBets)}
                                    mainUnit={this.props.game.tokenInfo.details.symbol}
                                    detail={$.convert.formatStableToken(totalPlacedBets * this.props.priceInStableToken)}
                                    detailUnit={$.contracts.stableToken.symbol}
                                    info={this.getActivity(this.props.game.totalPosition.placedBets.count, this.props.game.totalPosition.placedBets.timestamp, now)}
                                />
                                <PriceBox
                                    title="Claimed Bets"
                                    main={this.format.formatToken(totalClaimedBets)}
                                    mainUnit={this.props.game.tokenInfo.details.symbol}
                                    detail={$.convert.formatStableToken(totalClaimedBets * this.props.priceInStableToken)}
                                    detailUnit={$.contracts.stableToken.symbol}
                                    info={this.getActivity(this.props.game.totalPosition.claimedBets.count, this.props.game.totalPosition.claimedBets.timestamp, now)}
                                />
                                <PriceBox
                                    title="Claimed Prizes"
                                    main={this.format.formatToken(totalClaimedPrizes)}
                                    mainUnit={this.props.game.tokenInfo.details.symbol}
                                    detail={$.convert.formatStableToken(totalClaimedPrizes * this.props.priceInStableToken)}
                                    detailUnit={$.contracts.stableToken.symbol}
                                    info={this.getActivity(this.props.game.totalPosition.claimedPrizes.count, this.props.game.totalPosition.claimedPrizes.timestamp, now)}
                                />
                            </div>
                        }
                    </div>
                </div>

                {this.state.showLastBetPool && this.getBetPool("Last Bet Pool", this.props.game.lastBetPool, this.props.game.outcomeCount)}
                {this.state.showSenderLastBetPool && this.getBetPool("Your Last Bet Pool", this.props.game.senderLastBetPool, this.props.game.outcomeCount)}
                {this.state.showYourBetHistory && this.getSenderBetPools()}
            </div>
        );
    }

    getSenderBetPools() {
        const items = [];

        for (let i = 0; i < this.state.senderBetPools.length; ++i) {
            items.push(<div key={i}>{this.getBetPool(`Your Bet Pool #${i + 1}`, this.state.senderBetPools[i])}</div>);
        }

        return (
            <>
                {items}
                <div className="flex items-center justify-center text-[0.75em]">
                    <button
                        type="button"
                        className="w-48 h-9 btn btn-dark"
                        disabled={(this.state.isLoading || this.state.senderBetPools.length === parseInt(this.props.game.senderBetPoolCount))}
                        onClick={this.loadSenderBetPools}>Load more</button>
                </div>
                {this.state.isLoading && <Spinner type="cloud" title={`Loading ${$.convert.formatInteger(this.props.game.senderBetPoolCount)} pools.`} />}
            </>
        );
    }

    getBetPool(title, betPool, outcomeCount) {
        const blocksFromDraw = parseInt(this.props.game.currentBlock) - parseInt(betPool.poolId);
        const lastBetPoolPot = this.format.fromWei(betPool.statement.allAggregatedBets.total);
        const lastBetPoolWinningPrize = this.format.fromWei(betPool.statement.senderPrize);
        const isUnderWin = this.isSenderUnderWin(betPool.statement);

        const senderAggregatedBets = this.format.fromWei(betPool.statement.senderAggregatedBets.total);
        const senderLosingBets = this.format.fromWei(betPool.statement.senderLosingBets.total);
        const senderPrize = this.format.fromWei(betPool.statement.senderPrize);
        const hasOpponent = this.isWinner(betPool) > 0;
        const senderPnL = hasOpponent ? (senderPrize - senderLosingBets) : senderAggregatedBets;

        return (
            <>
                <div className={`card bg-white/5 bg-diagonal-lines`}>
                    <div className="grid grid-cols-12 gap-x-2 gap-y-0.5">
                        <div className="col-span-5">
                            <span className="card-header-title-text opacity-75">
                                {title}
                            </span>
                        </div>
                        <div className="col-span-7 flex items-center justify-end card-header-item-text">
                            <span className="uppercase opacity-50">Time from Draw</span>
                            <span className="numeric opacity-75 ml-1 glow">{betPool.poolId > 0 ? $.convert.formatTimespan(blocksFromDraw * this.props.blockTime) : "-"}</span>
                            <span className="2xs:hidden xs:block numeric opacity-75 ml-2">{betPool.poolId > 0 ? $.convert.formatInteger(blocksFromDraw) : "-"}</span>
                            <span className="2xs:hidden xs:block uppercase opacity-50 ml-1">blocks</span>
                        </div>
                        <div className="col-span-7 flex items-center justify-start card-header-item-text">
                            <span className="uppercase opacity-50">Pool Id</span>
                            <span className="numeric opacity-25 ml-1">{betPool.poolId > 0 ? $.convert.formatInteger(betPool.poolId) : "-"}</span>
                        </div>
                        <div className="col-span-5 flex items-center justify-end card-header-item-text">
                            <span className="opacity-25">
                                You {this.isWinnerText(betPool)}
                            </span>
                        </div>

                        <div className="col-span-12 mt-0.5 mb-3 border-bottom-t2 border-2"></div>
                    </div>

                    <div className="grid grid-cols-2 sm:grid-cols-4 gap-x-2 gap-y-4 text-[80%]">
                        <PriceBox
                            title="Outcome"
                            main={getOutcomeText(parseInt(this.props.game.outcomeCount), parseInt(betPool.outcome))}
                            titleClassName="light-box"
                            mainClassName="font-display neon"
                        />
                        <PriceBox
                            title="Pot"
                            main={this.format.formatToken(lastBetPoolPot)}
                            mainUnit={this.props.game.tokenInfo.details.symbol}
                            detail={$.convert.formatStableToken(lastBetPoolPot * this.props.priceInStableToken)}
                            detailUnit={$.contracts.stableToken.symbol}
                            // info={(<span><i className="bi bi-app-indicator mr-1" />{$.convert.formatInteger(betPool.statement.allAggregatedBets.count)}</span>)}
                            titleClassName="light-box"
                        />

                        <PriceBox
                            title="Your Prize"
                            main={this.format.formatToken(lastBetPoolWinningPrize)}
                            mainUnit={this.props.game.tokenInfo.details.symbol}
                            detail={$.convert.formatStableToken(lastBetPoolWinningPrize * this.props.priceInStableToken)}
                            detailUnit={$.contracts.stableToken.symbol}
                            info={isUnderWin ? "Under Win" : null}
                            titleClassName="light-box"
                            mainClassName="neon"
                            infoClassName={`glow ${isUnderWin ? " blinker" : null}}`}
                        />
                        <PriceBox
                            title="PnL"
                            main={this.format.formatToken(senderPnL)}
                            mainUnit={this.props.game.tokenInfo.details.symbol}
                            detail={$.convert.formatStableToken(senderPnL * this.props.priceInStableToken)}
                            detailUnit={$.contracts.stableToken.symbol}
                            titleClassName="light-box"
                            mainClassName="glow"
                        />

                        <div className="col-span-2 sm:col-span-4">
                            {this.getBetInfo(betPool, outcomeCount)}
                        </div>
                    </div>
                </div>
            </>
        );
    }

    isSenderUnderWin(winningStatement) {
        const senderPrize = this.format.fromWei(winningStatement.senderPrize);
        const senderLosingBets = this.format.fromWei(winningStatement.senderLosingBets.total);

        return senderPrize > 0 && senderPrize < senderLosingBets;
    }

    getPopoverWinningStatementAllBetsDetail(winningStatement) {
        const allAggregatedBets = this.format.fromWei(winningStatement.allAggregatedBets.total);
        const allWinningBets = this.format.fromWei(winningStatement.allWinningBets.total);
        const allLosingBets = this.format.fromWei(winningStatement.allLosingBets.total);

        return (
            <div className="grid grid-cols-2 gap-x-2 gap-y-4 text-[80%]">
                <PriceBox
                    title="All Bets"
                    main={this.format.formatToken(allAggregatedBets)}
                    mainUnit={this.props.game.tokenInfo.details.symbol}
                    detail={$.convert.formatStableToken(allAggregatedBets * this.props.priceInStableToken)}
                    detailUnit={$.contracts.stableToken.symbol}
                    info={(<span><i className="bi bi-app-indicator mr-1" />{$.convert.formatInteger(winningStatement.allAggregatedBets.count)}</span>)}
                />
                <PriceBox
                    title="Winning Bets"
                    main={this.format.formatToken(allWinningBets)}
                    mainUnit={this.props.game.tokenInfo.details.symbol}
                    detail={$.convert.formatStableToken(allWinningBets * this.props.priceInStableToken)}
                    detailUnit={$.contracts.stableToken.symbol}
                    info={(<span><i className="bi bi-app-indicator mr-1" />{$.convert.formatInteger(winningStatement.allWinningBets.count)}</span>)}
                />
                <PriceBox
                    title="Losing Bets"
                    main={this.format.formatToken(allLosingBets)}
                    mainUnit={this.props.game.tokenInfo.details.symbol}
                    detail={$.convert.formatStableToken(allLosingBets * this.props.priceInStableToken)}
                    detailUnit={$.contracts.stableToken.symbol}
                    info={(<span><i className="bi bi-app-indicator mr-1" />{$.convert.formatInteger(winningStatement.allLosingBets.count)}</span>)}
                    mainClassName="glow"
                />
                <PriceBox
                    title="Payout"
                    main={`${$.convert.formatNumberScaled(allWinningBets === 0 ? 0 : allLosingBets / allWinningBets, 2)}`}
                    mainClassName="neon"
                />
            </div>
        );
    }

    getPopoverWinningStatementSenderBetsDetail(winningStatement) {
        const allWinningBets = this.format.fromWei(winningStatement.allWinningBets.total);

        const senderAggregatedBets = this.format.fromWei(winningStatement.senderAggregatedBets.total);
        const senderWinningBets = this.format.fromWei(winningStatement.senderWinningBets.total);
        const senderLosingBets = this.format.fromWei(winningStatement.senderLosingBets.total);

        return (
            <div className="grid grid-cols-2 gap-x-2 gap-y-4 text-[80%]">
                <PriceBox
                    title="All Bets"
                    main={this.format.formatToken(senderAggregatedBets)}
                    mainUnit={this.props.game.tokenInfo.details.symbol}
                    detail={$.convert.formatStableToken(senderAggregatedBets * this.props.priceInStableToken)}
                    detailUnit={$.contracts.stableToken.symbol}
                    info={(<span><i className="bi bi-app-indicator mr-1" />{$.convert.formatInteger(winningStatement.senderAggregatedBets.count)}</span>)}
                />
                <PriceBox
                    title="Winning Bets"
                    main={this.format.formatToken(senderWinningBets)}
                    mainUnit={this.props.game.tokenInfo.details.symbol}
                    detail={$.convert.formatStableToken(senderWinningBets * this.props.priceInStableToken)}
                    detailUnit={$.contracts.stableToken.symbol}
                    info={(<span><i className="bi bi-app-indicator mr-1" />{$.convert.formatInteger(winningStatement.senderWinningBets.count)}</span>)}
                    mainClassName="neon"
                />
                <PriceBox
                    title="Losing Bets"
                    main={this.format.formatToken(senderLosingBets)}
                    mainUnit={this.props.game.tokenInfo.details.symbol}
                    detail={$.convert.formatStableToken(senderLosingBets * this.props.priceInStableToken)}
                    detailUnit={$.contracts.stableToken.symbol}
                    info={(<span><i className="bi bi-app-indicator mr-1" />{$.convert.formatInteger(winningStatement.senderLosingBets.count)}</span>)}
                    mainClassName="glow"
                />
                <PriceBox
                    title="Winning Share"
                    main={$.convert.formatPercentage(allWinningBets === 0 ? 0 : parseFloat(senderWinningBets) / parseFloat(allWinningBets))}
                    mainUnit="%"
                    mainClassName="neon"
                />
            </div>
        );
    }

    getPopoverBetPoolWinningStatementDetail(winningStatement) {
        const allAggregatedBets = this.format.fromWei(winningStatement.allAggregatedBets.total);
        const allWinningBets = this.format.fromWei(winningStatement.allWinningBets.total);
        const allLosingBets = this.format.fromWei(winningStatement.allLosingBets.total);

        const senderAggregatedBets = this.format.fromWei(winningStatement.senderAggregatedBets.total);
        const senderWinningBets = this.format.fromWei(winningStatement.senderWinningBets.total);
        const senderLosingBets = this.format.fromWei(winningStatement.senderLosingBets.total);

        const senderPrize = this.format.fromWei(winningStatement.senderPrize);
        const senderPnL = senderPrize - senderLosingBets;

        return (
            <div className="grid grid-cols-4 gap-x-2 gap-y-4 text-[80%]">
                <span className="group-title opacity-75 col-span-4 glass-box">Total Position</span>
                <PriceBox
                    title="All Bets"
                    main={this.format.formatToken(allAggregatedBets)}
                    mainUnit={this.props.game.tokenInfo.details.symbol}
                    detail={$.convert.formatStableToken(allAggregatedBets * this.props.priceInStableToken)}
                    detailUnit={$.contracts.stableToken.symbol}
                    info={(<span><i className="bi bi-app-indicator mr-1" />{$.convert.formatInteger(winningStatement.allAggregatedBets.count)}</span>)}
                />
                <PriceBox
                    title="Winning Bets"
                    main={this.format.formatToken(allWinningBets)}
                    mainUnit={this.props.game.tokenInfo.details.symbol}
                    detail={$.convert.formatStableToken(allWinningBets * this.props.priceInStableToken)}
                    detailUnit={$.contracts.stableToken.symbol}
                    info={(<span><i className="bi bi-app-indicator mr-1" />{$.convert.formatInteger(winningStatement.allWinningBets.count)}</span>)}
                />
                <PriceBox
                    title="Losing Bets"
                    main={this.format.formatToken(allLosingBets)}
                    mainUnit={this.props.game.tokenInfo.details.symbol}
                    detail={$.convert.formatStableToken(allLosingBets * this.props.priceInStableToken)}
                    detailUnit={$.contracts.stableToken.symbol}
                    info={(<span><i className="bi bi-app-indicator mr-1" />{$.convert.formatInteger(winningStatement.allLosingBets.count)}</span>)}
                    mainClassName="glow"
                />
                <PriceBox
                    title="Payout"
                    main={`${$.convert.formatNumberScaled(allWinningBets === 0 ? 0 : allLosingBets / allWinningBets, 2)}`}
                    mainClassName="neon"
                />

                <span className="group-title opacity-75 col-span-4 glass-box">Your Position</span>
                <PriceBox
                    title="All Bets"
                    main={this.format.formatToken(senderAggregatedBets)}
                    mainUnit={this.props.game.tokenInfo.details.symbol}
                    detail={$.convert.formatStableToken(senderAggregatedBets * this.props.priceInStableToken)}
                    detailUnit={$.contracts.stableToken.symbol}
                    info={(<span><i className="bi bi-app-indicator mr-1" />{$.convert.formatInteger(winningStatement.senderAggregatedBets.count)}</span>)}
                />
                <PriceBox
                    title="Winning Bets"
                    main={this.format.formatToken(senderWinningBets)}
                    mainUnit={this.props.game.tokenInfo.details.symbol}
                    detail={$.convert.formatStableToken(senderWinningBets * this.props.priceInStableToken)}
                    detailUnit={$.contracts.stableToken.symbol}
                    info={(<span><i className="bi bi-app-indicator mr-1" />{$.convert.formatInteger(winningStatement.senderWinningBets.count)}</span>)}
                    mainClassName="neon"
                />
                <PriceBox
                    title="Losing Bets"
                    main={this.format.formatToken(senderLosingBets)}
                    mainUnit={this.props.game.tokenInfo.details.symbol}
                    detail={$.convert.formatStableToken(senderLosingBets * this.props.priceInStableToken)}
                    detailUnit={$.contracts.stableToken.symbol}
                    info={(<span><i className="bi bi-app-indicator mr-1" />{$.convert.formatInteger(winningStatement.senderLosingBets.count)}</span>)}
                    mainClassName="glow"
                />
                <PriceBox
                    title="PnL"
                    main={this.format.formatToken(senderPnL)}
                    mainUnit={this.props.game.tokenInfo.details.symbol}
                    detail={$.convert.formatStableToken(senderPnL * this.props.priceInStableToken)}
                    detailUnit={$.contracts.stableToken.symbol}
                    mainClassName="glow"
                />
                <PriceBox
                    containerClassName="col-span-4"
                    title="Winning Share"
                    main={$.convert.formatPercentage(allWinningBets === 0 ? 0 : parseFloat(senderWinningBets) / parseFloat(allWinningBets))}
                    mainUnit="%"
                    mainClassName="neon"
                />
            </div>
        );
    }

    getActivity(count, timestamp, now) {
        return (
            <div className="flex justify-evenly space-x-1">
                <span className="light-box w-full"><i className="hidden xs:inline-block bi bi-app-indicator mr-1" />{$.convert.formatInteger(count)}</span>
                <span className="light-box w-full"><i className="hidden xs:inline-block bi bi-clock mr-1" />{timestamp > 0 ? $.convert.formatTimespan(now - timestamp) : "-"}</span>
            </div>
        );
    }

    // Game functions.
    getPayoutOf(betPool, outcome) {
        const zero = Web3.utils.toBN(0);

        let winningBets = zero;
        let losingBets = zero;

        const allBets = betPool.allBets;

        for (let i = 0; i < allBets.length; ++i) {
            var bet = allBets[i];

            if (i === outcome)
                winningBets = winningBets.add(Web3.utils.toBN(bet.total));
            else
                losingBets = losingBets.add(Web3.utils.toBN(bet.total));
        }

        if (winningBets.cmp(zero) === 0) {
            return null;
        }

        return this.format.fromWei(losingBets) / this.format.fromWei(winningBets);
    }

    // Content generators
    getOutcomeNames() {
        const items = [];

        for (let i = 0; i < this.props.game.outcomeCount; ++i) {
            items.push((<th key={i} className="text-[1em] font-normal light-box px-2">{getOutcomeText(parseInt(this.props.game.outcomeCount), i)}</th>));
        }

        return items;
    }

    getBetCounts(bets) {
        const items = [];

        for (let i = 0; i < this.props.game.outcomeCount; ++i) {
            items.push((<td key={i} className="text-[1em] lighter-box px-2">{bets[i]?.count ? $.convert.formatInteger(bets[i].count) : 0}</td>));
        }

        return items;
    }

    getBetAmounts(bets) {
        const items = [];

        for (let i = 0; i < this.props.game.outcomeCount; ++i) {
            items.push((<td key={i}><span className="text-[1em] px-2 py-1">{bets[i]?.total ? this.format.formatToken(this.format.fromWei(bets[i].total)) : 0}</span></td>));
        }

        return items;
    }

    getBetsSum(bets) {
        let sum = 0;

        for (let i = 0; i < this.props.game.outcomeCount; ++i) {
            sum += bets[i]?.total ? this.format.fromWei(bets[i].total) : 0;
        }

        return sum;
    }

    getPayouts(betPool) {
        const payouts = [];

        for (let i = 0; i < this.props.game.outcomeCount; ++i) {
            payouts.push(this.getPayoutOf(betPool, i));
        }

        const max = Math.max(...payouts);
        const items = [];

        for (let i = 0; i < payouts.length; ++i) {
            items.push((<td key={i}><span className={`text-[1em] px-2 py-1 ${payouts[i] === max ? " glow" : ""}`}>{payouts[i] ? $.convert.formatNumberScaled(payouts[i], 2) : ""}</span></td>));
        }

        return items;
    }

    getAllBets(betPool) {
        return (
            <table className="mx-auto table-fixed border-separate leading-none w-full">
                <thead className="group-title uppercase tracking-wider">
                    <tr>
                        {this.getOutcomeNames()}
                    </tr>
                </thead>
                <tbody className="numeric">
                    <tr className="opacity-75">
                        {this.getBetCounts(betPool.allBets)}
                    </tr>
                    <tr>
                        {this.getBetAmounts(betPool.allBets)}
                    </tr>
                    <tr>
                        {this.getPayouts(betPool)}
                    </tr>
                </tbody>
            </table>
        )
    }

    getSenderBets(betPool) {
        return (
            <table className="mx-auto table-fixed border-separate leading-none w-full">
                <thead className="group-title uppercase tracking-wider">
                    <tr>
                        {this.getOutcomeNames()}
                    </tr>
                </thead>
                <tbody className="numeric">
                    <tr className="opacity-75">
                        {this.getBetCounts(betPool.senderBets)}
                    </tr>
                    <tr>
                        {this.getBetAmounts(betPool.senderBets)}
                    </tr>
                    <tr>
                        <td colSpan={this.props.game.outcomeCount}>
                            {/* &nbsp; */}
                            {/* <span className="text-[14px] px-2 py-1 glow">
                                {this.format.formatToken(this.getBetsSum(betPool.senderBets))}
                            </span> */}
                        </td>
                    </tr>
                </tbody>
            </table>
        );
    }

    getBetInfo(betPool, outcomeCount) {
        return (
            <>
                <div className={`grid ${outcomeCount > 2 ? "grid-cols-1 sm:grid-cols-2" : "grid-cols-2"} gap-x-2 gap-y-2 text-center`}>
                    <div className="flex flex-col items-center">
                        <span className="group-title light-box w-full uppercase tracking-wider">
                            <span className="mr-1">Total Bets</span>
                            <StyledPopover children={this.getPopoverWinningStatementAllBetsDetail(betPool.statement)} />
                        </span>
                        {/* <span className="group-title opacity-25 mt-1">{this.props.game.tokenInfo.details.symbol}</span> */}
                        <div className="overflow-x-auto overflow-y-hidden section">
                            {this.getAllBets(betPool)}
                        </div>
                    </div>
                    <div className="flex flex-col items-center">
                        <span className="group-title light-box w-full uppercase tracking-wider">
                            <span className="mr-1">Your Bets</span>
                            <StyledPopover children={this.getPopoverWinningStatementSenderBetsDetail(betPool.statement)} />
                        </span>
                        {/* <span className="group-title opacity-25 mt-1">{this.props.game.tokenInfo.details.symbol}</span> */}
                        <div className="overflow-x-auto overflow-y-hidden section">
                            {this.getSenderBets(betPool)}
                        </div>
                    </div>
                </div>
            </>
        );
    }

    isWinner(betPool) {
        if (betPool.senderBets.length > 0) {
            return betPool.senderBets[betPool.outcome].count > 0
                ? Web3.utils.toBN(betPool.statement.allLosingBets.count).cmp(Web3.utils.toBN(0)) === 1 ? 1 : -1
                : Web3.utils.toBN(betPool.statement.allWinningBets.count).cmp(Web3.utils.toBN(0)) === 1 ? 2 : -1;
        } else {
            return 0;
        }
    }

    isWinnerText(betPool) {
        switch (this.isWinner(betPool)) {
            case -1:
                return "had no opponent";
            case 0:
                return "didn't bet";
            case 1:
                return "won";
            case 2:
                return "lost";
        }
    }

    // Actions
    togglePoolTotalsVisibility() {
        this.setState({ isPoolTotalsVisibile: !this.state.isPoolTotalsVisibile });
    }

    toggleLastBetPoolVisibility() {
        this.setState({ showLastBetPool: !this.state.showLastBetPool });
    }

    toggleSenderLastBetPoolVisibility() {
        this.setState({ showSenderLastBetPool: !this.state.showSenderLastBetPool });
    }

    async execute(func) {
        this.setState({ isInteractionDisabled: true });

        try {
            await func();
        } catch (error) {
            console.log(`${error.code}: ${error.message}`);
        } finally {
            this.setState({ isInteractionDisabled: false });
        }
    }

    async toggleYourBetHistoryVisibility() {
        const show = this.state.showYourBetHistory;
        this.setState({ showYourBetHistory: !show });

        if (!show) {
            await this.loadSenderBetPools();
        }
    }

    async loadSenderBetPools() {
        this.setState({ isLoading: true });

        try {
            await this.loadSenderBetPoolsInternal();
        } catch (error) {
            console.error(error);
        } finally {
            this.setState({ isLoading: false });
        }
    }

    async loadSenderBetPoolsInternal() {
        const pageLength = 3;

        const senderBetPoolCount = parseInt(this.props.game.senderBetPoolCount);
        const remaining = senderBetPoolCount - this.state.senderBetPools.length;

        if (remaining > 0) {
            let offset;
            let count;

            offset = this.state.senderBetPools.length;

            if (remaining > pageLength) {
                count = pageLength;
            } else {
                count = remaining;
            }

            let pools = [...await this.props.senderPoolSnapshots(offset, count)];

            if (pools.length > 0 && parseInt(pools[pools.length - 1].poolId) === parseInt(this.props.game.currentPoolId)) {
                pools = pools.slice(0, pools.length - 1);
            }

            this.setState({ senderBetPools: [...this.state.senderBetPools, ...pools] });
        }
    }
}