import Web3 from "web3";

import IERC20 from "../abi/IERC20.json";
import IUniswapV2Pair from "../abi/IUniswapV2Pair.json";

import Configuration from "../../../Configuration";
import { fromWei, toWei } from "../../../utils/Web3Converter";
import {
    formatAccount,
    formatDateTime,
    formatTimespan,
    formatInteger,
    formatPercentage,
    formatNumberScaled,
    formatCurrency
} from "../../../utils/Format";
import { MAX_UINT256, promisify, waitMined } from "../../../utils/Utils";

import { createReaderContract, createReaders, createSigners } from "./Contracts";

export default (function Services() {
    const self = {
        init: (context) => {
            if (!context.chainId || !Configuration.isSupportedChain(context.chainId)) {
                console.log("Can not initialize services due to unsupported chain.");
                return;
            }

            self.chainId = context.chainId;
            self.account = context.account;

            self.nativeCoin = Configuration.getNativeCoin(context.chainId);
            self.contracts = Configuration.getContracts(context.chainId);

            if (context.reader) {
                self.reader = context.reader;
                self.readers = createReaders(context.reader, context.account, self.contracts);
            }

            if (context.signer) {
                self.signer = context.signer;
                self.signers = createSigners(context.signer, context.account, self.contracts);
            }
        },
        settings: {
            slippageToleranceKey: "slippageTolerance",
            getSlippageTolerance: () => parseFloat(window.localStorage.getItem(self.settings.slippageToleranceKey) ?? Configuration.getSlippageTolerance()),
            setSlippageTolerance: (value) => {
                if (value) {
                    window.localStorage.setItem(self.settings.slippageToleranceKey, value);
                }
            },
            getTokenListUri: () => {
                const chainId = Web3.utils.toHex(self.chainId == 31337 ? 1 : self.chainId);
                return `https://deep-index.moralis.io/api/v2/${self.account}/erc20?chain=${chainId}`;
            },
        },
        convert: {
            formatAccount: value => formatAccount(value),
            formatDateTime: value => formatDateTime(value),
            formatTimespan: value => formatTimespan(value),
            formatInteger: value => formatInteger(value),
            formatPercentage: (value, maxDecimals = 2, minDecimals = 0) => formatPercentage(value, maxDecimals, minDecimals),
            formatNumberScaled: (value, maxDecimals = 2, minDecimals = 0) => formatNumberScaled(value, maxDecimals, minDecimals),
            fromWei: (value, decimals) => parseFloat(fromWei(value, decimals)),
            fromWeiNativeCoin: value => self.convert.fromWei(value, self.nativeCoin.decimals),
            fromWeiNativeToken: value => self.convert.fromWei(value, self.contracts.nativeToken.decimals),
            fromWeiStableToken: value => self.convert.fromWei(value, self.contracts.stableToken.decimals),
            fromWeiMainToken: value => self.convert.fromWei(value, self.contracts.token.decimals),
            trimToDecimals: (value, decimals) => {
                if (typeof value === "number") {
                    return parseFloat(value.toFixed(decimals));
                }

                const index = value.indexOf(".");

                if (index != -1) {
                    const count = value.length - (index + 1) - decimals;

                    if (count > 0) {
                        value = value.substr(0, value.length - count);
                    }
                }

                return value;
            },
            toWei: (value, decimals) => toWei(self.convert.trimToDecimals(value, decimals), decimals),
            toWeiNativeCoin: value => self.convert.toWei(value, self.nativeCoin.decimals),
            toWeiNativeToken: value => self.convert.toWei(value, self.contracts.nativeToken.decimals),
            toWeiStableToken: value => self.convert.toWei(value, self.contracts.stableToken.decimals),
            toWeiMainToken: value => self.convert.toWei(value, self.contracts.token.decimals),
            formatCurrency: (value, currencyDecimals) => formatCurrency(value, currencyDecimals),
            formatCurrency: (value, currencyDecimals, maxDecimals, minDecimals) => formatCurrency(value, currencyDecimals, maxDecimals, minDecimals),
            formatNativeCoin: value => self.convert.formatCurrency(value, self.nativeCoin.decimals),
            formatNativeCoin: (value, maxDecimals, minDecimals) => self.convert.formatCurrency(value, self.nativeCoin.decimals, maxDecimals, minDecimals),
            formatNativeToken: value => self.convert.formatCurrency(value, self.contracts.nativeToken.decimals),
            formatNativeToken: (value, maxDecimals, minDecimals) => self.convert.formatCurrency(value, self.contracts.nativeToken.decimals, maxDecimals, minDecimals),
            formatStableToken: value => self.convert.formatCurrency(value, self.contracts.stableToken.decimals),
            formatStableToken: (value, maxDecimals, minDecimals) => self.convert.formatCurrency(value, self.contracts.stableToken.decimals, maxDecimals, minDecimals),
            formatMainToken: value => self.convert.formatCurrency(value, self.contracts.token.decimals),
            formatMainToken: (value, maxDecimals, minDecimals) => self.convert.formatCurrency(value, self.contracts.token.decimals, maxDecimals, minDecimals),
            formatDepositToken: (value, pool) => self.convert.formatCurrency(value, pool.depositToken.details.decimals),
            formatDepositToken: (value, pool, maxDecimals, minDecimals) => self.convert.formatCurrency(value, pool.depositToken.details.decimals, maxDecimals, minDecimals),
        },
        format: decimals => {
            const _format = {
                fromWei: (value) => self.convert.fromWei(value, decimals),
                formatCurrency: (value, maxDecimals = 4, minDecimals = 0) => self.convert.formatCurrency(value, decimals, maxDecimals, minDecimals),
                formatToken: (value, maxDecimals = 4, minDecimals = 0) => _format.formatCurrency(value, maxDecimals, minDecimals),
            };

            return _format;
        },
        block: {
            getBlock: (blockId) => promisify(cb => self.reader.eth.getBlock(blockId, cb)),
            getAverageBlockTime: async count => {
                const blockF = await self.block.getBlock("latest");
                const now = Date.now() / 1000;
                const blockI = await self.block.getBlock(blockF.number - count);
                const blockTime = (blockF.timestamp - blockI.timestamp) / count;
                console.log(`Block Time: ${blockTime} s; F: ${blockF.timestamp} - I: ${blockI.timestamp} = ${blockF.timestamp - blockI.timestamp} s. Lag: ${now - blockF.timestamp} s.`);

                return blockTime;
            }
        },
        pair: {
            getTokenReserves: async (tokenAddress, decimals) => {
                // Check the order of pair tokens.
                const isTokenFirst = Web3.utils.toBN(tokenAddress).cmp(Web3.utils.toBN(self.contracts.nativeToken.address)) === -1;
                const reserves = await promisify(cb => self.readers.pair.methods.getReserves().call(cb));

                return isTokenFirst
                    ? {
                        tokenReserve: self.convert.fromWei(reserves.reserve0, decimals),
                        nativeTokenReserve: self.convert.fromWei(reserves.reserve1, self.contracts.nativeToken.decimals)
                    }
                    : {
                        tokenReserve: self.convert.fromWei(reserves.reserve1, decimals),
                        nativeTokenReserve: self.convert.fromWei(reserves.reserve0, self.contracts.nativeToken.decimals)
                    };
            },
            getMainTokenReserves: () => self.pair.getTokenReserves(self.contracts.token.address, self.contracts.token.decimals),
            getMainTokenInitialReserves: () => {
                const initialLiquidity = Configuration.getInitialLiquidity(self.chainId);

                return {
                    initialTokenReserve: self.convert.fromWeiMainToken(initialLiquidity.tokenReserve),
                    initialNativeTokenReserve: self.convert.fromWeiNativeToken(initialLiquidity.nativeTokenReserve)
                };
            }
        },
        iziSwap: {
            quote: (sourceRouter, sourceToken, isSourceLiquidityToken, sourceTokenAmount, targetRouter, targetToken, isTargetLiquidityToken) => promisify(cb => self.readers.iziSwap.methods.quote(sourceRouter, sourceToken, isSourceLiquidityToken, sourceTokenAmount, targetRouter, targetToken, isTargetLiquidityToken).call(cb)),
            swap: async (sourceRouter, sourceToken, isSourceLiquidityToken, sourceTokenAmount, targetRouter, targetToken, isTargetLiquidityToken, to, deadline, amountOutMin) => waitMined(self.reader, await promisify(cb => self.signers.iziSwap.methods.swap(sourceRouter, sourceToken, isSourceLiquidityToken, sourceTokenAmount, targetRouter, targetToken, isTargetLiquidityToken, to, deadline, amountOutMin).send(cb))),
            getTokenPriceInStableToken: async (
                depositToken,
                decimals,
                isLiquidityToken
            ) => {
                const depositRouter = Configuration.getDefaultSwapRouter(self.chainId);
                const creditRouter = depositRouter;
                const amount = await self.iziSwap.quote(depositRouter, depositToken, isLiquidityToken, self.convert.toWei(1, decimals), creditRouter, self.contracts.stableToken.address, false);

                return self.convert.fromWeiStableToken(amount);
            },
            getTokenPriceInMainToken: async (
                depositToken,
                decimals,
                isLiquidityToken
            ) => {
                const depositRouter = Configuration.getDefaultSwapRouter(self.chainId);
                const creditRouter = depositRouter;
                const amount = await self.iziSwap.quote(depositRouter, depositToken, isLiquidityToken, self.convert.toWei(1, decimals), creditRouter, self.contracts.token.address, false);

                return self.convert.fromWeiMainToken(amount);
            }
        },
        router: {
            getTokenPriceInStableToken: async (tokenAddress, decimals) => {
                const amounts = await promisify(cb => self.readers.router.methods.getAmountsOut(self.convert.toWei(1, decimals), [
                    tokenAddress,
                    self.contracts.nativeToken.address,
                    self.contracts.stableToken.address
                ]).call(cb));

                return self.convert.fromWeiStableToken(amounts[amounts.length - 1]);
            },
            getMainTokenPriceInStableToken: () => {
                return self.router.getTokenPriceInStableToken(self.contracts.token.address, self.contracts.token.decimals);
            },
            getNativeTokenPriceInStableToken: async () => {
                const amounts = await promisify(cb => self.readers.router.methods.getAmountsOut(self.convert.toWei(1, self.contracts.nativeToken.decimals), [
                    self.contracts.nativeToken.address,
                    self.contracts.stableToken.address
                ]).call(cb));

                return self.convert.fromWeiStableToken(amounts[amounts.length - 1]);
            },
            getTokenPriceInMainToken: async (tokenAddress, decimals) => {
                const amounts = await promisify(cb => self.readers.router.methods.getAmountsOut(self.convert.toWei(1, decimals), [
                    tokenAddress,
                    self.contracts.nativeToken.address,
                    self.contracts.token.address
                ]).call(cb));

                return self.convert.fromWeiMainToken(amounts[amounts.length - 1]);
            },
            getMainTokenAmountFromNativeCoin: async (nativeCoinAmountIn) => {
                const amounts = await promisify(cb => self.readers.router.methods.getAmountsOut(self.convert.toWeiNativeCoin(nativeCoinAmountIn), [
                    self.contracts.nativeToken.address,
                    self.contracts.token.address
                ]).call(cb));

                return self.convert.fromWeiMainToken(amounts[amounts.length - 1]);
            },
            getNativeCoinAmountFromMainToken: async (mainTokenAmountIn) => {
                const amounts = await promisify(cb => self.readers.router.methods.getAmountsOut(self.convert.toWeiMainToken(mainTokenAmountIn), [
                    self.contracts.token.address,
                    self.contracts.nativeToken.address
                ]).call(cb));

                return self.convert.fromWeiNativeCoin(amounts[amounts.length - 1]);
            },
            swapNativeTokenForMainToken: async (nativeCoinAmountIn, tokenAmountOutMin, recipient) => {
                return waitMined(self.reader,
                    await promisify(cb => self.signers.router.methods.swapExactETHForTokensSupportingFeeOnTransferTokens(
                        self.convert.toWeiMainToken(tokenAmountOutMin),
                        [self.contracts.nativeToken.address, self.contracts.token.address],
                        recipient ? recipient : self.account,
                        parseInt((Date.now() / 1000) + 3600)
                    ).send({ value: self.convert.toWeiNativeCoin(nativeCoinAmountIn) }, cb)));
            },
            swapMainTokenForNativeToken: async (mainTokenAmountIn, amountNativeTokenOutMin, recipient) => {
                return waitMined(self.reader,
                    await promisify(cb => self.signers.router.methods.swapExactTokensForETHSupportingFeeOnTransferTokens(
                        self.convert.toWeiMainToken(mainTokenAmountIn),
                        self.convert.toWeiNativeCoin(amountNativeTokenOutMin),
                        [self.contracts.token.address, self.contracts.nativeToken.address],
                        recipient ? recipient : self.account,
                        parseInt((Date.now() / 1000) + 3600)
                    ).send(cb)));
            },
        },
        token: {
            balanceOf: async (tokenAddress) => {
                const reader = createReaderContract(self.reader, IERC20, tokenAddress, { from: self.account });
                return promisify(cb => reader.methods.balanceOf(self.account).call(cb));
            },
            transfer: async (recipient, amount) => waitMined(self.reader, await promisify(cb => self.signers.token.methods.transfer(recipient, amount).send(cb))),
            getAllowance: (owner, spender) => promisify(cb => self.readers.token.methods.allowance(owner, spender).call(cb)),
            getTokenSnapshot: () => promisify(cb => self.readers.token.methods.senderTokenSnapshot().call(cb)),
            harvestPendingReward: async () => waitMined(self.reader, await promisify(cb => self.readers.token.methods.harvestPendingReward().send(cb))),
            approveRouter: async () => waitMined(self.reader, await promisify(cb => self.signers.token.methods.approve(self.readers.router.options.address, MAX_UINT256).send(cb))),
            approveGame: async () => waitMined(self.reader, await promisify(cb => self.signers.token.methods.approve(self.readers.game.options.address, MAX_UINT256).send(cb))),
            isAllowed: async (minAmount, allowance) => allowance.eq(Web3.utils.toBN(0)) ? false : allowance.cmp(Web3.utils.toBN(minAmount)) >= 0,
            isAllowanceSatisfied: async (spender, minAmount) => {
                console.log(`minAmount: ${minAmount}`);
                const allowance = Web3.utils.toBN(await self.token.getAllowance(self.account, spender));
                return self.token.isAllowed(minAmount, allowance);
            },
        },
        game: {
            getAllowance: () => self.token.getAllowance(self.account, self.readers.game.options.address),
            getSnapshots: (isEnabled) => promisify(cb => self.readers.game.methods.senderGameSnapshots(isEnabled).call(cb)),
            getSenderBetPoolCount: (gameId) => promisify(cb => self.readers.game.methods.senderBetPoolCount(gameId).call(cb)),
            getSenderPoolSnapshots: (gameId, offset, count) => promisify(cb => self.readers.game.methods.senderPoolSnapshots(gameId, offset, count).call(cb)),
            getSenderBetPools: (gameId) => promisify(cb => self.readers.game.methods.senderBetPools(gameId).call(cb)),
            betMainToken: async (gameId, amount, outcome) => {
                console.log("bet main");
                return waitMined(self.reader, await promisify(cb => self.signers.game.methods.bet(gameId, amount, outcome).send(cb)));
            },
            bet: async (gameId, sourceToken, sourceAmount, outcome, gameTokenInfo) => {
                const sourceAmountInWei = self.convert.toWei(sourceAmount, sourceToken.info.details.decimals);
                const amountOut = self.convert.fromWei(await self.iziSwap.quote(sourceToken.info.router, sourceToken.info.token, sourceToken.isLiquidityToken, sourceAmountInWei, gameTokenInfo.router, gameTokenInfo.token, false), gameTokenInfo.details.decimals);
                const amountMin = amountOut * self.settings.getSlippageTolerance() / 10000;
                const amountOutMin = amountOut - amountMin;

                return waitMined(self.reader, await promisify(cb => self.signers.game.methods.bet(gameId, sourceToken.info.router, sourceToken.info.token, sourceToken.isLiquidityToken, self.convert.toWei(amountOut, gameTokenInfo.details.decimals), self.convert.toWei(amountOutMin, gameTokenInfo.details.decimals), outcome).send(cb)));
            },
            compound: async (gameId, outcome) => waitMined(self.reader, await promisify(cb => self.signers.game.methods.compound(gameId, outcome).send(cb))),
            claim: async (gameId) => waitMined(self.reader, await promisify(cb => self.signers.game.methods.claim(gameId).send(cb))),
            harvestPendingReward: (gameId, outcome) => self.game.betMainToken(gameId, 0, outcome),
            isAllowanceSatisfied: async (minAmount) => {
                const allowance = Web3.utils.toBN(await self.game.getAllowance());
                return self.token.isAllowed(minAmount, allowance);
            },
        },
        farm: {
            getDepositTokenSymbol: (pool) => pool.isLiquidityToken ? `${pool.token0Details.symbol}/${pool.token1Details.symbol}` : pool.depositToken.details.symbol,
            conversionRateOf: (depositRouter, depositToken, isLiquidityToken, deposit, creditRouter, creditToken) => promisify(cb => self.readers.farm.methods.conversionRateOf(depositRouter, depositToken, isLiquidityToken, deposit, creditRouter, creditToken).call(cb)),
            getAllowance: (reader) => promisify(cb => reader.methods.allowance(self.account, self.readers.farm.options.address).call(cb)),
            approveFarm: async (signer) => waitMined(self.reader, await promisify(cb => signer.methods.approve(self.readers.farm.options.address, MAX_UINT256).send(cb))),
            getSnapshot: (isEnabled) => promisify(cb => self.readers.farm.methods.senderFarmSnapshot(isEnabled).call(cb)),
            getLiquidatablesSnapshot: (isEnabled) => promisify(cb => self.readers.farm.methods.senderFarmLiquidatablesSnapshot(isEnabled).call(cb)),
            REMOVE_deposit: async (poolId, amount) => waitMined(self.reader, await promisify(cb => self.signers.farm.methods.deposit(poolId, amount, self.settings.getSlippageTolerance()).send(cb))),
            deposit: async (poolId, sourceToken, sourceAmount, pool) => {
                const sourceAmountInWei = self.convert.toWei(sourceAmount, sourceToken.info.details.decimals);
                const amountOut = self.convert.fromWei(await self.iziSwap.quote(sourceToken.info.router, sourceToken.info.token, sourceToken.isLiquidityToken, sourceAmountInWei, pool.depositToken.router, pool.depositToken.token, pool.isLiquidityToken), pool.depositToken.details.decimals);
                const amountMin = amountOut * self.settings.getSlippageTolerance() / 10000;
                const amountOutMin = amountOut - amountMin;

                return waitMined(self.reader, await promisify(cb => self.signers.farm.methods.deposit(poolId, sourceToken.info.router, sourceToken.info.token, sourceToken.isLiquidityToken, self.convert.toWei(amountOut, pool.depositToken.details.decimals), self.convert.toWei(amountOutMin, pool.depositToken.details.decimals)).send(cb)));
            },
            withdraw: async (poolId, amount) => waitMined(self.reader, await promisify(cb => self.signers.farm.methods.withdraw(poolId, amount).send(cb))),
            rebalance: async (poolId) => waitMined(self.reader, await promisify(cb => self.signers.farm.methods.rebalance(poolId).send(cb))),
            liquidate: async (poolId) => waitMined(self.reader, await promisify(cb => self.signers.farm.methods.liquidateSenderUnchecked(poolId, self.settings.getSlippageTolerance()).send(cb))),
            liquidateAccount: async (poolId, investor) => waitMined(self.reader, await promisify(cb => self.signers.farm.methods.liquidateAccount(poolId, investor, self.settings.getSlippageTolerance()).send(cb))),
            isAllowanceSatisfied: async (pool, minAmount) => {
                const reader = createReaderContract(self.reader, pool.isLiquidityToken ? IUniswapV2Pair : IERC20, pool.depositToken.token, { from: self.account });
                const allowance = Web3.utils.toBN(await self.farm.getAllowance(reader));
                return self.token.isAllowed(minAmount, allowance);
            },
            getTokenPriceInDepositToken: async (token, pool) => {
                const amount = await self.iziSwap.quote(token.info.router, token.info.token, token.isLiquidityToken, self.convert.toWei(1, token.info.details.decimals), pool.depositToken.router, pool.depositToken.token, pool.isLiquidityToken);

                return self.convert.fromWei(amount, pool.depositToken.details.decimals);
            },
        },
        tokenFinder: {
            findAll: (tokenAddress) => promisify(cb => self.readers.tokenFinder.methods.findAll(tokenAddress).call(cb)),
        }
    };

    return self;
})();