import Configuration from "../../Configuration";

export function getSupportedSwapRouterName(chainId, pool) {
    const supportedSwapRouters = Configuration.getSwapRouters(chainId);
    const address = pool.depositToken.router;

    return address == null || supportedSwapRouters == null ? null : supportedSwapRouters[address].name ?? null;
}

export function getDepositTokenSymbol(pool) {
    return pool.isLiquidityToken ? `${pool.token0Details.symbol}/${pool.token1Details.symbol}` : pool.depositToken.details.symbol;
}

export function getDepositTokenDecimals(pool) {
    return pool.isLiquidityToken ? Math.min(pool.token0Details.decimals, pool.token1Details.decimals) : pool.depositToken.details.decimals;
}