import React, { Component, Fragment } from "react";
import PropTypes from "prop-types";

import Spinner from "../../Spinner";

import { WalletContext } from "../../contexts/WalletContext";

import { createSignerContract } from "./core/Contracts";
import $ from "./core/Services";

import IERC20 from "./abi/IERC20.json";
import IUniswapV2Pair from "./abi/IUniswapV2Pair.json";

import Token from "./Token";
import Swap from "./Swap";
import Transfer from "./Transfer";
import TokenCalculators from "./TokenCalculators";
import Game from "./Game";
import FarmPools from "./FarmPools";
import FarmLiquidatables from "./FarmLiquidatables";

import BuyCryptoDialog from "./dialogs/BuyCryptoDialog";
import SwapDialog from "./dialogs/SwapDialog";
import ApproveRouterDialog from "./dialogs/ApproveRouterDialog";
import ApproveHarvesterDialog from "./dialogs/ApproveHarvesterDialog";
import GameBetDialog from "./dialogs/GameBetDialog";
import ApproveFarmDialog from "./dialogs/ApproveFarmDialog";
import FarmDepositDialog from "./dialogs/FarmDepositDialog";
import AmountSelectionDialog from "./dialogs/AmountSelectionDialog";

import { ShowComponentType } from "./Enums";

export default class Main extends Component {
    static propTypes = {
        interval: PropTypes.number
    }

    static defaultProps = {
        interval: undefined /* will force avg. block time calculation */,
        showComponent: ShowComponentType.Token
    };

    static contextType = WalletContext;

    constructor(props) {
        super(props)

        this.state = {
            blockTime: undefined,
            tokenPriceInStableToken: undefined,
            tokenPriceInNativeToken: undefined,
            nativeTokenPriceInStableToken: undefined,
            token: undefined,
            games: undefined,
            farm: undefined,

            showBuyCryptoDialog: false,
            showSwapDialog: false,
            showRouterApproveDialog: {
                show: false,
                callback: null
            },
            showGameApproveDialog: {
                show: false,
                callback: null
            },
            showGameHarvestDialog: {
                show: false,
                balance: 0,
                callback: null
            },
            showGameCompoundDialog: {
                show: false,
                gameId: -1,
                balance: 0,
                callback: null
            },
            showGameBetDialog: {
                show: false,
                gameId: -1,
                balance: 0,
                callback: null
            },
            showFarmApproveDialog: {
                show: false,
                callback: null
            },
            showFarmDepositDialog: {
                show: false,
                symbol: null,
                balance: 0,
                depositTokenPriceInStableToken: 0,
                callback: null
            },
            showFarmWithdrawDialog: {
                show: false,
                symbol: null,
                balance: 0,
                creditTokenPriceInStableToken: 0,
                callback: null
            },

            showToken: (props.showComponent & ShowComponentType.Token) === ShowComponentType.Token,
            showGames: (props.showComponent & ShowComponentType.Games) === ShowComponentType.Games,
            showFarms: (props.showComponent & ShowComponentType.Farms) === ShowComponentType.Farms,
            showLiquidatables: (props.showComponent & ShowComponentType.Liquidatables) === ShowComponentType.Liquidatables,
        }

        this.blockLength = 10;

        this.onTick = this.onTick.bind(this);

        this.showComponent = this.showComponent.bind(this);

        this.swap = this.swap.bind(this);
        this.harvestPendingReward = this.harvestPendingReward.bind(this);
        this.gameBet = this.gameBet.bind(this);
        this.gameCompound = this.gameCompound.bind(this);
        this.farmDeposit = this.farmDeposit.bind(this);
        this.farmWithdraw = this.farmWithdraw.bind(this);
    }

    async componentDidMount() {
        if (await this.initialize()) {
            console.log("Initialized on component mount.");
        }
    }

    componentWillUnmount() {
        this.stopTimer();
    }

    async componentDidUpdate(prevProps) {
        if (await this.initialize()) {
            console.log("Initialized on component update.");
        }

        if (this.props.showComponent != prevProps.showComponent) {
            this.showComponent(this.props.showComponent);
        }
    }

    async initialize() {
        if (this.isInitializing) {
            return false;
        }

        this.isInitializing = true;

        try {
            if (this.context.chainId /*&& this.context.account #to make the reader work with disconnected account, at least to get the price*/) {
                if (this.context.reader) {
                    $.init(this.context);

                    if (!this.timerId) {
                        const blockTime = this.props.interval ?? (await $.block.getAverageBlockTime(this.blockLength) * 1000);
                        this.startTimer(blockTime);

                        return true;
                    }
                }

                if (this.context.signer) {
                    $.init(this.context);
                }
            }

            return false;
        } finally {
            this.isInitializing = false;
        }
    }

    startTimer(blockTime) {
        this.timerId = setInterval(async () => await this.onTick(), blockTime);
        console.log(`Started timer: ${this.timerId}; Interval: ${blockTime} ms`);
    }

    stopTimer() {
        const timerId = this.timerId;

        if (timerId) {
            clearInterval(timerId);
            this.timerId = undefined;

            console.log(`Stopped timer: ${timerId}`);
        }
    }

    render() {
        const isHomePage = !this.state.showGames && !this.state.showFarms && !this.state.showLiquidatables;

        return (
            <main className="mt-3 grid grid-cols-1 gap-y-10 mx-auto">
                {(this.state.showToken && this.state.token)
                    ? <Fragment key={"token"}>
                        <Token
                            showCompact={true}
                            showHeader={isHomePage}
                            showHarvestButton={isHomePage || this.state.showGames}
                            token={this.state.token}
                            priceInNativeToken={this.state.tokenPriceInNativeToken}
                            priceInStableToken={this.state.tokenPriceInStableToken}
                            harvestPendingReward={this.harvestPendingReward}
                            blockTime={this.state.blockTime}
                        />
                    </Fragment>
                    : this.state.showToken && <Spinner title="Loading token snapshot" />
                }

                {!isHomePage &&
                    <div className="flex items-center justify-between gap-x-5">
                        <button
                            type="button"
                            className={`min-w-fit w-fit h-9 px-3 ${this.state.showToken ? "-my-5" : ""} font-semibold btn btn-indigo text-[80%]`}
                            onClick={() => {
                                this.setState({
                                    showBuyCryptoDialog: true
                                });
                            }}
                        >
                            Buy Crypto <i className="bi bi-credit-card" />
                        </button>

                        {this.state.token &&
                            <button
                                type="button"
                                className={`min-w-fit w-fit h-9 px-3 ${this.state.showToken ? "-my-5" : ""} font-semibold btn btn-rose text-[80%]`}
                                onClick={() => {
                                    this.setState({
                                        showSwapDialog: true
                                    });
                                }}
                            >
                                Buy {$.contracts.token.symbol}
                            </button>
                        }
                    </div>
                }

                {(this.state.showToken && isHomePage) && this.state.token && this.state.tokenPriceInNativeToken && this.state.tokenPriceInStableToken &&
                    <>
                        <div className="grid grid-cols-1 sm:grid-cols-2 gap-x-5 gap-y-10">
                            <div className={`card bg-weeb-card1/20 bg-diagonal-lines`}>
                                <Swap
                                    nativeCoinBalance={this.context.balance}
                                    token={this.state.token}
                                    nativeTokenPriceInStableToken={this.state.nativeTokenPriceInStableToken}
                                    swap={async (amount, minReceived, recipient, isBuy) => {
                                        try {
                                            await this.swap(amount, minReceived, recipient, isBuy);
                                        } catch (error) {
                                            alert(JSON.stringify(error));
                                        }
                                    }}
                                />
                            </div>
                            <div className={`card bg-weeb-card1/20 bg-diagonal-lines`}>
                                <Transfer
                                    balance={$.convert.fromWeiMainToken(this.state.token.senderFreeBalance)}
                                    transfer={async (recipient, amount) => {
                                        try {
                                            await $.token.transfer(recipient, amount);
                                        } catch (error) {
                                            alert(JSON.stringify(error));
                                        }
                                    }}
                                />
                            </div>
                        </div>
                        <div className="grid grid-cols-1 gap-y-5">
                            <div className="flex items-center justify-center group-title opacity-75 light-box h-9 px-2">
                                <span>Calculators</span>
                            </div>
                            <TokenCalculators
                                token={this.state.token}
                                priceInNativeToken={this.state.tokenPriceInNativeToken}
                                priceInStableToken={this.state.tokenPriceInStableToken}
                                blockTime={this.state.blockTime}
                            />
                        </div>
                    </>
                }
                {((this.state.showGames && this.state.showToken) && this.state.token && this.state.games)
                    ? <div className="grid grid-cols-1 lg:grid-cols-2 gap-x-5 gap-y-10">
                        {this.getGameCards()}
                    </div>
                    : (this.state.showGames && this.state.showToken) && <Spinner type="cloud" />
                }
                {(this.state.showFarms && this.state.token && this.state.farm)
                    ? <FarmPools
                        farm={this.state.farm}
                        nativeTokenPriceInStableToken={this.state.nativeTokenPriceInStableToken}
                        deposit={this.farmDeposit}
                        withdraw={this.farmWithdraw}
                        blockTime={this.state.blockTime}
                    />
                    : this.state.showFarms && <Spinner type="cloud" />
                }
                {(this.state.showLiquidatables && this.state.token && this.state.farmLiquidatables)
                    ? <Fragment key={"farmLiquidatables"}>
                        <FarmLiquidatables
                            farm={this.state.farmLiquidatables}
                            nativeTokenPriceInStableToken={this.state.nativeTokenPriceInStableToken}
                            blockTime={this.state.blockTime}
                            buyWeeb={this.farmBuyWeebInternal}
                        />
                    </Fragment>
                    : this.state.showLiquidatables && <Spinner type="cloud" />
                }
                {this.state.showBuyCryptoDialog &&
                    <BuyCryptoDialog
                        isOpen={this.state.showBuyCryptoDialog}
                        setIsOpen={() => {
                            this.setState({
                                showBuyCryptoDialog: false
                            });
                        }}
                        canceled={() => {
                            this.setState({
                                showBuyCryptoDialog: false
                            });
                        }}
                    />
                }
                {this.state.showSwapDialog &&
                    <SwapDialog
                        isOpen={this.state.showSwapDialog}
                        setIsOpen={() => {
                            this.setState({
                                showSwapDialog: false
                            });
                        }}
                        nativeCoinBalance={this.context.balance}
                        token={this.state.token}
                        nativeTokenPriceInStableToken={this.state.nativeTokenPriceInStableToken}
                        swap={async (amount, minReceived, recipient, isBuy) => {
                            try {
                                await this.swap(amount, minReceived, recipient, isBuy);
                            } catch (error) {
                                alert(JSON.stringify(error));
                            }
                        }}
                        canceled={() => {
                            this.setState({
                                showSwapDialog: false
                            });
                        }}
                    />
                }
                {this.state.showRouterApproveDialog.show &&
                    <ApproveRouterDialog
                        isOpen={this.state.showRouterApproveDialog.show}
                        setIsOpen={() => {
                            this.setState({
                                showRouterApproveDialog: {
                                    show: false
                                }
                            });
                        }}
                        submitted={async () => {
                            const callback = this.state.showRouterApproveDialog.callback;
                            this.setState({
                                showRouterApproveDialog: {
                                    show: false
                                }
                            });
                            await callback();
                        }}
                        canceled={() => {
                            this.setState({
                                showRouterApproveDialog: {
                                    show: false
                                }
                            });
                        }}
                    />
                }
                {this.state.showGameApproveDialog.show &&
                    <ApproveHarvesterDialog
                        isOpen={this.state.showGameApproveDialog.show}
                        setIsOpen={() => {
                            this.setState({
                                showGameApproveDialog: {
                                    show: false
                                }
                            });
                        }}
                        submitted={async () => {
                            const callback = this.state.showGameApproveDialog.callback;
                            this.setState({
                                showGameApproveDialog: {
                                    show: false
                                }
                            });
                            await callback();
                        }}
                        canceled={() => {
                            this.setState({
                                showGameApproveDialog: {
                                    show: false
                                }
                            });
                        }}
                    />
                }
                {this.state.showGameHarvestDialog.show &&
                    <GameBetDialog
                        isOpen={this.state.showGameHarvestDialog.show}
                        setIsOpen={() => {
                            this.setState({
                                showGameHarvestDialog: {
                                    show: false
                                }
                            });
                        }}
                        title="Harvest Pending Reward"
                        submitButtonText="Harvest"
                        submitted={async (gameId, selectedToken, amount, outcome, tokenInfo) => {
                            const callback = this.state.showGameHarvestDialog.callback;
                            this.setState({
                                showGameHarvestDialog: {
                                    show: false
                                }
                            });
                            await callback(gameId, outcome);
                        }}
                        canceled={() => {
                            this.setState({
                                showGameHarvestDialog: {
                                    show: false
                                }
                            });
                        }}
                        games={this.state.games}
                        defaultGameId={"0"}
                        defaultOutcome={"0"}
                        balance={this.state.showGameHarvestDialog.balance}
                        enableGameSelection={true}
                        enableAmountEntry={false}
                        token={this.state.token}
                    />
                }
                {this.state.showGameCompoundDialog.show &&
                    <GameBetDialog
                        isOpen={this.state.showGameCompoundDialog.show}
                        setIsOpen={() => {
                            this.setState({
                                showGameCompoundDialog: {
                                    show: false
                                }
                            });
                        }}
                        title="Compound / Bet the Prize"
                        submitButtonText="Bet"
                        submitted={async (gameId, selectedToken, amount, outcome, tokenInfo) => {
                            const callback = this.state.showGameCompoundDialog.callback;
                            this.setState({
                                showGameCompoundDialog: {
                                    show: false
                                }
                            });
                            await callback(gameId, outcome);
                        }}
                        canceled={() => {
                            this.setState({
                                showGameCompoundDialog: {
                                    show: false
                                }
                            });
                        }}
                        games={this.state.games}
                        defaultGameId={this.state.showGameCompoundDialog.gameId}
                        defaultOutcome={0}
                        balance={this.state.showGameCompoundDialog.balance}
                        enableGameSelection={false}
                        enableAmountEntry={false}
                        token={this.state.token}
                    />
                }
                {this.state.showGameBetDialog.show &&
                    <GameBetDialog
                        isOpen={this.state.showGameBetDialog.show}
                        setIsOpen={() => {
                            this.setState({
                                showGameBetDialog: {
                                    show: false
                                }
                            });
                        }}
                        title="Place a Bet"
                        submitButtonText="Bet"
                        submitted={async (gameId, selectedToken, amount, outcome, tokenInfo) => {
                            const callback = this.state.showGameBetDialog.callback;
                            this.setState({
                                showGameBetDialog: {
                                    show: false
                                }
                            });
                            await callback(gameId, selectedToken, amount, outcome, tokenInfo);
                        }}
                        canceled={() => {
                            this.setState({
                                showGameBetDialog: {
                                    show: false
                                }
                            });
                        }}
                        games={this.state.games}
                        defaultGameId={this.state.showGameBetDialog.gameId}
                        defaultOutcome={0}
                        balance={this.state.showGameBetDialog.balance}
                        enableGameSelection={false}
                        enableAmountEntry={true}
                        token={this.state.token}
                    />
                }
                {this.state.showFarmApproveDialog.show &&
                    <ApproveFarmDialog
                        isOpen={this.state.showFarmApproveDialog.show}
                        setIsOpen={() => {
                            this.setState({
                                showFarmApproveDialog: {
                                    show: false
                                }
                            });
                        }}
                        submitted={async () => {
                            const callback = this.state.showFarmApproveDialog.callback;
                            this.setState({
                                showFarmApproveDialog: {
                                    show: false
                                }
                            });
                            await callback();
                        }}
                        canceled={() => {
                            this.setState({
                                showFarmApproveDialog: {
                                    show: false
                                }
                            });
                        }}
                    />
                }
                {this.state.showFarmDepositDialog.show &&
                    <FarmDepositDialog
                        isOpen={this.state.showFarmDepositDialog.show}
                        setIsOpen={() => {
                            this.setState({
                                showFarmDepositDialog: {
                                    show: false
                                }
                            });
                        }}
                        title={`Deposit (${this.state.showFarmDepositDialog.symbol})`}
                        submitButtonText="Deposit"
                        submitted={async (selectedToken, selectedTokenAmount, depositTokenAmountInWei) => {
                            const callback = this.state.showFarmDepositDialog.callback;
                            this.setState({
                                showFarmDepositDialog: {
                                    show: false
                                }
                            });
                            await callback(selectedToken, selectedTokenAmount, depositTokenAmountInWei);
                        }}
                        canceled={() => {
                            this.setState({
                                showFarmDepositDialog: {
                                    show: false
                                }
                            });
                        }}
                        pool={this.state.showFarmDepositDialog.pool}

                    />
                }
                {this.state.showFarmWithdrawDialog.show &&
                    <AmountSelectionDialog
                        isOpen={this.state.showFarmWithdrawDialog.show}
                        setIsOpen={() => {
                            this.setState({
                                showFarmWithdrawDialog: {
                                    show: false
                                }
                            });
                        }}
                        title={`Withdraw (${this.state.showFarmWithdrawDialog.symbol})`}
                        submitButtonText="Withdraw"
                        submitted={async (amount) => {
                            const callback = this.state.showFarmWithdrawDialog.callback;
                            this.setState({
                                showFarmWithdrawDialog: {
                                    show: false
                                }
                            });
                            await callback(amount);
                        }}
                        canceled={() => {
                            this.setState({
                                showFarmWithdrawDialog: {
                                    show: false
                                }
                            });
                        }}
                        balance={this.state.showFarmWithdrawDialog.balance}
                        priceInStableToken={this.state.showFarmWithdrawDialog.creditTokenPriceInStableToken}
                        decimals={4}
                    />
                }
            </main>
        );
    }

    async onTick() {
        try {
            const [
                blockTime,
                tokenPriceInStableToken,
                nativeTokenPriceInStableToken,
                reserves,
                token,
                games,
                farm,
                farmLiquidatables
            ] = await Promise.all([
                $.block.getAverageBlockTime(this.blockLength),
                $.router.getMainTokenPriceInStableToken(),
                $.router.getNativeTokenPriceInStableToken(),
                $.pair.getMainTokenReserves(),
                $.token.getTokenSnapshot(),
                this.state.showToken ? $.game.getSnapshots(1) : Promise.resolve(undefined),
                this.state.showFarms ? $.farm.getSnapshot(1) : Promise.resolve(undefined),
                this.state.showLiquidatables ? $.farm.getLiquidatablesSnapshot(255/*All*/) : Promise.resolve(undefined)
            ]);

            let map;

            if (this.state.showToken) {
                map = new Map();

                for (let i = 0; i < games.length; ++i) {
                    map.set(games[i].gameId, games[i]);
                    // if (games[i].isEnabled === true && equals(games[i].tokenInfo.token, $.contracts.token.address)) {
                    //     map.set(i, games[i]);
                    // }
                }
            }

            this.setState({
                blockTime,
                tokenPriceInStableToken,
                tokenPriceInNativeToken: reserves.nativeTokenReserve / reserves.tokenReserve,
                nativeTokenPriceInStableToken,
                token: {
                    ...token,
                    contractAddress: $.contracts.token.address,
                    tokenReserve: reserves.tokenReserve,
                    nativeTokenReserve: reserves.nativeTokenReserve,
                    ...$.pair.getMainTokenInitialReserves()
                },
                games: map,
                farm: this.state.showFarms ? farm : undefined,
                farmLiquidatables: this.state.showLiquidatables ? farmLiquidatables : undefined
            });
        } catch (error) {
            console.error(error);
        }
    }

    showComponent(componentType) {
        this.setState({
            showToken: (componentType & ShowComponentType.Token) === ShowComponentType.Token,
            showGames: (componentType & ShowComponentType.Games) === ShowComponentType.Games,
            showFarms: (componentType & ShowComponentType.Farms) === ShowComponentType.Farms,
            showLiquidatables: (componentType & ShowComponentType.Liquidatables) === ShowComponentType.Liquidatables
        });
    }

    // Content generators
    getGameCards() {
        const balance = $.convert.fromWeiNativeToken(this.state.token.senderFreeBalance);
        const items = [];

        this.state.games.forEach(game => {
            items.push((
                <div key={game.gameId} className="max-h-full">
                    <Game
                        game={game}
                        balance={balance}
                        priceInStableToken={this.state.tokenPriceInStableToken}
                        bet={() => this.gameBet(game.gameId)}
                        compound={() => this.gameCompound(game.gameId)}
                        claim={() => $.game.claim(game.gameId)}
                        senderPoolSnapshots={(offset, count) => $.game.getSenderPoolSnapshots(game.gameId, offset, count)}
                        blockTime={this.state.blockTime}
                    />
                </div>
            ));
        });

        return items;
    }

    // Actions
    async swap(amount, minReceived, recipient, isBuy) {
        if (isBuy) {
            await $.router.swapNativeTokenForMainToken(amount, minReceived, recipient);
        } else {
            if (await $.token.isAllowanceSatisfied($.readers.router.options.address, $.convert.toWeiMainToken(amount))) {
                await $.router.swapMainTokenForNativeToken(amount, minReceived, recipient);

                return;
            }

            const callback = async () => {
                if (!(await $.token.isAllowanceSatisfied($.readers.router.options.address, $.convert.toWeiMainToken(amount)))) {
                    await $.token.approveRouter();
                }

                await $.router.swapMainTokenForNativeToken(amount, minReceived, recipient);
            };

            this.setState({
                showRouterApproveDialog: {
                    show: true,
                    callback
                }
            });
        }
    }

    async harvestPendingReward() {
        if (this.state.token.isDirectlyHarvestable) {
            await $.token.harvestPendingReward();
        } else {
            if (!this.state.games || this.state.games.size === 0) {
                console.log("No game found.");
                return;
            }

            const callback = async (gameId, outcome) => {
                if (!(await $.game.isAllowanceSatisfied(this.state.token.senderPendingReward))) {
                    this.setState({
                        showGameApproveDialog: {
                            show: true,
                            callback: async () => {
                                await $.token.approveGame();
                                await $.game.harvestPendingReward(gameId, outcome);

                                this.showComponent(ShowComponentType.Token | ShowComponentType.Games);
                            }
                        }
                    });
                } else {
                    await $.game.harvestPendingReward(gameId, outcome);

                    this.showComponent(ShowComponentType.Token | ShowComponentType.Games);
                }
            };

            this.setState({
                showGameHarvestDialog: {
                    show: true,
                    balance: $.convert.fromWeiMainToken(this.state.token.senderFreeBalance),
                    callback
                }
            });
        }
    }

    async gameBet(selectedGameId) {
        const callback = async (gameId, sourceToken, sourceAmount, outcome, gameTokenInfo) => {
            if (!(await $.game.isAllowanceSatisfied(this.state.token.senderPendingReward))) {
                this.setState({
                    showGameApproveDialog: {
                        show: true,
                        callback: async () => {
                            await $.token.approveGame();
                            await $.game.bet(gameId, sourceToken, sourceAmount, outcome, gameTokenInfo);
                        }
                    }
                });
            } else {
                await $.game.bet(gameId, sourceToken, sourceAmount, outcome, gameTokenInfo);
            }
        };

        this.setState({
            showGameBetDialog: {
                show: true,
                gameId: selectedGameId,
                balance: $.convert.fromWeiMainToken(this.state.token.senderFreeBalance),
                callback
            }
        });
    }

    async gameCompound(selectedGameId) {
        const callback = async (gameId, outcome) => {
            if (!(await $.game.isAllowanceSatisfied(this.state.token.senderPendingReward))) {
                this.setState({
                    showGameApproveDialog: {
                        show: true,
                        callback: async () => {
                            await $.token.approveGame();
                            await $.game.compound(gameId, outcome);
                        }
                    }
                });
            } else {
                await $.game.compound(gameId, outcome);
            }
        };

        this.setState({
            showGameCompoundDialog: {
                show: true,
                gameId: selectedGameId,
                balance: $.convert.fromWeiMainToken(this.state.token.senderFreeBalance),
                callback
            }
        });
    }

    async farmDeposit(selectedPoolId, depositTokenPriceInStableToken) {
        const pool = this.state.farm.pools[selectedPoolId];

        const callback = async (selectedToken, selectedTokenAmount, depositTokenAmountInWei) => {
            if (!(await $.farm.isAllowanceSatisfied(pool, depositTokenAmountInWei))) {
                this.setState({
                    showFarmApproveDialog: {
                        show: true,
                        callback: async () => {
                            const signer = createSignerContract($.signer, pool.isLiquidityToken ? IUniswapV2Pair : IERC20, pool.depositToken.token, { from: $.account });

                            await $.farm.approveFarm(signer);
                            await $.farm.deposit(selectedPoolId, selectedToken, selectedTokenAmount, pool);
                        }
                    }
                });
            } else {
                await $.farm.deposit(selectedPoolId, selectedToken, selectedTokenAmount, pool);
            }
        };

        this.setState({
            showFarmDepositDialog: {
                show: true,
                symbol: $.farm.getDepositTokenSymbol(pool),
                balance: $.convert.fromWei(pool.senderBalance, pool.depositToken.details.decimals),
                depositTokenPriceInStableToken,
                pool,
                callback
            }
        });
    }

    async farmWithdraw(selectedPoolId, creditTokenPriceInStableToken) {
        const pool = this.state.farm.pools[selectedPoolId];

        const callback = async (amount) => {
            const amountInWei = $.convert.toWei(amount, this.state.farm.creditToken.details.decimals);
            await $.farm.withdraw(selectedPoolId, amountInWei);
        };

        this.setState({
            showFarmWithdrawDialog: {
                show: true,
                symbol: $.farm.getDepositTokenSymbol(pool),
                balance: $.convert.fromWei(pool.senderPosition.credited, this.state.farm.creditToken.details.decimals),
                creditTokenPriceInStableToken,
                callback
            }
        });
    }
}
