import { useState, useEffect } from "react";
import Web3 from "web3";

import $ from "../core/Services";

import Configuration from "../../../Configuration";

import ModalDialog from "./core/ModalDialog";
import RangeInput from "./core/RangeInput";
import PredefinedValueSelector from "./core/PredefinedValueSelector";

import { MainText } from "../../InfoBox";
import { StyledPopover } from "../../StyledPopover";

//import tokens from "../../../tokens.json";

export default function FarmDepositDialog({
    isOpen,
    setIsOpen,
    title,
    submitButtonText,
    submitted,
    canceled,
    pool
}) {

    const [tokens, setTokens] = useState();
    const [selectedToken, setSelectedToken] = useState();
    const [tokenPriceInDepositToken, setTokenPriceInDepositToken] = useState(0);
    const [tokenPriceInStableToken, setTokenPriceInStableToken] = useState(0);
    const [amount, setAmount] = useState(0);
    const [percentage, setPercentage] = useState(0);
    const [isLoading, setIsLoading] = useState();
    const [isValidAddress, setIsValidAddress] = useState();

    const tooltipDescriptionText = (
        <div className="tooltip text-[80%]">
            <p>
                Harvesting rewards though UI requires a minimum bet amount from the pending reward to be placed in a game.
            </p>
            <p>
                The rest of the pending reward is added to your balance.
            </p>
            <p>
                If you don't want to place a bet you need to trigger a transfer event either by claiming game prizes, placing bets, farming, liquidity hunting, etc. or by performing a manual wallet to wallet transfer.
            </p>
        </div>
    );

    useEffect(() => {
        fetch($.settings.getTokenListUri(), {
            headers: {
                "X-API-Key": Configuration.getMoralisApiKey()
            }
        })
            .then(response => response.json())
            .then(values => {
                setTokens(values);
            })
    }, [$.account, $.chainId]);

    return (
        <ModalDialog
            isOpen={isOpen}
            setIsOpen={setIsOpen}
            disableBackgroundCancel={false}
            body={(
                <div className="grid grid-cols-1 gap-y-3">
                    <div className="flex flex-col justify-between">
                        {tokens &&
                            <PredefinedValueSelector
                                predefinedValueListTitle="Select the token to bet:"
                                userSuppliedValueTitle="Or enter the token address:"
                                predefinedValues={getTokenList()}
                                defaultValue={pool.depositToken.token}
                                isValidValue={isValidAddress}
                                delay={400}
                                onInput={async address => {
                                    if (!Web3.utils.isAddress(address.toLowerCase())) {
                                        setIsValidAddress(false);
                                        console.log(`${address} is not a valid address.`);
                                        return;
                                    }

                                    address = Web3.utils.toChecksumAddress(address);

                                    setIsValidAddress(true);
                                    setIsLoading(true);

                                    try {
                                        const results = await $.tokenFinder.findAll(address);

                                        if (results.length == 0) {
                                            return;
                                        }

                                        const i = getMaxReservesIndex(results);
                                        const t = results[i];

                                        setSelectedToken(t);

                                        const tokenPriceInDepositToken = await $.farm.getTokenPriceInDepositToken(t, pool);
                                        setTokenPriceInDepositToken(tokenPriceInDepositToken);

                                        const tokenPriceInStableToken = await $.iziSwap.getTokenPriceInStableToken(address, t.info.details.decimals, t.isLiquidityToken);
                                        setTokenPriceInStableToken(tokenPriceInStableToken);
                                    } catch (error) {
                                        setSelectedToken(undefined);
                                        console.error("Token selection error.", error);
                                    } finally {
                                        setIsLoading(false);
                                    }
                                }}
                            />
                        }
                        <span className="text-[0.75em] font-medium text-right">
                            {isLoading
                                ? (<span className="animate-pulse">Fetching...</span>)
                                : selectedToken ? `${getDepositTokenSymbol(selectedToken)} @ ${Configuration.getSwapRouterName($.chainId, selectedToken.info.router)}` : "-"
                            }
                        </span>
                    </div>

                    <div className="grid grid-cols-1 gap-y-0.5">
                        <div className="flex items-center justify-between leading-6">
                            <span className="input-label">
                                Amount:
                                <span className="ml-1">
                                    (
                                    <MainText
                                        main={$.convert.formatPercentage(percentage)}
                                        mainUnit="%"
                                        mainClassName="text-[0.875em]"
                                        mainUnitClassName="ml-0.5"
                                    />
                                    )
                                </span>
                            </span>
                            <span className="input-label">
                                Balance:
                                <MainText
                                    main={selectedToken ? $.convert.formatCurrency($.convert.fromWei(selectedToken.senderBalance, selectedToken.info.details.decimals), selectedToken.info.details.decimals) : "-"}
                                    mainUnit={selectedToken ? getDepositTokenSymbol(selectedToken) : "-"}
                                    mainClassName="text-[0.875em] ml-1"
                                    mainUnitClassName="ml-0.5"
                                />
                            </span>
                        </div>
                        <div className={`${isLoading ? "animate-pulse" : ""}`}>
                            <RangeInput
                                isDisabled={isLoading}
                                min={0}
                                max={selectedToken ? $.convert.fromWei(selectedToken.senderBalance, selectedToken.info.details.decimals) : 0}
                                decimals={selectedToken ? Math.min(4, selectedToken.info.details.decimals) : 0}
                                setValue={setAmount}
                                setPercentage={setPercentage}
                                valueClassName="text-indigo-900 border-indigo-300/50 focus:ring-indigo-300/50"
                                rangeClassName="accent-indigo-900"
                            />
                            <div className="grid grid-cols-2 gap-x-2 mt-3">
                                <div className="justify-self-end">
                                    <MainText
                                        main={$.convert.formatDepositToken(amount * tokenPriceInDepositToken, pool)}
                                        mainUnit={$.farm.getDepositTokenSymbol(pool)}
                                        mainClassName="text-[0.875em]"
                                    />
                                </div>
                                <div className="justify-self-start">
                                    <MainText
                                        main={$.convert.formatStableToken(amount * tokenPriceInStableToken)}
                                        mainUnit={$.contracts.stableToken.symbol}
                                        mainClassName="text-[0.875em]"
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            )}
            title={title}
            info={(<StyledPopover isTooltip={true} children={tooltipDescriptionText} icon="bi-info" />)}
            submitButtonText={submitButtonText}
            isSubmitButtonDisabled={isLoading || amount <= 0}
            submitted={async () => {
                const depositTokenAmountInWei = $.convert.toWei(amount * tokenPriceInDepositToken, pool.depositToken.details.decimals);
                await submitted(selectedToken, amount, depositTokenAmountInWei);
            }}
            canceled={canceled}
        />
    );

    function getMaxReservesIndex(results) {
        if (results.length == 0) {
            return undefined;
        }

        let maxIndex;
        let maxValue;

        for (let i = 0; i < results.length; ++i) {
            const reserve0 = $.convert.fromWei(results[i].reserveInfo.reserve0, results[i].info.details.decimals);
            const reserve1 = $.convert.fromWei(results[i].reserveInfo.reserve1, results[i].info.details.decimals);

            // Selected token's reserve is always returned as reserve0.
            const price = reserve1 / reserve0;
            const k = reserve0 * reserve1;

            if (!maxValue || k > maxValue) {
                maxIndex = i;
                maxValue = k;
            }
        }

        return maxIndex;
    }

    function getTokenList() {
        const items = [
            (<option key={""} value="">- Not in list -</option>),
        ];

        if (!tokens.map(t => t.token_address).includes(pool.depositToken.token)) {
            items.push((<option key={pool.depositToken.token} value={pool.depositToken.token}>{$.farm.getDepositTokenSymbol(pool)}</option>));
        }

        tokens.forEach(token => {
            items.push((<option key={token.token_address} value={token.token_address}>{token.symbol}</option>));
        });

        return items;
    }

    function getDepositTokenSymbol(token) {
        return token.isLiquidityToken ? `${token.token0Details.symbol}/${token.token1Details.symbol}` : token.info.details.symbol;
    }
}
