export function formatAccount(value) {
    return `${value.substring(0, 6)}...${value.substring(value.length - 4)}`;
}

export function formatDateTime(date) {
    const parts = new Date(date).toISOString().split("T");
    return `${parts[0]} ${parts[1]}`;
    // .toLocaleString(undefined, {
    //     year: 'numeric',
    //     month: '2-digit',
    //     day: '2-digit',
    //     hour: '2-digit',
    //     hour12: false,
    //     minute: '2-digit',
    //     second: '2-digit'
    // });
}

export function formatTimespan(timespan) {
    const h = parseInt(timespan / 3600);
    const r = timespan % 3600;
    const m = parseInt(r / 60);
    const s = parseInt(r % 60);

    const hh = h < 10 ? `0${h}` : `${h}`;
    const mm = m < 10 ? `0${m}` : `${m}`;
    const ss = s < 10 ? `0${s}` : `${s}`;

    return `${hh}:${mm}:${ss}`;
}

function formatFloat(value) {
    const parts = value.split(".");

    return (
        <span className="number">
            <span className="integer">
                {parts[0]}
            </span>
            {parts[1] &&
                <span className="decimal">
                    .{parts[1]}
                </span>
            }
        </span>
    );
}

function formatNumber(number, maxDecimals = 2, minDecimals = 0) {
    const result = new Intl.NumberFormat([], {
        maximumFractionDigits: maxDecimals,
        minimumFractionDigits: minDecimals
    }).format(number);

    return result;
}

export function formatInteger(number) {
    return formatNumber(number, 0);
}

export function formatPercentage(number, maxDecimals = 2, minDecimals = 0) {
    console.assert(typeof number === "number", "The 'number' is not of numeric type.");
    return formatNumberScaled(number * 100, maxDecimals, minDecimals);
}

export function formatNumberScaled(number, maxDecimals = 2, minDecimals = 0) {
    console.assert(typeof number === "number", "The 'number' is not of numeric type.");

    if (number === 0)
        return `${number}`;

    if (number >= 10000) {
        return formatNumber(number, Math.min(0, maxDecimals), 0);
    }

    if (number >= 1000) {
        return formatNumber(number, Math.min(1, maxDecimals), 0);
    }

    if (number >= 100) {
        return formatNumber(number, Math.min(2, maxDecimals), 0);
    }

    return formatNumber(number, maxDecimals, minDecimals);
}

export function formatCurrency(number, currencyDecimals, maxDecimals = 4, minDecimals = 0) {
    return formatNumberScaled(number, Math.min(currencyDecimals, maxDecimals), Math.min(currencyDecimals, minDecimals));
}
