import { useState } from "react";
import Web3 from "web3";

import $ from "./core/Services";

import RangeInput from "./dialogs/core/RangeInput";
import DelayedInput from "./dialogs/core/DelayedInput";

import { MainText } from "../InfoBox";

import { MAX_UINT256 } from "../../utils/Utils";

export default function Transfer({
    balance
}) {
    const [isInteractionDisabled, setIsInteractionDisabled] = useState(false);
    const [amount, setAmount] = useState(0);
    const [percentage, setPercentage] = useState(0);
    const [recipient, setRecipient] = useState();

    return (
        <div className="grid grid-cols-1 gap-y-3 items-center">
            <div className="flex items-center justify-center group-title light-box opacity-75 h-9 px-2">
                <span>
                    Transfer {$.contracts.token.symbol}
                </span>
            </div>
            <div className="grid grid-cols-1 gap-y-0.5">
                <div className="flex items-center justify-between leading-6">
                    <span className="input-label">
                        Amount:
                        <span className="ml-1">
                            (
                            <MainText
                                main={$.convert.formatPercentage(percentage)}
                                mainUnit="%"
                                mainClassName="text-[1em]"
                                mainUnitClassName="ml-0.5"
                            />
                            )
                        </span>
                    </span>
                    <span className="input-label">
                        Balance:
                        <MainText
                            main={balance ? $.convert.formatMainToken(balance) : "-"}
                            mainUnit={$.contracts.token.symbol}
                            mainClassName="text-[1em] ml-1"
                            mainUnitClassName="ml-0.5"
                        />
                    </span>
                </div>
                <div>
                    <RangeInput
                        min={0}
                        max={balance ? balance : 0}
                        decimals={Math.min($.contracts.token.decimals, 4)}
                        setValue={setAmount}
                        setPercentage={setPercentage}
                        buttonClassName="btn btn-violet text-indigo-50 numeric w-11 h-7 bg-violet-900/20 hover:text-violet-900 focus:hover:text-violet-200"
                    />
                </div>
            </div>
            <div className="grid grid-cols-1 gap-y-1">
                <label htmlFor="recipient" className="flex leading-6">
                    <span className="input-label">Recipient:</span>
                </label>
                <DelayedInput
                    delay={400}
                    type="search"
                    id="recipient"
                    className="numeric !text-[1em] text-right"
                    placeholder="Enter recipient address"
                    onInput={value => setRecipient(value)}
                />
            </div>
            <div className="flex items-start justify-end">
                <button
                    type="button"
                    className="w-28 h-9 text-[0.75em] btn btn-green"
                    disabled={isInteractionDisabled || !(recipient && Web3.utils.isAddress(recipient.toLowerCase())) || amount === 0}
                    onClick={async () => {
                        const value = amount === balance ? MAX_UINT256 : $.convert.toWeiMainToken(amount);
                        await execute(() => $.token.transfer(recipient, value));
                    }}>
                    Send
                </button>
            </div>
        </div>
    );

    async function execute(func) {
        setIsInteractionDisabled(true);

        try {
            await func();
        } catch (error) {
            console.log(error);
        } finally {
            setIsInteractionDisabled(false);
        }
    }
}
