import { NavLink } from "react-router-dom";

export default function Links() {
  return (
    <>
      <NavLink className="nav-link" to="/" activeclassname="active">Home</NavLink>
      <NavLink className="nav-link" to="/Games" activeclassname="active">Games</NavLink>
      <NavLink className="nav-link" to="/Farms" activeclassname="active">Farms</NavLink>
      <NavLink className="nav-link" to="/Liquidatables" activeclassname="active">Liquidation Hunt</NavLink>
    </>
  );
}
