import { useState } from "react";
import { Portal, Popover } from "@headlessui/react";
import { usePopper } from "react-popper";

export function StyledPopover({ isTooltip, children, icon }) {
    const [referenceElement, setReferenceElement] = useState();
    const [popperElement, setPopperElement] = useState();

    const { styles, attributes } = usePopper(referenceElement, popperElement);

    return (
        <Popover as="span">
            <Popover.Button as="i" ref={setReferenceElement} className={`bi ${isTooltip ? icon ?? "bi-question-circle" : icon ?? "bi-three-dots"} cursor-pointer hover:animate-pulse`}>
            </Popover.Button>

            <Portal>
                <Popover.Panel ref={setPopperElement} style={styles.popper} {...attributes.popper} className="z-10 card popover rounded-lg shadow w-full max-w-md">
                    {children}
                </Popover.Panel>
            </Portal>
        </Popover>
    );
}
