import { useState, useEffect } from "react";
import PropTypes from "prop-types";

import $ from "./core/Services";

import DelayedInput from "./dialogs/core/DelayedInput";
import { PriceBox } from "../InfoBox";

import { getQuoteAssetInflowRequiredToIncreaseThePriceByFactorOf, getBaseAssetOutflowRequiredToDecreaseThePriceByFactorOf } from "../../utils/PriceExtensions";

export default function InflowOutflowCalculator({
    tokenReserve,
    nativeTokenReserve,
    tokenPriceInStableToken,
    nativeTokenPriceInStableToken
}) {
    const Mode = Object.freeze({
        None: 0,
        Inflow: 1,
        Outflow: 2
    });

    const [targetPrice, setTargetPrice] = useState(0);
    const [mode, setMode] = useState(Mode.None);
    const [assetAmountRequired, setAssetAmountRequired] = useState();

    useEffect(() => {
        const factor = targetPrice / tokenPriceInStableToken;

        if (targetPrice > tokenPriceInStableToken) {
            const quoteAssetAmountRequired = getQuoteAssetInflowRequiredToIncreaseThePriceByFactorOf(factor, nativeTokenReserve);
            setAssetAmountRequired(quoteAssetAmountRequired);
            setMode(Mode.Inflow);
        } else if (targetPrice < tokenPriceInStableToken) {
            const baseAssetAmountRequired = getBaseAssetOutflowRequiredToDecreaseThePriceByFactorOf(factor, tokenReserve);
            setAssetAmountRequired(baseAssetAmountRequired);
            setMode(Mode.Outflow);
        } else {
            setAssetAmountRequired(undefined);
            setMode(Mode.None);
        }
    }, [tokenReserve, nativeTokenReserve, tokenPriceInStableToken, nativeTokenPriceInStableToken, targetPrice]);

    return (
        <>
            <div className="grid grid-cols-1 gap-x-2 gap-y-4">
                <div>
                    <span className="text-[0.5em] text-violet-100/50 light-box block mb-4 min-h-[5.5em] xs:min-h-[4em]">
                        Calculates the amount of inflow or outflow required to bring the price to the desired level.
                    </span>
                    <div className="grid grid-cols-1 gap-y-1">
                        <label htmlFor="price" className="flex leading-6">
                            <span className="input-label">Target price</span>
                        </label>
                        <DelayedInput
                            delay={400}
                            type="number"
                            id="price"
                            className="numeric !text-[1em] text-right"
                            defaultValue={$.convert.formatStableToken(tokenPriceInStableToken * 2, 4, 0)}
                            placeholder={`${$.contracts.token.symbol} price in ${$.contracts.stableToken.symbol}`}
                            min={0}
                            step={1}
                            onInput={value => setTargetPrice(parseFloat(value))}
                        />
                    </div>
                </div>

                <div className="text-[80%]">
                    {mode === Mode.Inflow &&
                        <PriceBox
                            title="Required Inflow"
                            main={$.convert.formatNativeToken(assetAmountRequired)}
                            mainUnit={$.contracts.nativeToken.symbol}
                            detail={$.convert.formatStableToken(assetAmountRequired * nativeTokenPriceInStableToken)}
                            detailUnit={$.contracts.stableToken.symbol}
                            mainUnitClassName="neon"
                        />
                    }
                    {mode === Mode.Outflow &&
                        <PriceBox
                            title="Required Outflow"
                            main={$.convert.formatMainToken(assetAmountRequired)}
                            mainUnit={$.contracts.token.symbol}
                            detail={$.convert.formatStableToken(assetAmountRequired * tokenPriceInStableToken)}
                            detailUnit={$.contracts.stableToken.symbol}
                            mainUnitClassName="glow"
                        />
                    }
                    {mode === Mode.None && <p>No change</p>}
                </div>
            </div>
        </>
    );
}

InflowOutflowCalculator.propTypes = {
    tokenReserve: PropTypes.number,
    nativeTokenReserve: PropTypes.number,
    tokenPriceInStableToken: PropTypes.number,
    nativeTokenPriceInStableToken: PropTypes.number
}

InflowOutflowCalculator.defaultProps = {

};
