import ModalDialog from "./core/ModalDialog";

export default function ApproveFarmDialog({
    isOpen,
    setIsOpen,
    submitted,
    canceled
}) {
    return (
        <ModalDialog
            isOpen={isOpen}
            setIsOpen={setIsOpen}
            disableBackgroundCancel={false}
            body={(
                <>
                    <p>The farm contract needs to be approved <u>once</u>.</p>
                    <p>Action will resume after approval.</p>
                </>
            )}
            title="Approve the Farm"
            submitButtonText="Approve"
            submitted={submitted}
            canceled={canceled}
        />
    )
}
