import React, { Component } from "react";
import PropTypes from "prop-types";

import Configuration from "../../Configuration";

import $ from "./core/Services";

import { getSupportedSwapRouterName } from "./FarmPoolExtensions";

import { PriceBox } from "../InfoBox";
import { StyledPopover } from "../StyledPopover";

/*
{
    creditToken,
    pool,
    nativeTokenPriceInStableToken,
    deposit,
    withdraw,
    blockTime,
    currentBlock
}
*/
export default class FarmPool extends Component {
    static propTypes = {
        deposit: PropTypes.func,
        withdraw: PropTypes.func,
    }

    static defaultProps = {
        deposit: async () => {
        },
        withdraw: async () => {
        }
    };

    constructor(props) {
        super(props);

        this.state = {
            isInteractionDisabled: false,
            isPoolTotalsVisibile: false,
            // depositTokenPriceInStableToken: 0,
            // creditTokenPriceInStableToken: 0
        };

        this.onTick = this.onTick.bind(this);

        this.formatDepositToken = $.format(this.props.pool.depositToken.details.decimals);
        this.formatCreditToken = $.format(this.props.creditToken.details.decimals);

        this.togglePoolTotalsVisibility = this.togglePoolTotalsVisibility.bind(this);

        this.execute = this.execute.bind(this);

        this.popoverFarmPoolRef = React.createRef();
        this.tooltipSeigniorageFeeRef = React.createRef();
    }

    async componentDidMount() {
        if (await this.initialize()) {
            console.log("Initialized on component mount.");
        }
    }

    componentWillUnmount() {
        this.stopTimer();
    }

    async componentDidUpdate() {
        if (await this.initialize()) {
            console.log("Initialized on component update.");
        }
    }

    async initialize() {
        if (this.isInitializing) {
            return false;
        }

        this.isInitializing = true;

        try {
            if (!this.timerId) {
                this.startTimer(this.props.blockTime * 1000);

                return true;
            }

            return false;
        } finally {
            this.isInitializing = false;
        }
    }

    startTimer(blockTime) {
        this.timerId = setInterval(async () => await this.onTick(), blockTime);
        console.log(`Started timer: ${this.timerId}; Interval: ${blockTime} ms`);
    }

    stopTimer() {
        const timerId = this.timerId;

        if (timerId) {
            clearInterval(timerId);
            this.timerId = undefined;

            console.log(`Stopped timer: ${timerId}`);
        }
    }

    async onTick() {
        try {
            const depositTokenPriceInStableToken = await $.iziSwap.getTokenPriceInStableToken(this.props.pool.depositToken.token, this.props.pool.depositToken.details.decimals, this.props.pool.isLiquidityToken);
            const creditTokenPriceInStableToken = await $.iziSwap.getTokenPriceInStableToken(this.props.creditToken.token, this.props.creditToken.details.decimals, false);

            this.setState({
                depositTokenPriceInStableToken,
                creditTokenPriceInStableToken
            });
        } catch (error) {
            console.error(error);
        }
    }

    render() {
        const now = Date.now() / 1000;
        const creditMultiplier = parseFloat(this.props.pool.creditMultiplier) / 10000;
        const depositFeeRate = parseFloat(this.props.pool.depositFee.rate) / 10000;
        const creditLiquidationRate = parseFloat(this.props.pool.creditLiquidationRate) / 10000;
        const liquidatorFeeRate = parseFloat(this.props.pool.liquidatorFee.rate) / 10000;
        const senderBalance = this.formatDepositToken.fromWei(this.props.pool.senderBalance);
        const senderDeposited = this.formatDepositToken.fromWei(this.props.pool.senderPosition.deposited);
        const senderCredited = this.formatCreditToken.fromWei(this.props.pool.senderPosition.credited);
        const senderLiquidated = this.formatDepositToken.fromWei(this.props.pool.senderPosition.liquidated.total);
        const senderCreditLiquidationLevel = this.formatCreditToken.fromWei(this.props.pool.senderPosition.creditLiquidationLevel);
        const senderCurrentCredit = this.formatCreditToken.fromWei(this.props.pool.senderCurrentCredit);
        const requiresRebalancing = senderCurrentCredit > senderCredited || senderCurrentCredit < senderCreditLiquidationLevel;
        const totalDeposited = this.formatDepositToken.fromWei(this.props.pool.totalPosition.deposited);
        const totalCredited = this.formatCreditToken.fromWei(this.props.pool.totalPosition.credited);
        const totalLiquidated = this.formatDepositToken.fromWei(this.props.pool.totalPosition.liquidated.total);
        const seigniorageFee = parseFloat(this.props.pool.seigniorageFee.rate) / 10000;
        const seigniorageFeeCollected = $.convert.fromWeiNativeToken(this.props.pool.seigniorageFee.collected.total);
        const seignioryExplorerUri = `${Configuration.getBlockExplorerLink($.chainId)}/address/${this.props.pool.seigniory}`;

        let updatedBlockId = parseInt(this.props.pool.senderPosition.updatedBlockId);
        const senderTimeFromUpdate = updatedBlockId > 0 ? (parseInt(this.props.currentBlock) - updatedBlockId) * this.props.blockTime : 0;

        updatedBlockId = parseInt(this.props.pool.totalPosition.updatedBlockId);
        const totalTimeFromUpdate = updatedBlockId > 0 ? (parseInt(this.props.currentBlock) - updatedBlockId) * this.props.blockTime : 0;

        const tooltipSeigniorageFeeText = (<span className="tooltip text-[80%]">Fee paid to the seignory (token owner) from burns.</span>);

        const popoverFarmPoolContent = (
            <div className="grid grid-cols-3 gap-x-2 gap-y-4 text-[80%]">
                <PriceBox
                    title="Investor Count"
                    main={$.convert.formatInteger(this.props.pool.investorCount)}
                />
                <PriceBox
                    title={(<><span className="mr-1">Seigniorage Fee</span><StyledPopover isTooltip={true} children={tooltipSeigniorageFeeText} /></>)}
                    main={$.convert.formatPercentage(seigniorageFee)}
                    mainUnit="%"
                    detail={(<a href={seignioryExplorerUri} rel="noreferrer" target="_blank" className="numeric">{$.convert.formatAccount(this.props.pool.seigniory)}</a>)}
                    info={this.props.pool.isSenderSeignory ? (<span className="font-display">You're Seignory</span>) : null}
                    infoClassName="opacity-75 light-box font-semibold uppercase text-red-500"
                />
                <PriceBox
                    title="Seigniorage Collected"
                    main={$.convert.formatNativeToken(seigniorageFeeCollected)}
                    mainUnit={$.contracts.nativeToken.symbol}
                    detail={$.convert.formatStableToken(seigniorageFeeCollected * this.props.nativeTokenPriceInStableToken)}
                    detailUnit={$.contracts.stableToken.symbol}
                    infoText={this.getActivity(this.props.pool.seigniorageFee.collected.count, this.props.pool.seigniorageFee.collected.timestamp, now)}
                />
            </div>
        );

        return (
            <div className={`card bg-[#1e0f3c80] bg-diagonal-lines`}>
                <div className="grid grid-cols-12 gap-x-2 gap-y-0.5">
                    <div className="col-span-5">
                        <span className="block opacity-75 text-uppercase fs-6 m-0">
                            <span className="mr-1">{$.farm.getDepositTokenSymbol(this.props.pool)}</span><StyledPopover children={popoverFarmPoolContent} />
                        </span>
                        <span className="opacity-25 text-[0.75em]">
                            {getSupportedSwapRouterName($.chainId, this.props.pool)}
                        </span>
                    </div>
                    <div className="col-span-7 flex items-center justify-end text-[0.75em]">
                        {this.props.pool.isLiquidityToken
                            ? <>
                                <div className="avatar">
                                    <span className="card-title opacity-25 text-uppercase m-0" style={{ fontSize: "x-small" }}>{this.props.pool.token0Details.symbol}</span>
                                </div>
                                <div className="avatar">
                                    <span className="card-title opacity-25 text-uppercase m-0" style={{ fontSize: "x-small" }}>{this.props.pool.token1Details.symbol}</span>
                                </div>
                            </>
                            : <div className="avatar">
                                <span className="card-title opacity-25 text-uppercase m-0" style={{ fontSize: "x-small" }}>{this.props.pool.depositToken.details.symbol}</span>
                            </div>
                        }
                    </div>

                    <div className="col-span-12 mt-0.5 mb-3 border-bottom-t4 border-2"></div>
                </div>

                <div className="grid grid-cols-1 gap-x-2 gap-y-4">
                    <div className="grid grid-cols-2 sm:grid-cols-4 gap-x-2 gap-y-4 text-[80%]">
                        <PriceBox
                            title="Credit Multiplier"
                            main={`${$.convert.formatNumberScaled(1 + creditMultiplier, 2)}`}
                            titleClassName="light-box"
                        />
                        <PriceBox
                            title="Deposit Fee"
                            main={$.convert.formatPercentage(depositFeeRate)}
                            mainUnit="%"
                            titleClassName="light-box"
                        />
                        <PriceBox
                            title="Credit Liq. Rate"
                            main={$.convert.formatPercentage(creditLiquidationRate)}
                            mainUnit="%"
                            titleClassName="light-box"
                        />
                        <PriceBox
                            title="Liquidator Fee"
                            main={$.convert.formatPercentage(liquidatorFeeRate)}
                            mainUnit="%"
                            titleClassName="light-box"
                        />
                    </div>

                    <div className="text-[80%] -mb-2">
                        <div className="flex items-center justify-between group-title opacity-75 light-box px-2">
                            <span>Your Position</span>
                            <span className="numeric opacity-25">
                                <i className="bi bi-clock mr-1" />
                                {$.convert.formatTimespan(senderTimeFromUpdate)}
                            </span>
                        </div>
                    </div>

                    <div className="grid grid-cols-3 gap-x-2 gap-y-4 text-[80%]">
                        <PriceBox
                            title="Balance"
                            main={this.formatDepositToken.formatCurrency(senderBalance)}
                            mainUnit={$.farm.getDepositTokenSymbol(this.props.pool)}
                            detail={this.state.depositTokenPriceInStableToken ? $.convert.formatStableToken(this.state.depositTokenPriceInStableToken * senderBalance) : "-"}
                            detailUnit={$.contracts.stableToken.symbol}
                            isMainUnitOnTop={true}
                            mainUnitClassName="!opacity-25 mb-3"
                            titleClassName="light-box"
                        />
                        <PriceBox
                            title="Deposited"
                            main={this.formatDepositToken.formatCurrency(senderDeposited)}
                            mainUnit={$.farm.getDepositTokenSymbol(this.props.pool)}
                            detail={this.state.depositTokenPriceInStableToken ? $.convert.formatStableToken(this.state.depositTokenPriceInStableToken * senderDeposited) : "-"}
                            detailUnit={$.contracts.stableToken.symbol}
                            isMainUnitOnTop={true}
                            mainUnitClassName="!opacity-25 mb-3"
                            titleClassName="light-box"
                        />
                        <PriceBox
                            title="Credited"
                            main={this.formatCreditToken.formatCurrency(senderCredited)}
                            mainUnit={this.props.creditToken.details.symbol}
                            detail={this.state.creditTokenPriceInStableToken ? $.convert.formatStableToken(this.state.creditTokenPriceInStableToken * senderCredited) : "-"}
                            detailUnit={$.contracts.stableToken.symbol}
                            isMainUnitOnTop={true}
                            mainUnitClassName="!opacity-25 mb-3"
                            titleClassName="light-box"
                        />

                        <PriceBox
                            title="Current Level"
                            main={this.formatCreditToken.formatCurrency(senderCurrentCredit)}
                            mainUnit={this.props.creditToken.details.symbol}
                            isMainUnitOnTop={true}
                            mainClassName={`${senderCurrentCredit > senderCredited ? "animate-pulse neon" : senderCurrentCredit < senderCreditLiquidationLevel ? "animate-pulse glow" : ""}`}
                            mainUnitClassName="!opacity-25 mb-3"
                            titleClassName="light-box"
                        />
                        <PriceBox
                            title="Liquidation Level"
                            main={this.formatCreditToken.formatCurrency(senderCreditLiquidationLevel)}
                            mainUnit={this.props.creditToken.details.symbol}
                            isMainUnitOnTop={true}
                            mainUnitClassName="!opacity-25 mb-3"
                            titleClassName="light-box"
                        />
                        <PriceBox
                            title="Liquidated"
                            main={this.formatDepositToken.formatCurrency(senderLiquidated)}
                            mainUnit={$.farm.getDepositTokenSymbol(this.props.pool)}
                            detail={this.state.depositTokenPriceInStableToken ? $.convert.formatStableToken(this.state.depositTokenPriceInStableToken * senderLiquidated) : "-"}
                            detailUnit={$.contracts.stableToken.symbol}
                            info={this.getActivity(this.props.pool.senderPosition.liquidated.count, this.props.pool.senderPosition.liquidated.timestamp, now)}
                            isMainUnitOnTop={true}
                            mainUnitClassName="!opacity-25 mb-3"
                            titleClassName="light-box"
                        />
                    </div>

                    <div className="grid grid-cols-4 gap-x-2 gap-y-4 leading-none auto-rows-auto text-[0.75em]">
                        <button type="button" className="h-9 btn btn-blue" disabled={this.state.isInteractionDisabled || senderBalance === 0}
                            onClick={async () => {
                                await this.execute(() => this.props.deposit(this.state.depositTokenPriceInStableToken));
                            }}
                        >
                            Deposit
                        </button>
                        <button type="button" className="h-9 btn btn-slate" disabled={this.state.isInteractionDisabled || senderDeposited === 0}
                            onClick={async () => {
                                await this.execute(() => this.props.withdraw(this.state.creditTokenPriceInStableToken));
                            }}
                        >
                            Withdraw
                        </button>
                        <button type="button" className="h-9 btn btn-green" disabled={this.state.isInteractionDisabled || senderDeposited === 0 || !requiresRebalancing}
                            onClick={async () => {
                                await this.execute(() => $.farm.rebalance(this.props.pool.poolId));
                            }}
                        >
                            <span className={`${requiresRebalancing ? "animate-pulse" : ""}`}>Rebalance</span>
                        </button>
                        <button type="button" className="h-9 btn btn-red" disabled={this.state.isInteractionDisabled || senderDeposited === 0}
                            onClick={async () => {
                                await this.execute(() => $.farm.liquidate(this.props.pool.poolId));
                            }}
                        >
                            Liquidate
                        </button>

                        <div className="col-span-4 border-bottom-t4"></div>
                    </div>

                    <div className="flex items-center justify-between -mt-2">
                        <span className="text-[0.75em] uppercase opacity-75 cursor-pointer" onClick={this.togglePoolTotalsVisibility}><i className={`bi ${(this.state.isPoolTotalsVisibile ? "bi-caret-down-fill" : "bi-caret-right-fill")} mr-1`} />
                            Pool Totals
                        </span>
                        <span className="numeric text-[0.75em] opacity-25">
                            <i className="bi bi-clock mr-1" />
                            {$.convert.formatTimespan(totalTimeFromUpdate)}
                        </span>
                    </div>

                    {this.state.isPoolTotalsVisibile &&
                        <div className="grid grid-cols-3 gap-x-2 text-[80%]">
                            <PriceBox
                                title="Deposited"
                                main={this.formatDepositToken.formatCurrency(totalDeposited)}
                                mainUnit={$.farm.getDepositTokenSymbol(this.props.pool)}
                                detail={this.state.depositTokenPriceInStableToken ? $.convert.formatStableToken(this.state.depositTokenPriceInStableToken * totalDeposited) : "-"}
                                detailUnit={$.contracts.stableToken.symbol}
                                isMainUnitOnTop={true}
                                mainUnitClassName="!opacity-25 mb-3"
                                titleClassName="light-box"
                            />
                            <PriceBox
                                title="Credited"
                                main={this.formatCreditToken.formatCurrency(totalCredited)}
                                mainUnit={this.props.creditToken.details.symbol}
                                detail={this.state.creditTokenPriceInStableToken ? $.convert.formatStableToken(this.state.creditTokenPriceInStableToken * totalCredited) : "-"}
                                detailUnit={$.contracts.stableToken.symbol}
                                isMainUnitOnTop={true}
                                mainUnitClassName="!opacity-25 mb-3"
                                titleClassName="light-box"
                            />
                            <PriceBox
                                title="Liquidated"
                                main={this.formatDepositToken.formatCurrency(totalLiquidated)}
                                mainUnit={$.farm.getDepositTokenSymbol(this.props.pool)}
                                detail={this.state.depositTokenPriceInStableToken ? $.convert.formatStableToken(this.state.depositTokenPriceInStableToken * totalLiquidated) : "-"}
                                detailUnit={$.contracts.stableToken.symbol}
                                info={this.getActivity(this.props.pool.totalPosition.liquidated.count, this.props.pool.totalPosition.liquidated.timestamp, now)}
                                isMainUnitOnTop={true}
                                mainUnitClassName="!opacity-25 mb-3"
                                titleClassName="light-box"
                            />
                        </div>
                    }
                </div>
            </div>
        );
    }

    getTokenAvatars() {
        return this.props.pool.isLiquidityToken
            ? (<>
                <div className="avatar">
                    <span className="card-title opacity-25 text-uppercase m-0">{this.props.pool.token0Details.symbol}</span>
                </div>
                <div className="avatar">
                    <span className="card-title opacity-25 text-uppercase m-0">{this.props.pool.token1Details.symbol}</span>
                </div>
            </>)
            : (<div className="avatar">
                <span className="card-title opacity-25 text-uppercase m-0">{this.props.pool.depositToken.details.symbol}</span>
            </div>);
    }

    getActivity(count, timestamp, now) {
        return (
            <div className="flex justify-evenly space-x-1">
                <span className="light-box w-full"><i className="bi bi-app-indicator mr-1" />{$.convert.formatInteger(count)}</span>
                <span className="light-box w-full"><i className="bi bi-clock mr-1" />{timestamp > 0 ? $.convert.formatTimespan(now - timestamp) : "-"}</span>
            </div>
        );
    }

    // Actions
    togglePoolTotalsVisibility() {
        this.setState({ isPoolTotalsVisibile: !this.state.isPoolTotalsVisibile });
    }

    async execute(func) {
        this.setState({ isInteractionDisabled: true });

        try {
            await func();
        } catch (error) {
            console.log(`${error.code}: ${error.message}`);
        } finally {
            this.setState({ isInteractionDisabled: false });
        }
    }
}
