import React, { Component } from "react";
import Web3 from "web3";
import WalletConnectProvider from "@walletconnect/web3-provider";

import Configuration from "../Configuration";

export const WalletContext = React.createContext();

export default class WalletProvider extends Component {
    constructor(props) {
        super(props)

        this.state = {
            self: this,
            // account: "0x0000000000000000000000000000000000000000",
            balance: 0,
            setBalance: b => {
                this.setState({ balance: b });
            },
        }

        this.disconnect = this.disconnect.bind(this);
        this.isInjectedProviderInstalled = this.isInjectedProviderInstalled.bind(this);
        this.getProvider = this.getProvider.bind(this);
        this.initializeProvider = this.initializeProvider.bind(this);
        this.initializeWalletConnectProvider = this.initializeWalletConnectProvider.bind(this);

        this.handleChainChanged = this.handleChainChanged.bind(this);
        this.handleAccountsChanged = this.handleAccountsChanged.bind(this);
        this.handleChainChangedInternal = this.handleChainChangedInternal.bind(this);
        this.handleDisconnect = this.handleDisconnect.bind(this);

        this.addEthereumChain = this.addEthereumChain.bind(this);
        this.switchOrAddEthereumChain = this.switchOrAddEthereumChain.bind(this);
    }

    async componentDidMount() {
        const isMobileDevice = /android|webos|iphone|ipad|ipod|blackberry|iemobile|opera mini|mobile/i.test(navigator.userAgent);
        this.setState({ isMobileDevice });

        if (this.isInjectedProviderInstalled()) {
            await this.initializeInjectedProvider();

            this.setState({
                isInjectedProviderInstalled: true
            });
        } else {
            this.setState({
                isInjectedProviderInstalled: false
            });

            // Start with the default chain till the wallet connect is initialized.
            this.handleChainChangedInternal(Configuration.getDefaultChainId());
            await this.initializeWalletConnectProvider();
        }
    }

    componentWillUnmount() {
        if (this.state.signer && this.state.signer.givenProvider) {
            this.unsubscribeProvider(this.state.signer.givenProvider);
        }
    }

    render() {
        return (
            <WalletContext.Provider value={this.state}>
                {this.props.children}
            </WalletContext.Provider>
        )
    }

    disconnect() {
        this.handleDisconnect(410, "User requested disconnect.");

        this.setState({
            account: undefined,
            balance: undefined
        });
    }

    isInjectedProviderInstalled() {
        return typeof window.ethereum !== "undefined" ? true : typeof window.web3 !== "undefined";
    }

    getProvider() {
        if (typeof window.ethereum !== "undefined") {
            return window.ethereum;
        }

        if (typeof window.web3 !== "undefined") {
            return window.web3.currentProvider;
        }

        throw "Provider not found.";
    }

    async initializeProvider(provider) {
        const signer = WalletProvider.createWeb3(provider);

        this.setState({
            provider: provider,
            signer
        });

        this.subscribeProvider(provider);

        const hexChainId = await provider.request({ method: "eth_chainId" });
        this.handleChainChanged(hexChainId);

        // Is already connected?
        let accounts = await provider.request({ method: "eth_accounts" });

        if (accounts.length === 0) {
            try {
                // Connect, MetaMask pop-up windows appears. Internally triggers "accountsChanged" event.
                await provider.request({ method: "eth_requestAccounts" });
            } catch (error) {
                this.setState({ error });
                console.error(error);
            }
        } else {
            this.handleAccountsChanged(accounts);
        }
    }

    async initializeInjectedProvider() {
        console.log("initializing Injected provider.");

        const provider = this.getProvider();
        await this.initializeProvider(provider);
    }

    async initializeWalletConnectProvider() {
        console.log("initializing WalletConnect provider.");

        const provider = new WalletConnectProvider({
            rpc: Configuration.getRpcUris(),
            qrcode: true,
        });

        provider.connector.on("display_uri", (err, payload) => {
            const uri = payload.params[0];
            console.log(`WalletConnect QR code: ${uri}`);
            //CustomQRCodeModal.display(uri);
        });

        try {
            const accounts = await provider.enable();

            console.log(accounts);
            console.log(provider.walletMeta.name);

            await this.initializeProvider(provider);
        } catch (error) {
            console.log(`${error.code}: ${error.message}`);
        }
    }

    subscribeProvider(provider) {
        if (!provider.on) {
            console.log("Provider doesn't support events.");
            return;
        }

        provider.on("chainChanged", this.handleChainChanged);
        provider.on("accountsChanged", this.handleAccountsChanged);
        provider.on("disconnect", this.handleDisconnect);
    };

    unsubscribeProvider(provider) {
        if (!provider.on) {
            console.log("Provider doesn't support events.");
            return;
        }

        provider.removeListener("chainChanged", this.handleChainChanged);
        provider.removeListener("accountsChanged", this.handleAccountsChanged);
        provider.removeListener("disconnect", this.handleDisconnect);
    }

    updateReader(chainId) {
        this.setState({
            reader: WalletProvider.createWeb3(Configuration.getRpcUri(chainId))
        });
    }

    handleChainChangedInternal(chainId) {
        this.setState({
            error: undefined,
            chainId
        });

        if (!Configuration.isSupportedChain(chainId)) {
            this.setState({
                error: `Unsupported chain: ${chainId}`,
            });
            return;
        }

        this.updateReader(chainId);
    }

    handleChainChanged(hexChainId) {
        const chainId = Web3.utils.hexToNumber(hexChainId);
        console.log(`chainChanged: ${chainId}`);
        this.handleChainChangedInternal(chainId);
    }

    handleAccountsChanged(accounts) {
        console.log(`accountsChanged: ${accounts.length}`);

        if (accounts.length === 0) {
            this.setState({
                error: undefined,
                connected: false,
                account: undefined
            });
            // MetaMask is locked or the user has not connected any accounts
            console.log("Please connect to MetaMask.");
        } else {
            this.setState({
                error: undefined,
                connected: true,
                account: accounts[0]
            });
        }
    }

    handleDisconnect(code, reason) {
        console.log({ source: "Disconnect handler.", code, reason });

        if (this.state.signer && this.state.signer.currentProvider) {
            if (this.state.signer.currentProvider instanceof WalletConnectProvider) {
                this.setState({
                    signer: undefined
                });
                this.handleAccountsChanged([]);
            } else {
                if (this.state.signer.currentProvider.connected && this.state.signer.currentProvider.close) {
                    this.state.signer.currentProvider.close()
                        .then(() => {
                            this.setState({
                                signer: undefined
                            })
                            this.handleAccountsChanged([]);
                        })
                        .catch(error => {
                            console.log("Wallet close failed.");
                            console.log(error);
                        });
                } else {
                    this.setState({
                        signer: undefined
                    })
                    this.handleAccountsChanged([]);
                }
            }
        } else {
            this.setState({
                signer: undefined
            });
            this.handleAccountsChanged([]);
        }
    }

    async addEthereumChain(chainId, chainName, nativeCurrencyName, nativeCurrencySymbol, nativeCurrencyDecimals, rpcUrls, blockExplorerUrls, iconUrls) {
        if (!this.state.provider) {
            console.log("Provider not set.");
            return;
        }

        if (!Web3.utils.isHex(chainId)) {
            chainId = Web3.utils.numberToHex(chainId);
        }

        const p = {
            chainId: chainId,
            chainName: chainName,
            nativeCurrency: {
                name: nativeCurrencyName,
                symbol: nativeCurrencySymbol,
                decimals: nativeCurrencyDecimals
            },
            rpcUrls: rpcUrls,
            blockExplorerUrls: blockExplorerUrls,
            iconUrls: iconUrls
        };

        // Returns null - The method returns null if the request was successful, and an error otherwise.
        return this.state.provider.request({ method: "wallet_addEthereumChain", params: [p] });
    }

    async switchOrAddEthereumChain(chainId, chainName, nativeCurrencyName, nativeCurrencySymbol, nativeCurrencyDecimals, rpcUrls, blockExplorerUrls, iconUrls) {
        if (!this.state.provider) {
            console.log("Provider not set.");
            return;
        }

        if (!Web3.utils.isHexStrict(chainId)) {
            chainId = Web3.utils.numberToHex(chainId);
        }

        console.log(`Switching to chain: ${chainId}`);

        try {
            await this.state.provider.request({
                method: "wallet_switchEthereumChain",
                params: [{ chainId: chainId }],
            });
        } catch (switchError) {
            if (switchError.code === 4902/*For desktop MM*/ || switchError.code === -32603/*For iPhone MM*/) {
                console.log(`Non-existent chain: ${chainId}. Trying to add.`);

                try {
                    await this.addEthereumChain(chainId, chainName, nativeCurrencyName, nativeCurrencySymbol, nativeCurrencyDecimals, rpcUrls, blockExplorerUrls, iconUrls);
                } catch (addError) {
                    this.setState({ error: addError.message });
                    console.error(addError);
                }
            } else {
                this.setState({ error: switchError.message });
                console.error(switchError);
            }
        }
    }

    static createWeb3(provider) {
        const web3 = new Web3(provider);

        web3.eth.extend({
            methods: [
                {
                    name: "chainId",
                    call: "eth_chainId",
                    outputFormatter: web3.utils.hexToNumber
                }
            ]
        });

        return web3;
    }
}
