import { useState, useEffect } from "react";
import { Switch } from "@headlessui/react";
import Web3 from "web3";

import $ from "./core/Services";

import { getAmountOut } from "../../utils/PriceExtensions";

import RangeInput from "./dialogs/core/RangeInput";
import DelayedInput from "./dialogs/core/DelayedInput";

import { MainText, PriceBox } from "../InfoBox";
import Configuration from "../../Configuration";

export default function Swap({
    isDarkTheme,
    nativeCoinBalance,
    token,
    nativeTokenPriceInStableToken,
    swap
}) {
    const Mode = Object.freeze({
        Buy: 0,
        Sell: 1
    });

    const [showSlippageTolerance, setShowSlippageTolerance] = useState(false);
    const [isInteractionDisabled, setIsInteractionDisabled] = useState(false);

    const [mode, setMode] = useState(Mode.Buy);
    const [amount, setAmount] = useState(0);
    const [percentage, setPercentage] = useState(0);
    const [recipient, setRecipient] = useState();

    const [maxReceived, setMaxReceived] = useState(0);
    const [minReceived, setMinReceived] = useState(0);
    const [maxPriceImpact, setMaxPriceImpact] = useState(0);
    const [minPriceImpact, setMinPriceImpact] = useState(0);

    const tokenBalance = $.convert.fromWeiMainToken(token.senderFreeBalance);

    useEffect(() => {
        (async () => {
            if (amount > 0) {
                const tokenPriceInNativeToken = token.nativeTokenReserve / token.tokenReserve;
                const swapFee = Configuration.getSwapRouterSwapFee($.chainId, Configuration.getDefaultSwapRouter($.chainId)) / 10000;

                console.log({ tokenReserve: token.tokenReserve, nativeTokenReserve: token.nativeTokenReserve });
                console.log(`initial price: ${nativeTokenPriceInStableToken * tokenPriceInNativeToken}`);

                if (mode === Mode.Buy) {
                    // const max = await $.router.getMainTokenAmountFromNativeCoin(amount);
                    // setMaxReceived(max);

                    const max = getAmountOut(amount * (1 - swapFee), token.nativeTokenReserve, token.tokenReserve);
                    setMaxReceived(max);

                    const min = max - max * $.settings.getSlippageTolerance() / 10000;
                    setMinReceived(min);

                    let finalTokenPriceInNativeToken = (amount * (1 - swapFee) + token.nativeTokenReserve) / (token.tokenReserve - max);
                    let priceImpact = (finalTokenPriceInNativeToken - tokenPriceInNativeToken) / tokenPriceInNativeToken;
                    setMaxPriceImpact(priceImpact);

                    finalTokenPriceInNativeToken = (amount * (1 - swapFee) + token.nativeTokenReserve) / (token.tokenReserve - min);
                    priceImpact = (finalTokenPriceInNativeToken - tokenPriceInNativeToken) / tokenPriceInNativeToken;
                    setMinPriceImpact(priceImpact);

                    console.log(`mainTokenOut: ${max}; price: ${nativeTokenPriceInStableToken * finalTokenPriceInNativeToken}`);
                } else {
                    // const max = await $.router.getNativeCoinAmountFromMainToken(amount);
                    // setMaxReceived(max);

                    const max = getAmountOut(amount * (1 - swapFee), token.tokenReserve, token.nativeTokenReserve);
                    setMaxReceived(max);

                    const min = max - max * $.settings.getSlippageTolerance() / 10000;
                    setMinReceived(min);

                    let finalTokenPriceInNativeToken = (token.nativeTokenReserve - max) / (token.tokenReserve + amount * (1 - swapFee));
                    let priceImpact = (finalTokenPriceInNativeToken - tokenPriceInNativeToken) / tokenPriceInNativeToken;
                    setMaxPriceImpact(priceImpact);

                    finalTokenPriceInNativeToken = (token.nativeTokenReserve - min) / (token.tokenReserve + amount * (1 - swapFee));
                    priceImpact = (finalTokenPriceInNativeToken - tokenPriceInNativeToken) / tokenPriceInNativeToken;
                    setMinPriceImpact(priceImpact);

                    console.log(`nativeTokenOut: ${max}; price: ${nativeTokenPriceInStableToken * finalTokenPriceInNativeToken}`);
                }
            }
        })();
    }, [amount, token.tokenReserve, token.nativeTokenReserve]);

    return (
        <>
            <div className="grid grid-cols-1 gap-y-3">
                <div className="flex items-center justify-between group-title light-box opacity-75 h-9 px-2">
                    <Switch.Group>
                        <div className="flex items-center">
                            <Switch
                                checked={mode === Mode.Buy}
                                onChange={toggleMode}
                                className={`${isDarkTheme ? "bg-violet-900/80" : "bg-violet-300"} relative inline-flex items-center h-5 rounded-full w-11 transition-colors focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-offset-slate-800 focus:ring-violet-300/50`}
                            >
                                <span className="sr-only">Swap mode</span>
                                <span className={`${mode === Mode.Buy ? "translate-x-6" : "translate-x-1"} inline-block w-4 h-4 transform ${isDarkTheme ? "bg-violet-300" : "bg-slate-800"} rounded-full transition-transform`} />
                            </Switch>
                            <Switch.Label className="leading-6 ml-1.5">
                                {mode === Mode.Buy ? "Buy" : "Sell"} {$.contracts.token.symbol}
                            </Switch.Label>
                        </div>
                    </Switch.Group>
                </div>

                <div className="grid grid-cols-1 gap-y-0.5">
                    <div className="flex items-center justify-between leading-6">
                        <span className="input-label">
                            Amount:
                            <span className="ml-1">
                                (
                                <MainText
                                    main={$.convert.formatPercentage(percentage)}
                                    mainUnit="%"
                                    mainClassName="text-[1em]"
                                    mainUnitClassName="ml-0.5"
                                />
                                )
                            </span>
                        </span>
                        <span className="input-label">
                            Balance:
                            <MainText
                                main={mode === Mode.Buy ? nativeCoinBalance ? $.convert.formatNativeCoin(nativeCoinBalance) : "-" : tokenBalance ? $.convert.formatMainToken(tokenBalance) : "-"}
                                mainUnit={mode === Mode.Buy ? $.nativeCoin.symbol : $.contracts.token.symbol}
                                mainClassName="text-[1em] ml-1"
                                mainUnitClassName="ml-0.5"
                            />
                        </span>
                    </div>
                    <div className={`${isDarkTheme ? "accent-violet-300" : ""}`}>
                        <RangeInput
                            min={0}
                            max={mode === Mode.Buy ? nativeCoinBalance ? nativeCoinBalance : 0 : tokenBalance ? tokenBalance : 0}
                            decimals={Math.min(mode === Mode.Buy ? $.nativeCoin.decimals : $.contracts.token.decimals, 4)}
                            setValue={setAmount}
                            setPercentage={setPercentage}
                            valueClassName={`${isDarkTheme ? "bg-violet-900/80" : ""}`}
                            rangeClassName={`${isDarkTheme ? "bg-violet-900/80" : ""}`}
                            buttonClassName={`btn btn-violet text-indigo-50 numeric w-11 h-7 ${isDarkTheme ? "bg-violet-900/80" : "bg-violet-900/20"} hover:text-violet-900 focus:hover:text-violet-200`}
                        />
                    </div>
                </div>
                <div className="grid grid-cols-1 gap-y-1">
                    <label htmlFor="recipient" className="flex leading-6">
                        <span className="input-label">Recipient:</span>
                    </label>
                    <DelayedInput
                        delay={400}
                        type="search"
                        id="recipient"
                        className={`numeric !text-[1em] text-right ${isDarkTheme ? "bg-violet-900/80" : ""}`}
                        placeholder={`Enter recipient address${$.account ? " or leave blank if it's you" : ""}`}
                        onInput={value => setRecipient(value)}
                    />
                </div>
                <div className="flex items-start justify-between">
                    <div className="lg:col-span-2 justify-self-start">
                        <span
                            className="cursor-pointer text-[1.125em] leading-none shadow-none"
                            onClick={() => {
                                toggleSlippageTolerance();
                            }}
                        >
                            {showSlippageTolerance
                                ? <i className="bi bi-gear-fill" />
                                : <i className="bi bi-gear" />
                            }
                        </span>
                        <span className="ml-2">
                            <input
                                type="number"
                                className={`w-20 numeric !text-[1em] text-right transition origin-left ${showSlippageTolerance ? "scale-x-100" : "scale-x-0"} ${isDarkTheme ? "bg-violet-900/80" : ""}`}
                                defaultValue={$.settings.getSlippageTolerance() / 100}
                                placeholder="Enter slippage tolerance"
                                min={0}
                                step={0.01}
                                onChange={e => {
                                    $.settings.setSlippageTolerance(parseFloat(e.target.value) * 100);
                                }}
                            />
                            <span className={`ml-1 numeric transition ${showSlippageTolerance ? null : "hidden"}`}>%</span>
                        </span>
                    </div>
                    <button
                        type="button"
                        className={`lg:col-span-2 w-28 h-9 text-[0.75em] btn btn-green justify-self-end ${isDarkTheme ? "text-violet-50" : ""}`}
                        disabled={isInteractionDisabled || amount === 0 || !(recipient ? Web3.utils.isAddress(recipient.toLowerCase()) : true)}
                        onClick={async () => {
                            await execute(() => swap(amount, minReceived, recipient, mode === Mode.Buy));
                        }}>
                        {mode === Mode.Buy ? "Buy" : "Sell"}
                    </button>
                </div>
                <div className="grid grid-cols-2 gap-y-4 gap-x-2 items-center text-[80%]">
                    <PriceBox
                        title="To be Received (Min, Max)"
                        main={mode === Mode.Buy ? $.convert.formatNativeCoin(minReceived) : $.convert.formatMainToken(minReceived)}
                        mainUnit={mode === Mode.Buy ? $.contracts.token.symbol : $.nativeCoin.symbol}
                        detail={mode === Mode.Buy ? $.convert.formatNativeCoin(maxReceived) : $.convert.formatMainToken(maxReceived)}
                        detailUnit={mode === Mode.Buy ? $.contracts.token.symbol : $.nativeCoin.symbol}
                        detailClassName="opacity-100 text-[1.4em]"
                        detailUnitClassName="!opacity-75 !text-[0.75em]"
                    />
                    <PriceBox
                        title="Price Impact (Min, Max)"
                        detail={$.convert.formatPercentage(maxPriceImpact, 4, 2)}
                        detailUnit="%"
                        main={$.convert.formatPercentage(minPriceImpact, 4, 2)}
                        mainUnit="%"
                        detailClassName="opacity-100 text-[1.4em]"
                        detailUnitClassName="!opacity-75 !text-[0.75em]"
                    />
                </div>
            </div>
        </>
    );

    function toggleMode() {
        if (mode === Mode.Buy) {
            setMode(Mode.Sell);
        } else {
            setMode(Mode.Buy);
        }
    }

    function toggleSlippageTolerance() {
        setShowSlippageTolerance(!showSlippageTolerance);
    }

    async function execute(func) {
        setIsInteractionDisabled(true);

        try {
            await func();
        } catch (error) {
            console.log(error);
        } finally {
            setIsInteractionDisabled(false);
        }
    }
}
