import { useState, useEffect } from "react";

import ModalDialog from "./core/ModalDialog";

import Configuration from "../../../Configuration";
import { formatAccount, formatCurrency } from "../../../utils/Format";

import { PriceBox } from "../../InfoBox";

import QRCode from "qrcode";

export default function WalletDialog({ isOpen, setIsOpen, context, canceled }) {
    const [qrCode, setQrCode] = useState();

    useEffect(() => {
        if (!context.account) {
            setQrCode(undefined);
            return;
        }

        QRCode.toDataURL(context.account, {
            color: {
                dark: "#312e81",
                light: "#0000" // Transparent background
            }
        })
            .then(url => {
                setQrCode(url);
                console.log(url);
            })
            .catch(err => {
                console.error(err)
            })
    }, [context.account]);

    const chain = Configuration.getChain(context.chainId);
    const nativeCoin = Configuration.getNativeCoin(context.chainId);

    return (
        <ModalDialog
            isOpen={isOpen}
            setIsOpen={setIsOpen}
            disableBackgroundCancel={false}
            body={
                <div className="grid grid-cols-2 gap-x-2 gap-y-4 items-center justify-center text-indigo-900">
                    {context.account &&
                        <>
                            <PriceBox
                                containerClassName="col-span-2"
                                title="Connected Account"
                                main={context.account}
                                mainClassName="numeric break-all"
                            />
                            <img className="col-span-2 mx-auto" src={qrCode} />
                            <button
                                type="button"
                                className="btn btn-blue h-10 w-48 text-violet-50 text-[0.75em] justify-self-end"
                                onClick={async () => {
                                    try {
                                        await navigator.clipboard.writeText(context.account);
                                        console.log("The wallet address is copied.");
                                    } catch (error) {
                                        console.error("Failed to copy the wallet address: ", error);
                                    }
                                }}
                            >
                                <i className="bi bi-clipboard mr-1" />Copy
                            </button>
                            <button
                                type="button"
                                className="btn btn-indigo h-10 w-48 text-violet-50 text-[0.75em] justify-self-start"
                                onClick={() => {
                                    context.self.disconnect();
                                    setIsOpen(false);
                                }}
                            >
                                Disconnect
                            </button>
                            <PriceBox
                                title="Chain Id"
                                main={context.chainId}
                                mainClassName="numeric"
                                detail={chain.name}
                                detailClassName="font-display"
                            />
                            <PriceBox
                                title="Balance"
                                main={formatCurrency(context.balance, nativeCoin.decimals)}
                                mainUnit={nativeCoin.symbol}
                                detail="&nbsp;"
                            />
                            
                            {/*
                            <PriceBox
                                title="Injected Provider"
                                main={context.isInjectedProviderInstalled ? "Yes" : "No"}
                                detail={context.provider.isMetaMask ? "MetaMask" : context.provider?.walletMeta?.name ?? "Unknown"}
                                detailClassName="font-display"
                            />
                            <PriceBox
                                title="Web3 Version"
                                main={context.signer.version}
                                mainClassName="numeric"
                                detail="&nbsp;"
                            />
                            */}
                        </>
                    }
                </div>
            }
            title="Wallet Info"
            hideSubmitButton={true}
            cancelButtonText="Close"
            canceled={canceled}
        />
    );
}
