export const BYTE_MAX_VALUE = 255;

export function getOutcomeText(outcomeCount, outcome) {
    if (outcome === BYTE_MAX_VALUE) {
        return "Unknown";
    }

    if (outcome > outcomeCount) {
        throw `Outcome "${outcome}" is greater than outcome count: ${outcomeCount}`;
    }

    if (outcomeCount === 2) {
        if (outcome === 0) {
            return "Heads";
        }

        if (outcome === 1) {
            return "Tails";
        }
    }

    return `${outcome + 1}`;
}

export function getOutcomeImage(outcomeCount, outcome) {
    if (outcome === BYTE_MAX_VALUE) {
        return "Unknown";
    }

    if (outcome > outcomeCount) {
        throw `Outcome "${outcome}" is greater than outcome count: ${outcomeCount}`;
    }

    // https://miro.medium.com/max/1838/1*p-SahjISD2_mdOf_e-uF5A.png
    if (outcomeCount === 2) {
        if (outcome === 0) {
            return (<img src="./images/heads-200px.png" width="140" height="140" />);
        }

        if (outcome === 1) {
            return (<img src="./images/tails-200px.png" width="140" height="140" />);
        }
    } else if (outcomeCount === 6) {
        return (<i className={`bi bi-dice-${outcome + 1} text-[2.5rem] sm:text-[3.5rem]`}></i>);
    }

    return `${outcome}`;
}

export function getRandomizeText(outcomeCount) {
    if (outcomeCount === 2) {
        return "Flip";
    }

    if (outcomeCount === 6) {
        return "Roll";
    }

    return "Randomize";
}

export function getRandomInteger(min, max) {
    return Math.floor(Math.random() * (max - min)) + min;
}