import { useState } from "react";

import $ from "../core/Services";

import { MainText } from "../../InfoBox";

import ModalDialog from "./core/ModalDialog";
import RangeInput from "./core/RangeInput";

export default function AmountSelectionDialog({
    isOpen,
    setIsOpen,
    title,
    submitButtonText,
    submitted,
    canceled,
    balance,
    priceInStableToken,
    decimals
}) {
    const [amount, setAmount] = useState(0);
    const [percentage, setPercentage] = useState(0);

    return (
        <ModalDialog
            isOpen={isOpen}
            setIsOpen={setIsOpen}
            disableBackgroundCancel={false}
            body={(
                <>
                    <div className="flex items-center justify-between input-label">
                        <span>
                            Amount: (<span className="numeric">{$.convert.formatPercentage(percentage)}%</span>)
                        </span>
                        <span>
                            Balance: <span className="numeric">{$.convert.formatCurrency(balance, decimals)}</span>
                        </span>
                    </div>
                    <RangeInput
                        min={0}
                        max={balance}
                        decimals={decimals}
                        setValue={setAmount}
                        setPercentage={setPercentage}
                        valueClassName="text-indigo-900 border-indigo-300/50 focus:ring-indigo-300/50"
                        rangeClassName="accent-indigo-900"
                    />
                    <div className="mt-3 text-center">
                        <MainText
                            main={priceInStableToken ? $.convert.formatMainToken(amount * priceInStableToken) : "-"}
                            mainUnit={$.contracts.stableToken.symbol}
                            mainClassName="text-[0.875em]" />
                    </div>
                </>
            )}
            title={title}
            submitButtonText={submitButtonText}
            submitted={() => submitted(amount)}
            canceled={canceled}
        />
    )
}
