import React, { Component, Fragment } from "react";
import PropTypes from "prop-types";

import Configuration from "../Configuration";

import { WalletContext } from "../contexts/WalletContext";

import { fromWei } from "../utils/Web3Converter";
import { formatAccount, formatCurrency } from "../utils/Format";

import WalletDialog from "./dapp/dialogs/WalletDialog";
import ConnectDialog from "./dapp/dialogs/ConnectDialog";

export default class Wallet extends Component {
    static propTypes = {
        interval: PropTypes.number,
    };

    static defaultProps = {
        interval: 1000,
    };

    static contextType = WalletContext;

    constructor(props) {
        super(props);

        this.state = {
            balance: undefined,
            showWalletDialog: false,
            showConnectDialog: false,
        };

        this.onTick = this.onTick.bind(this);
        this.switchOrAddEthereumChain = this.switchOrAddEthereumChain.bind(this);
    }

    componentDidMount() {
        if (this.initialize()) {
            console.log("Initialized on component mount.");
        }
    }

    componentWillUnmount() {
        this.stopTimer();
    }

    componentDidUpdate() {
        if (this.initialize()) {
            console.log("Initialized on component update.");
        }
    }

    initialize() {
        if (this.isInitializing) {
            return false;
        }

        this.isInitializing = true;

        try {
            if (!this.context.chainId) {
                return false;
            } else if (!Configuration.isSupportedChain(this.context.chainId)) {
                this.switchOrAddEthereumChain();
                return false;
            }

            if (this.context.account && this.context.reader) {
                if (!this.timerId) {
                    this.startTimer();
                    return true;
                }
            }

            return false;
        } finally {
            this.isInitializing = false;
        }
    }

    startTimer() {
        this.timerId = setInterval(
            async () => await this.onTick(),
            this.props.interval
        );
        console.log(`Started timer: ${this.timerId}`);
    }

    stopTimer() {
        const timerId = this.timerId;

        if (timerId) {
            clearInterval(timerId);
            this.timerId = undefined;

            console.log(`Stopped timer: ${timerId}`);
        }
    }

    render() {
        if (!this.context.connected && !this.context.error) {
            return (
                <Fragment key={0}>
                    <div className="flex items-center">
                        <div className="rounded bg-gray-100/10 px-1 text-[0.875em]">
                            <span
                                className="cursor-pointer"
                                onClick={() => {
                                    this.setState({
                                        showConnectDialog: true,
                                    });
                                }}
                            >
                                Connect to Wallet
                            </span>
                        </div>
                    </div>

                    {this.state.showConnectDialog && (
                        <ConnectDialog
                            isOpen={this.state.showConnectDialog}
                            setIsOpen={() => {
                                this.setState({
                                    showConnectDialog: false,
                                });
                            }}
                            context={this.context}
                            canceled={() => {
                                this.setState({
                                    showConnectDialog: false,
                                });
                            }}
                        />
                    )}
                </Fragment>
            );
        } else if (this.context.error) {
            return (
                <Fragment key={1}>
                    <div className="flex items-center">
                        <div className="rounded bg-gray-100/10 px-1 text-[0.875em]">
                            <span className="text-red-500">
                                {this.context.error.code === -32002
                                    ? "Approve the chain switch"
                                    : this.context.error
                                }
                            </span>
                        </div>
                    </div>
                </Fragment>
            );
        } else {
            if (!this.context.chainId || !Configuration.isSupportedChain(this.context.chainId)) {
                return (
                    <Fragment key={2}>
                        <span className="text-red-500">Unsupported chain.</span>
                    </Fragment>
                );
            }

            const nativeCoin = Configuration.getNativeCoin(this.context.chainId);
            const chain = Configuration.getChain(this.context.chainId);

            return (
                <Fragment key={3}>
                    <div className="flex items-center space-x-2">
                        <div className="hidden rounded bg-gray-100/10 px-1 py-0.5 text-[0.875em] xl:block">
                            <span>{chain.name}</span>
                        </div>
                        <div className="xs:block hidden rounded bg-gray-100/10 px-1 py-0.5 text-[0.875em]">
                            <div>
                                <span className="numeric">
                                    {this.state.balance
                                        ? formatCurrency(this.state.balance, nativeCoin.decimals)
                                        : "-"
                                    }
                                </span>
                                <span className="numeric-unit text-[0.75em]">
                                    {nativeCoin.symbol}
                                </span>
                            </div>
                        </div>
                        {this.context.account &&
                            <div className="cursor-pointer rounded bg-gray-100/[0.15] px-1 py-0.5 text-[0.875em]">
                                <span
                                    className="numeric"
                                    onClick={async () => {
                                        this.setState({
                                            showWalletDialog: true,
                                        });
                                    }}
                                >
                                    {formatAccount(this.context.account)}
                                </span>
                            </div>
                        }
                    </div>

                    {this.state.showWalletDialog && (
                        <WalletDialog
                            isOpen={this.state.showWalletDialog}
                            setIsOpen={() => {
                                this.setState({
                                    showWalletDialog: false,
                                });
                            }}
                            context={this.context}
                            canceled={() => {
                                this.setState({
                                    showWalletDialog: false,
                                });
                            }}
                        />
                    )}
                </Fragment>
            );
        }
    }

    async onTick() {
        if (this.context.chainId && Configuration.isSupportedChain(this.context.chainId) && this.context.account) {
            const result = await this.context.reader.eth.getBalance(this.context.account);
            const nativeCoin = Configuration.getNativeCoin(this.context.chainId);
            const balance = parseFloat(fromWei(result, nativeCoin.decimals));

            this.context.setBalance(balance);
            this.setState({ balance });
        }
    }

    async switchOrAddEthereumChain() {
        const chainId = Configuration.getDefaultChainId();
        const chain = Configuration.getChain(chainId);
        const chainName = chain.name;
        const nativeCurrencyName = undefined;
        const nativeCurrencySymbol = chain.nativeCoin.symbol;
        const nativeCurrencyDecimals = chain.nativeCoin.decimals;
        const rpcUrls = [Configuration.getRpcUri(chainId)];
        const blockExplorerUrls = undefined;
        const iconUrls = undefined;

        await this.context.self.switchOrAddEthereumChain(
            chainId,
            chainName,
            nativeCurrencyName,
            nativeCurrencySymbol,
            nativeCurrencyDecimals,
            rpcUrls,
            blockExplorerUrls,
            iconUrls
        );
    }
}
