import React, { Component } from "react";
import PropTypes from "prop-types";
import { Dialog } from "@headlessui/react";

export default class ModalDialog extends Component {
    static propTypes = {
        disableBackgroundCancel: PropTypes.bool,
        size: PropTypes.string,
        isVerticallyCentered: PropTypes.bool,
        isScrollable: PropTypes.bool,
        fullscreen: PropTypes.string,
        contentStyle: PropTypes.string,
        isOpen: PropTypes.bool,
        setIsOpen: PropTypes.func,
        header: PropTypes.object,
        body: PropTypes.object,
        footer: PropTypes.object,
        title: PropTypes.string,
        hideTitle: PropTypes.bool,
        info: PropTypes.object,
        hideCloseButton: PropTypes.bool,
        description: PropTypes.string,
        submitButtonText: PropTypes.string,
        hideSubmitButton: PropTypes.bool,
        isSubmitButtonDisabled: PropTypes.bool,
        cancelButtonText: PropTypes.string,
        hideCancelButton: PropTypes.bool,
        submitted: PropTypes.func,
        canceled: PropTypes.func,
        result: PropTypes.func
    };

    static defaultProps = {
        disableBackgroundCancel: false,
        isVerticallyCentered: true,
        isScrollable: true,
        fullscreen: null,
        contentStyle: "card relative w-full max-w-md max-h-screen overflow-y-auto mx-auto bg-violet-50 text-gray-900",
        isOpen: false,
        setIsOpen: value => {

        },
        hideTitle: false,
        hideCloseButton: false,
        submitButtonText: "OK",
        hideSubmitButton: false,
        isSubmitButtonDisabled: false,
        cancelButtonText: "Cancel",
        hideCancelButton: false,
        submitted: async () => {
        },
        canceled: async () => {
        },
        result: async value => {
        }
    };

    constructor(props) {
        super(props);

        this.state = {
            style: null
        };
    }

    componentDidMount() {
        const { size, isVerticallyCentered, isScrollable, fullscreen } = this.props;

        let style = "text-dark";

        if (size) {
            style += ` ${size}`;
        }

        if (isVerticallyCentered === true) {
            style += " modal-dialog-centered";
        }

        if (isScrollable === true) {
            style += " modal-dialog-scrollable";
        }

        if (fullscreen) {
            style += ` ${fullscreen}`;
        }

        this.setState({ style });
    }

    render() {
        const {
            isOpen,
            setIsOpen,
            disableBackgroundCancel,
            contentStyle,
            header,
            body,
            footer,
            title,
            hideTitle,
            info,
            hideCloseButton,
            description,
            submitButtonText,
            hideSubmitButton,
            isSubmitButtonDisabled,
            cancelButtonText,
            hideCancelButton,
            submitted,
            canceled,
            result
        } = this.props;

        return (
            <Dialog
                className="fixed z-10 inset-0 overflow-y-auto"
                open={isOpen}
                onClose={() => {
                    if (!disableBackgroundCancel) {
                        setIsOpen(false);
                    }
                }}
            >
                <div className="flex items-center justify-center min-h-screen text-[16px]">
                    <Dialog.Overlay className="fixed inset-0 bg-black/50" />

                    <div className={`modal-content ${contentStyle} bg-gradient-to-r to-indigo-200 from-purple-200`}>
                        {(header || !hideTitle || !hideCloseButton) &&
                            <div className="modal-header grid grid-cols-1 gap-x-2 gap-y-3">
                                {header
                                    ? { header }
                                    : <div className="flex items-center justify-between">
                                        {!hideTitle &&
                                            <Dialog.Title key="0" className="">
                                                {title}
                                            </Dialog.Title>
                                        }
                                        <div className="space-x-2">
                                            {info &&
                                                <button key="1" type="button" className="btn text-[2em] leading-none shadow-none btn-close" aria-label="Info">
                                                    {info}
                                                </button>
                                            }
                                            {!hideCloseButton &&
                                                <button key="2" type="button" className="btn text-[2em] leading-none shadow-none btn-close" data-bs-dismiss="modal" aria-label="Close" onClick={async () => {
                                                    await canceled();

                                                    if (result) {
                                                        result(false);
                                                    }
                                                }}>
                                                    <i className="bi bi-x" />
                                                </button>
                                            }
                                        </div>
                                    </div>
                                }
                            </div>
                        }

                        {description &&
                            <Dialog.Description className="mt-3">
                                {description}
                            </Dialog.Description>
                        }

                        <div className="modal-body mt-3">
                            {body}
                        </div>

                        {(footer || !hideSubmitButton || !hideCancelButton) &&
                            <div className="modal-footer mt-8">
                                {footer
                                    ? { footer }
                                    : <div className="flex items-center justify-end space-x-2 text-violet-50 text-[80%]">
                                        {!hideSubmitButton &&
                                            <button key="0" type="button" className="btn btn-green w-28 h-10" onClick={async () => {
                                                try {
                                                    await submitted();
                                                    console.log("Operation succeeded.");

                                                    if (result) {
                                                        result(true);
                                                    }
                                                } catch (error) {
                                                    console.error("Operation failed.", error);

                                                    if (result) {
                                                        result({ error });
                                                    }
                                                }
                                            }} disabled={isSubmitButtonDisabled}>
                                                {submitButtonText}
                                            </button>
                                        }
                                        {!hideCancelButton &&
                                            <button key="1" type="button" className="btn btn-dark w-28 h-10" data-bs-dismiss="modal" aria-label="Cancel" onClick={async () => {
                                                await canceled();

                                                if (result) {
                                                    result(false);
                                                }
                                            }}>
                                                {cancelButtonText}
                                            </button>
                                        }
                                    </div>
                                }
                            </div>
                        }
                    </div>
                </div>
            </Dialog>
        );
    }
}
