export default function Routes() {
    return (
        <BrowserRoutes>
            <Route path="/" element={<Main showComponent={ShowComponentType.Token} />} />
            <Route path="Games" element={<Main showComponent={ShowComponentType.Token | ShowComponentType.Games} />} />
            <Route path="Farms" element={<Main showComponent={ShowComponentType.Token | ShowComponentType.Farms} />} />
            <Route path="Liquidatables" element={<Main showComponent={ShowComponentType.Liquidatables} />} />
        </BrowserRoutes>
    )
}
