import IUniswapV2Pair from "../abi/IUniswapV2Pair.json";
import IUniswapV2Router02 from "../abi/IUniswapV2Router02.json";
import TokenFinder from "../abi/TokenFinder.json";
import IziSwap from "../abi/IziSwap.json";
import WeebToken from "../abi/WeebToken.json";
import WeebGame from "../abi/WeebGame.json";
import WeebFarm from "../abi/WeebFarm.json";

export function createReaderContract(reader, abi, address, options) {
    return new reader.eth.Contract(abi, address, options);
}

export function createSignerContract(signer, abi, address, options) {
    return new signer.eth.Contract(abi, address, options);
}

export function createReaders(reader, account, contracts) {
    const options = {
        from: account ?? "0x0000000000000000000000000000000000000000" /* Fix for hardhat which keeps returning data of previous account when a request comes from an empty account. */
    };

    return {
        pair: createReaderContract(reader, IUniswapV2Pair, contracts.liquidityToken, options),
        router: createReaderContract(reader, IUniswapV2Router02, contracts.defaultSwapRouter, options),
        token: createReaderContract(reader, WeebToken, contracts.token.address, options),
        game: createReaderContract(reader, WeebGame, contracts.game, options),
        farm: createReaderContract(reader, WeebFarm, contracts.farm, options),
        tokenFinder: createReaderContract(reader, TokenFinder, contracts.tokenFinder, options),
        iziSwap: createReaderContract(reader, IziSwap, contracts.iziSwap, options),
    };
}

export function createSigners(signer, account, contracts) {
    const options = {
        from: account
    };

    return {
        router: createSignerContract(signer, IUniswapV2Router02, contracts.defaultSwapRouter, options),
        token: createSignerContract(signer, WeebToken, contracts.token.address, options),
        game: createSignerContract(signer, WeebGame, contracts.game, options),
        farm: createSignerContract(signer, WeebFarm, contracts.farm, options),
        iziSwap: createSignerContract(signer, IziSwap, contracts.iziSwap, options),
    };
}