import { useState, useEffect } from "react";
import PropTypes from "prop-types";

export default function PredefinedValueSelector({
    predefinedValueListTitle,
    userSuppliedValueTitle,
    predefinedValues,
    defaultValue,
    isValidValue,
    delay,
    onInput
}) {
    const [input, setInput] = useState();

    useEffect(() => {
        if (!input) {
            setInput(defaultValue);
        }
    }, [defaultValue]);

    useEffect(() => {
        const timerId = setTimeout(() => onInput(input), delay);
        return () => clearTimeout(timerId);
    }, [input]);

    return (
        <>
            {/* <div className="flex flex-row items-center justify-start space-x-2 space-y-0"> */}
            <div className="flex flex-col sm:flex-row items-center justify-start sm:space-x-2 space-y-2 sm:space-y-0">
                <div className="flex flex-col justify-center w-full">
                    <label className="flex leading-6 mb-0.5" htmlFor="predefinedValueList">
                        <span className="input-label">
                            {predefinedValueListTitle}
                        </span>
                    </label>
                    <select
                        id="predefinedValueList"
                        className="text-indigo-900 bg-indigo-900/10 border-indigo-300/50 focus:ring-indigo-300/50"
                        value={input}
                        onChange={e => setInput(e.target.value)}
                    >
                        {predefinedValues}
                    </select>
                </div>

                <div className="w-full">
                    <label className="flex leading-6 mb-0.5" htmlFor="userSuppliedValue">
                        <span className="input-label">
                            {userSuppliedValueTitle}
                        </span>
                    </label>
                    <input
                        type="search"
                        id="userSuppliedValue"
                        className={`numeric text-indigo-900 bg-indigo-900/10 border-indigo-300/50 focus:ring-indigo-300/50 ${isValidValue ? "" : "bg-red-300"}`}
                        value={input ?? ""}
                        onChange={e => setInput(e.target.value)}
                    />
                </div>
            </div>
        </>
    );
}

PredefinedValueSelector.propTypes = {
    predefinedValueListTitle: PropTypes.string,
    userSuppliedValueTitle: PropTypes.string,
    predefinedValues: PropTypes.any,
    defaultValue: PropTypes.string,
    delay: PropTypes.number,
    onInput: PropTypes.func
}

PredefinedValueSelector.defaultProps = {
    delay: 400,
    onInput: () => {
    }
};
