import ModalDialog from "./core/ModalDialog";

import Swap from "../Swap";

export default function SwapDialog({
    isOpen,
    setIsOpen,
    nativeCoinBalance,
    token,
    nativeTokenPriceInStableToken,
    swap,
    canceled
}) {
    return (
        <ModalDialog
            isOpen={isOpen}
            setIsOpen={setIsOpen}
            disableBackgroundCancel={true}
            body={(
                <>
                    <Swap
                        isDarkTheme={true}
                        nativeCoinBalance={nativeCoinBalance}
                        token={token}
                        nativeTokenPriceInStableToken={nativeTokenPriceInStableToken}
                        swap={swap}
                    />
                </>
            )}
            title={`Buy-Sell ${token.details.symbol}`}
            hideSubmitButton={true}
            cancelButtonText="Close"
            canceled={canceled}
        />
    )
}
