import React from "react";

export function TitleText({
    title,
    titleClassName,
}) {
    return (
        <>
            <span className={`opacity-50 uppercase tracking-wider overflow-ellipsis break-normal text-[0.6875em] ${titleClassName ?? ""}`}>
                {title}
            </span>
        </>
    );
}

TitleText.defaultProps = {
};

export function MainText({
    main,
    mainClassName,
    mainUnit,
    mainUnitClassName,
    isUnitOnTop
}) {
    return isUnitOnTop
        ? (
            <>
                {mainUnit &&
                    <span className={`uppercase block mb-1 opacity-75 text-[0.75em] ${mainUnitClassName ?? ""}`}>
                        {mainUnit}
                    </span>
                }
                {main &&
                    <span className={`numeric text-[1.4em] ${mainClassName ?? ""}`}>
                        {main}
                    </span>
                }
            </>
        )
        : (
            <>
                {main &&
                    <span className={`numeric text-[1.4em] ${mainClassName ?? ""}`}>
                        {main}
                    </span>
                }
                {mainUnit &&
                    <span className={`uppercase ml-1 opacity-75 text-[0.75em] ${mainUnitClassName ?? ""}`}>
                        {mainUnit}
                    </span>
                }
            </>
        );
}

MainText.defaultProps = {
    isUnitOnTop: false
};

export function DetailText({
    detail,
    detailClassName,
    detailUnit,
    detailUnitClassName
}) {
    return (
        <>
            {detail &&
                <span className={`numeric opacity-75 text-[0.75em] ${detailClassName ?? ""}`}>
                    {detail}
                </span>
            }
            {detailUnit &&
                <span className={`uppercase ml-1 opacity-50 text-[0.625em] ${detailUnitClassName ?? ""}`}>
                    {detailUnit}
                </span>
            }
        </>
    );
}

DetailText.defaultProps = {
};

export function InfoText({
    info,
    infoClassName
}) {
    return (
        <>
            {info &&
                <div className={`numeric mt-2 text-[0.75em] ${infoClassName ?? ""}`}>
                    {info}
                </div>
            }
        </>
    );
}

InfoText.defaultProps = {
    infoClassName: "opacity-25"
};

function InfoBox({
    containerClassName,
    lineHeight,
    title,
    titleClassName,
    main,
    detail,
    info
}) {
    return (
        <div className={`grid grid-cols-1 auto-rows-min text-center ${lineHeight ?? ""} ${containerClassName ?? ""}`}>
            <TitleText title={title} titleClassName={titleClassName} />
            {main && <>{main}</>}
            {detail && <>{detail}</>}
            {info && <>{info}</>}
        </div>
    );
}

InfoBox.defaultProps = {
};

export function PriceBox({
    containerClassName,
    lineHeight,
    title,
    titleClassName,
    mainRaw,
    main,
    mainClassName,
    mainUnit,
    mainUnitClassName,
    isMainUnitOnTop,
    detail,
    detailClassName,
    detailUnit,
    detailUnitClassName,
    info,
    infoClassName
}) {
    return <InfoBox
        containerClassName={containerClassName}
        lineHeight={lineHeight}
        title={title}
        titleClassName={titleClassName}
        main={mainRaw
            ? <>{mainRaw}</>
            : (main || mainUnit) &&
            <div className="mt-2">
                <MainText
                    main={main}
                    mainClassName={mainClassName}
                    mainUnit={mainUnit}
                    mainUnitClassName={mainUnitClassName}
                    isUnitOnTop={isMainUnitOnTop}
                />
            </div>}
        detail={(detail || detailUnit) &&
            <div className="mt-1">
                <DetailText
                    detail={detail}
                    detailClassName={detailClassName}
                    detailUnit={detailUnit}
                    detailUnitClassName={detailUnitClassName}
                />
            </div>
        }
        info={(
            <InfoText info={info} infoClassName={infoClassName} />
        )}
    />;
}

PriceBox.defaultProps = {
    lineHeight: "leading-none",
    infoClassName: "opacity-25",
    isMainUnitOnTop: false
};
