import { useState, useEffect } from "react";

import ModalDialog from "./core/ModalDialog";

import Configuration from "../../../Configuration";
import { formatAccount, formatCurrency } from "../../../utils/Format";

import { PriceBox } from "../../InfoBox";

export default function ConnectDialog({ isOpen, setIsOpen, context, canceled }) {
    const chain = Configuration.getChain(context.chainId);
    const nativeCoin = Configuration.getNativeCoin(context.chainId);

    return (
        <ModalDialog
            isOpen={isOpen}
            setIsOpen={setIsOpen}
            disableBackgroundCancel={false}
            body={
                <div className="grid grid-cols-1 gap-x-4 gap-y-4 items-center justify-center text-indigo-900">
                    {context.self.isInjectedProviderInstalled()
                        ?
                        <button
                            type="button"
                            className="btn btn-red h-16 w-full text-violet-50 text-[0.75em]"
                            onClick={async () => {
                                context.self.initializeInjectedProvider();
                                setIsOpen(false);
                            }}
                        >
                            Use Injected Provider
                        </button>
                        : <div className="h-16 w-full text-violet-900 text-center">
                            <p>
                                <b>No injected provider found.</b>
                            </p>
                            <a href="https://metamask.io/download/" target="_blank">Install MetaMask <i className="bi bi-box-arrow-up-right" /></a>
                        </div>
                    }
                    <button
                        type="button"
                        className="btn btn-blue h-16 w-full text-violet-50 text-[0.75em]"
                        onClick={() => {
                            context.self.initializeWalletConnectProvider();
                            setIsOpen(false);
                        }}
                    >
                        Use Wallet Connect Provider
                    </button>
                </div>
            }
            title="Connect to Wallet"
            hideSubmitButton={true}
            cancelButtonText="Close"
            canceled={canceled}
        />
    );
}
