import { useState, useEffect } from "react";
import PropTypes from "prop-types";

import $ from "./core/Services";

import { DAYS_IN_WEEK, DAYS_IN_MONTH, DAYS_IN_YEAR, getRewardPerDay } from "../../utils/InterestExtensions";

import DelayedInput from "./dialogs/core/DelayedInput";
import { PriceBox } from "../InfoBox";

export default function RewardCalculator({
    senderBalance,
    totalStaked,
    currentRewardPerBlock,
    maxRewardPerBlock,
    isMaximizedEmission,
    blockTime,
    tokenPriceInStableToken
}) {
    const [targetBalance, setTargetBalance] = useState();
    const [currentRewards, setCurrentRewards] = useState([0, 0, 0, 0]);
    const [maxRewards, setMaxRewards] = useState([0, 0, 0, 0]);

    useEffect(() => {
        if (!targetBalance) {
            setTargetBalance(parseFloat(senderBalance));
        }
    }, [senderBalance]);

    useEffect(() => {
        const currentRewardPerD = getRewardPerDay(currentRewardPerBlock, blockTime, totalStaked, targetBalance);
        const currentRewardPerW = currentRewardPerD * DAYS_IN_WEEK;
        const currentRewardPerM = currentRewardPerD * DAYS_IN_MONTH;
        const currentRewardPerY = currentRewardPerD * DAYS_IN_YEAR;

        const maxRewardPerD = getRewardPerDay(maxRewardPerBlock, blockTime, totalStaked, targetBalance);
        const maxRewardPerW = maxRewardPerD * DAYS_IN_WEEK;
        const maxRewardPerM = maxRewardPerD * DAYS_IN_MONTH;
        const maxRewardPerY = maxRewardPerD * DAYS_IN_YEAR;

        setCurrentRewards([currentRewardPerD, currentRewardPerW, currentRewardPerM, currentRewardPerY]);
        setMaxRewards([maxRewardPerD, maxRewardPerW, maxRewardPerM, maxRewardPerY]);
    }, [totalStaked, currentRewardPerBlock, blockTime, targetBalance]);

    return (
        <>
            <div className="grid grid-cols-1 gap-x-2 gap-y-4">
                <div>
                    <span className="text-[0.5em] text-violet-100/50 light-box block mb-4 min-h-[5.5em] xs:min-h-[4em]">
                        Calculates potential reward levels (daily, weekly, monthly, yearly) for the given balance.
                    </span>
                    <div className="grid grid-cols-1 gap-y-1">
                        <div className="flex items-center justify-between leading-6">
                            <label htmlFor="balance" className="flex leading-6">
                                <span className="input-label">Target balance</span>
                            </label>
                            <span
                                className="cursor-pointer text-[1.125em] leading-none shadow-none"
                                onClick={() => {
                                    setTargetBalance(parseFloat(senderBalance));
                                }}
                            >
                                <i className="bi bi-arrow-clockwise"></i>
                            </span>
                        </div>
                        <DelayedInput
                            delay={400}
                            type="number"
                            id="balance"
                            className="numeric !text-[1em] text-right"
                            defaultValue={targetBalance}
                            placeholder="Balance in WEEB"
                            min={0}
                            step={1}
                            onInput={value => setTargetBalance(parseFloat(value))}
                        />
                    </div>
                </div>

                <div className="grid grid-cols-2 sm:grid-cols-4 gap-x-2 gap-y-4 text-[80%]">
                    <span className="group-title opacity-75 col-span-2 sm:col-span-4 light-box">Current Target Reward</span>
                    <PriceBox
                        sizeCount={1}
                        title="Per Day"
                        main={isNaN(currentRewards[0]) ? "" : $.convert.formatMainToken(currentRewards[0])}
                        mainUnit={$.contracts.token.symbol}
                        detail={isNaN(currentRewards[0]) ? "" : $.convert.formatStableToken(currentRewards[0] * tokenPriceInStableToken)}
                        detailUnit={$.contracts.stableToken.symbol}
                    />
                    <PriceBox
                        sizeCount={1}
                        title="Per Week"
                        main={isNaN(currentRewards[1]) ? "" : $.convert.formatMainToken(currentRewards[1])}
                        mainUnit={$.contracts.token.symbol}
                        detail={isNaN(currentRewards[1]) ? "" : $.convert.formatStableToken(currentRewards[1] * tokenPriceInStableToken)}
                        detailUnit={$.contracts.stableToken.symbol}
                    />
                    <PriceBox
                        sizeCount={1}
                        title="Per Month"
                        main={isNaN(currentRewards[2]) ? "" : $.convert.formatMainToken(currentRewards[2])}
                        mainUnit={$.contracts.token.symbol}
                        detail={isNaN(currentRewards[2]) ? "" : $.convert.formatStableToken(currentRewards[2] * tokenPriceInStableToken)}
                        detailUnit={$.contracts.stableToken.symbol}
                    />
                    <PriceBox
                        sizeCount={1}
                        title="Per Year"
                        main={isNaN(currentRewards[3]) ? "" : $.convert.formatMainToken(currentRewards[3])}
                        mainUnit={$.contracts.token.symbol}
                        detail={isNaN(currentRewards[3]) ? "" : $.convert.formatStableToken(currentRewards[3] * tokenPriceInStableToken)}
                        detailUnit={$.contracts.stableToken.symbol}
                    />

                    {!isMaximizedEmission &&
                        <>
                            <span className="group-title opacity-75 col-span-2 sm:col-span-4 light-box">Maximum Target Reward</span>
                            <PriceBox
                                sizeCount={1}
                                title="Per Day"
                                main={isNaN(maxRewards[0]) ? "" : $.convert.formatMainToken(maxRewards[0])}
                                mainUnit={$.contracts.token.symbol}
                                detail={isNaN(maxRewards[0]) ? "" : $.convert.formatStableToken(maxRewards[0] * tokenPriceInStableToken)}
                                detailUnit={$.contracts.stableToken.symbol}
                            />
                            <PriceBox
                                sizeCount={1}
                                title="Per Week"
                                main={isNaN(maxRewards[1]) ? "" : $.convert.formatMainToken(maxRewards[1])}
                                mainUnit={$.contracts.token.symbol}
                                detail={isNaN(maxRewards[1]) ? "" : $.convert.formatStableToken(maxRewards[1] * tokenPriceInStableToken)}
                                detailUnit={$.contracts.stableToken.symbol}
                            />
                            <PriceBox
                                sizeCount={1}
                                title="Per Month"
                                main={isNaN(maxRewards[2]) ? "" : $.convert.formatMainToken(maxRewards[2])}
                                mainUnit={$.contracts.token.symbol}
                                detail={isNaN(maxRewards[2]) ? "" : $.convert.formatStableToken(maxRewards[2] * tokenPriceInStableToken)}
                                detailUnit={$.contracts.stableToken.symbol}
                            />
                            <PriceBox
                                sizeCount={1}
                                title="Per Year"
                                main={isNaN(maxRewards[3]) ? "" : $.convert.formatMainToken(maxRewards[3])}
                                mainUnit={$.contracts.token.symbol}
                                detail={isNaN(maxRewards[3]) ? "" : $.convert.formatStableToken(maxRewards[3] * tokenPriceInStableToken)}
                                detailUnit={$.contracts.stableToken.symbol}
                            />
                        </>
                    }
                </div>
            </div>
        </>
    );
}

RewardCalculator.propTypes = {
    senderBalance: PropTypes.number,
    totalStaked: PropTypes.number,
    currentRewardPerBlock: PropTypes.number,
    blockTime: PropTypes.number,
    tokenPriceInStableToken: PropTypes.number
}

RewardCalculator.defaultProps = {

};
